import { KonvaEventObject } from "konva/lib/Node";
import React from "react";
import { Label, Tag, Text } from "react-konva";
import Colors from "../utils/Colors";
import KonvaUtils from "../utils/KonvaUtils";

type KonvaButtonProps = {
    x?: number;
    y?: number;
    width?: number;
    height?: number;
    cornerRadius?: number;
    backgroundColor?: string;
    text?: string;
    textOffsetY?: number;
    textColor?: string;
    fontSize?: number;
    onClick?: (evt: KonvaEventObject<MouseEvent>) => void;
}

export function KonvaButton(props: KonvaButtonProps): JSX.Element {
    const { x = 0, y = 0, width = 10, height = 10, cornerRadius = 0, backgroundColor = Colors.Blue[500], text = "", textOffsetY = 0, textColor = Colors.Blue[100], fontSize = 14, onClick = () => { return; } } = props;

    return (
        <Label x={x}
            y={y}
            onMouseEnter={event => {
                event.cancelBubble = true;
                KonvaUtils.setCursor(event, "pointer");
            }}
            onMouseLeave={event => {
                event.cancelBubble = true;
                KonvaUtils.setCursor(event, "default");
            }}
            onClick={onClick}>
            <Tag fill={backgroundColor}
                cornerRadius={cornerRadius} />
            <Text offsetY={textOffsetY}
                width={width}
                height={height}
                fontSize={fontSize}
                fontStyle="bold"
                align="center"
                verticalAlign="middle"
                fill={textColor}
                text={text} />
        </Label>
    );
}