import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppDispatch, RootState } from "../../app/store";
import DBService from "../../db/DBService";
import { UserObject } from "../../db/models";
import Cookies from "../../utils/Cookies";

// Should contain only the necessary user details, like:
// - user name
// - user settings
// - user id
// - auth token

export type UserState = {
    userList: UserObject[];
    currentUser: UserObject | undefined;
};

const initialState: UserState = {
    userList: [],
    currentUser: undefined
};

// Payload Types
type UpdateUserNamePayload = {
    userId: string;
    name: string;
}

export const slice = createSlice({
    name: "users",
    initialState: initialState,
    reducers: {
        addUser: (state, action: PayloadAction<UserObject>) => {
            state.userList.push(action.payload);
        },
        removeUser: (state, action: PayloadAction<string>) => {
            const id = action.payload;
            const userIndex = state.userList.findIndex(user => user.userId === id);
            if (userIndex < 0)
                return;

            const newArr = state.userList.slice();
            newArr.splice(userIndex, 1);
            state.userList = newArr;

            if (state.currentUser && state.currentUser.userId === id)
                state.currentUser = undefined;
        },
        setUserList: (state, action: PayloadAction<UserObject[]>) => {
            state.userList = action.payload;
        },
        setCurrentUser: (state, action: PayloadAction<UserObject>) => {
            state.currentUser = action.payload;
        },
        removeCurrentUser: (state, action: PayloadAction<undefined>) => {
            state.currentUser = undefined;
        },
        updateUserName: (state, action: PayloadAction<UpdateUserNamePayload>) =>{
            const payload = action.payload;
            if (state.currentUser && state.currentUser.userId === payload.userId)
                state.currentUser.name = payload.name;

            const userIndex = state.userList.findIndex(user => user.userId === payload.userId);
            if (userIndex < 0)
                return;

            state.userList[userIndex].name = payload.name;
        }
    }
});

// Actions
export const { addUser, removeUser, setUserList, setCurrentUser, removeCurrentUser, updateUserName } = slice.actions;

// Thunks
export const fetchOfflineUsersAsync = () => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            const users = await DBService.db.getAllUsers();
            dispatch(setUserList(users.map(user => user.toObject())));
        } catch (err) {
            console.log(err);
        }
    };
};

export const createNewUserAsync = (name: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            const user = await DBService.db.createNewUser(name);
            dispatch(addUser(user.toObject()));
        } catch (err) {
            console.log(err);
        }
    };
};

export const deleteOfflineUserAsync = (userId: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            // TODO: Should check if the update succeeded
            await DBService.db.removeUser(userId);
            dispatch(removeUser(userId));
        } catch (err) {
            console.log(err);
        }
    };
};

export const setCurrentOfflineUserAsync = (userId: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            const user = await DBService.db.getUserDetail(userId);
            Cookies.setCookie("user_id", user.userId);
            dispatch(setCurrentUser(user.toObject()));
        } catch (err) {
            console.log(err);
        }
    };
};

export const removeCurrentOfflineUserAsync = () => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            // Write any async API calls here
            // Either from a server or from IndexedDb
            Cookies.deleteCookie("user_id");
            dispatch(removeCurrentUser());
        } catch (err) {
            console.log(err);
        }
    };
};

export const updateUserNameAsync = (userId: string, name: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            const users = await DBService.db.updateUserName(userId, name);
            dispatch(updateUserName({ userId, name }));
        } catch (err) {
            console.log(err);
        }
    };
};

// Selectors
export const getCurrentUser = (state: RootState): UserObject | undefined => state.user.currentUser;
export const getUserList = (state: RootState): UserObject[] => state.user.userList;
export const hasCurrentUser = (state: RootState): boolean => state.user.currentUser !== undefined;

const userSlice = slice.reducer;
export default userSlice;