import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { DataFrame } from "danfojs";
import React from "react";
import Scrollbars from "react-custom-scrollbars-2";
import Dropdown from "react-overlays/esm/Dropdown";
import store from "../app/store";
import { CSVSource } from "../data/DataService";
import { DataSourceObject } from "../db/models";
import { createNewDataSourceAsync, deleteDataSourceAsync, setIsDraggingSourceAsync, updateDataSourceNameAsync } from "../features/notebooks/notebookSlice";
import { DataFrameOutput } from "../types";
import { SourceType, SourceTypes } from "../utils/Types";
import { OKButton, CustomModal, FileButton, PrimaryButton, SecondaryButton, Input, ContextMenuItem, CancelButton } from "./CustomComponents";
import { ShowResult } from "./NodeCell";

type SourceItemProps = {
    source: DataSourceObject;
}

type SourceItemState = {
    isOptions: boolean;
    isRename: boolean;
    renameValue: string;
    isDelete: boolean;
    isPreview: boolean;
    preview: DataFrameOutput;
}

class SourceItem extends React.Component<SourceItemProps, SourceItemState> {
    constructor(props: SourceItemProps) {
        super(props);

        this.state = {
            isOptions: false,
            isRename: false,
            renameValue: "",
            isDelete: false,
            isPreview: false,
            preview: {
                type: "dataframe",
                outputOrder: 0,
                columns: [],
                indexes: [],
                values: []
            }
        };

        this.onDragSourceStart = this.onDragSourceStart.bind(this);

        this.showRenameModal = this.showRenameModal.bind(this);
        this.hideRenameModal = this.hideRenameModal.bind(this);
        this.renameSource = this.renameSource.bind(this);

        this.showDeleteModal = this.showDeleteModal.bind(this);
        this.hideDeleteModal = this.hideDeleteModal.bind(this);
        this.deleteSource = this.deleteSource.bind(this);

        this.showPreviewModal = this.showPreviewModal.bind(this);
        this.hidePreviewModal = this.hidePreviewModal.bind(this);
    }

    async onDragSourceStart(event: React.DragEvent<HTMLDivElement>, source: DataSourceObject): Promise<void> {
        event.stopPropagation();
        await store.dispatch(setIsDraggingSourceAsync(true));

        const data = { dataType: "source", source };
        event.dataTransfer.setData("text/plain", JSON.stringify(data));
    }

    showRenameModal(): void {
        this.setState({isRename: true, isOptions: false, renameValue: ""});
    }

    hideRenameModal(): void {
        this.setState({isRename: false});
    }

    async renameSource(): Promise<void> {
        const { source } = this.props;
        const { renameValue } = this.state;
        if (renameValue !== "") {
            await store.dispatch(updateDataSourceNameAsync(source.sourceId, renameValue));
            this.hideRenameModal();
        }
    }

    showDeleteModal(): void {
        this.setState({isDelete: true, isOptions: false});
    }

    hideDeleteModal(): void {
        this.setState({isDelete: false});
    }

    async deleteSource(): Promise<void> {
        const { source } = this.props;
        this.setState({isOptions: false});
        await store.dispatch(deleteDataSourceAsync(source.sourceId));
        this.hideDeleteModal();
    }

    showPreviewModal(): void {
        const { source } = this.props;
        const output: DataFrameOutput = {
            type: "dataframe",
            outputOrder: 0,
            columns: [],
            indexes: [],
            values: []
        };

        const rowCount = 0;
        const csv = new CSVSource(source.sourceId);
        csv.loadToDf().then(result => {
            if (!result)
                throw new ReferenceError("Data Source could not be loaded.");

            const df = csv.df as DataFrame;
            const head = (df.shape[0] >= rowCount && rowCount > 0) ? df.head(rowCount) : df;
            let values: string[][];
            if (head.columns.length > 1) {
                values = head.values.map(value1 => {
                    const val1 = value1 as string[] | (string | number | boolean)[] | number[] | boolean[];
                    return val1.map(value2 => `${value2}`);
                });
            } else {
                values = head.values.map(value => {
                    const val = value as string | number | boolean;
                    return [`${val}`];
                });
            }

            output.columns = df.columns,
            output.values = values;
    
            this.setState({isPreview: true, isOptions: false, preview: output});
        });
    }

    hidePreviewModal(): void {
        const output: DataFrameOutput = {
            type: "dataframe",
            outputOrder: 0,
            columns: [],
            indexes: [],
            values: []
        };

        this.setState({isPreview: false, preview: output});
    }

    render(): JSX.Element {
        const { source } = this.props;
        const { isOptions, isRename, renameValue, isDelete, isPreview, preview } = this.state;
        return (
            <>
                <div className="transition duration-200 ease-in-out flex flex-row items-center justify-between p-2 text-sm text-gray-200 hover:bg-indigo-400 hover:text-indigo-100 select-none"
                    draggable
                    onDragStart={async event => await this.onDragSourceStart(event, source)}
                    onDragEnd={async () => await store.dispatch(setIsDraggingSourceAsync(false))}
                    onMouseLeave={() => this.setState({isOptions: false})}>
                    <span className="">{source.name}</span>
                    <div className="flex flex-row">
                        <div className="transition duration-200 ease-in-out flex items-center text-sm px-1 mr-2 bg-transparent hover:bg-indigo-600 cursor-move">
                            <FontAwesomeIcon icon="arrows-alt" />
                        </div>
                        <Dropdown show={isOptions}
                            onToggle={next => this.setState({isOptions: next})}>
                            <div className="relative flex flex-row-reverse">
                                <Dropdown.Toggle>
                                    {(props, meta) => (
                                        <SecondaryButton icon="ellipsis-v" 
                                            className="w-full flex items-center justify-center h-6 text-sm bg-transparent hover:bg-indigo-600"
                                            onClick={event => meta.toggle(!isOptions, event)} />
                                    )}
                                </Dropdown.Toggle>
                                <Dropdown.Menu show={isOptions} flip={true}>
                                    {(props) => (
                                        <>
                                            {isOptions && (
                                                <div className="absolute z-20 shadow-lg w-32" style={{top: "1.5rem"}}>
                                                    <div onClick={this.showPreviewModal}>
                                                        <ContextMenuItem text="Preview"
                                                            icon="search" />
                                                    </div>
                                                    <div onClick={this.showRenameModal}>
                                                        <ContextMenuItem text="Change Name"
                                                            icon="edit" />
                                                    </div>
                                                    <div onClick={this.showDeleteModal}>
                                                        <ContextMenuItem text="Delete"
                                                            icon="trash-alt"
                                                            className="bg-red-300 text-red-700 hover:bg-red-800 hover:text-red-200" />
                                                    </div>
                                                </div>
                                            ) || 
                                            !isOptions && (
                                                <div className=""></div>
                                            )}
                                        </>
                                    )}
                                </Dropdown.Menu>
                            </div>
                        </Dropdown>
                    </div>
                </div>

                <CustomModal show={isRename}
                    onHide={() => this.hideRenameModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Preview</h2>
                            <h2 className="text-lg font-bold">&apos;{source.name}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            <Input value={renameValue} 
                                label="Name" 
                                placeholder="Enter source name..."
                                onChange={event => this.setState({renameValue: event.target.value})} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.renameSource} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideRenameModal()} />
                        </div>
                    </div>
                </CustomModal>
                
                <CustomModal show={isPreview}
                    onHide={() => this.hidePreviewModal()}>
                    <div className="p-4 bg-gray-100" 
                        style={{width: "75%"}}>
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Preview</h2>
                            <h2 className="text-lg font-bold">&apos;{source.name}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            <ShowResult result={preview} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <SecondaryButton text="Close" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hidePreviewModal()} />
                        </div>
                    </div>
                </CustomModal>

                <CustomModal show={isDelete}
                    onHide={() => this.hideDeleteModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium text-red-600">Delete</h2>
                            <h2 className="text-lg font-bold text-red-600">&apos;{source.name}&apos;?</h2>
                        </div>
                        <div className="mb-4 w-full flex items-center">
                            <span className="text-center">Are you sure you want to delete the source &apos;{source.name}&apos;? This action is <span className="text-red-600 font-bold">irreversible</span> and might break any nodes using this source.</span>
                        </div>
                        <div className="flex flex-row-reverse">
                            <CancelButton text="Delete" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.deleteSource} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideDeleteModal()} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}

type SourceSidebarProps = {
    sources?: DataSourceObject[];
}

type SourceSidebarState = {
    isAddingSource: boolean;
    sourceName: string;
    sourceType: SourceType | undefined;
    sourceFile: File | undefined;
}

export default class SourceSidebar extends React.Component<SourceSidebarProps, SourceSidebarState> {
    constructor(props: SourceSidebarProps) {
        super(props);

        this.state = {
            isAddingSource: false,
            sourceName: "",
            sourceType: undefined,
            sourceFile: undefined
        };

        this.showAddSourceModal = this.showAddSourceModal.bind(this);
        this.hideAddSourceModal = this.hideAddSourceModal.bind(this);

        this.onChooseCSVFile = this.onChooseCSVFile.bind(this);
        this.onAddSource = this.onAddSource.bind(this);
    }

    showAddSourceModal(): void {
        this.setState({isAddingSource: true, sourceName: "", sourceType: undefined, sourceFile: undefined});
    }

    hideAddSourceModal(): void {
        this.setState({isAddingSource: false});
    }

    // TODO: Should be done as a dropdown
    async onChooseCSVFile(file: File): Promise<void> {
        this.setState({ sourceType: "CSV", sourceFile: file });
    }

    async onAddSource(): Promise<void> {
        const { sourceName, sourceType, sourceFile } = this.state;
        if (!sourceName || !sourceType || !sourceFile)
            return;

        const source = await store.dispatch(createNewDataSourceAsync(sourceName, sourceType));
        if (!source)
            return;

        if (sourceType === "CSV") {
            const csv = new CSVSource(source.sourceId, sourceFile);
            const loadResult = await csv.loadToDf();
            const saveResult = loadResult ? await csv.saveToDB() : false;
        }

        this.hideAddSourceModal();
    }

    render(): JSX.Element {
        const { sources } = this.props;
        const { isAddingSource, sourceName, sourceFile } = this.state;

        return (
            <>
                <div className="w-48 h-full flex flex-col bg-gray-600 justify-center">
                    <div className="p-2 text-md font-medium text-gray-300">Data Sources</div>
                    <Scrollbars>
                        <div className="grid gap-2">
                            <div className="grid gap-2">
                                {sources && sources.map((source, index) => (
                                    <SourceItem key={index} source={source} />
                                ))}
                            </div>
                            <OKButton icon="plus" text="Add Source"
                                className="text-xs w-full"
                                onClick={this.showAddSourceModal} />
                        </div>
                    </Scrollbars>
                </div>
                
                <CustomModal show={isAddingSource}
                    onHide={this.hideAddSourceModal}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Add Data Source</h2>
                        </div>
                        <div className="mb-4">
                            <Input value={sourceName}
                                label="Name"
                                placeholder="Enter source name..."
                                onChange={event => this.setState({sourceName: event.target.value})} />
                        </div>
                        <div className="flex flex-col mb-4">
                            {sourceFile && (
                                <div className="flex flex-col mb-2">
                                    <span className="text-xs text-gray-600">File</span>
                                    <span className="px-2 text-sm">{sourceFile?.name}</span>
                                </div>
                            )}
                            <FileButton icon="plus" text="Add CSV..." accept=".csv"
                                className="text-xs w-full"
                                onChange={this.onChooseCSVFile} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save"
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.onAddSource} />
                            <SecondaryButton text="Cancel"
                                className="px-2 py-1 text-xs"
                                onClick={this.hideAddSourceModal} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}