import React from "react";
import store from "../app/store";
import DBService from "../db/DBService";
import { UserObject } from "../db/models";
import { getCurrentUser, removeCurrentOfflineUserAsync } from "../features/users/userSlice";
import { CancelButton, OKButton } from "./CustomComponents";
import logo from "../assets/nodebooks-logo-low-3.png";

type TopbarProps = {

}

type TopbarState = {
    user: UserObject | undefined;
}

export default class Topbar extends React.Component<TopbarProps, TopbarState> {
    constructor(props: TopbarProps) {
        super(props);

        this.state = {
            user: undefined
        };
    }

    async componentDidMount(): Promise<void> {
        const user = getCurrentUser(store.getState());
        this.setState({user});
    }

    render(): JSX.Element {
        const { user } = this.state;
        
        return (
            <div className="w-screen h-16 flex flex-row justify-between items-center bg-gray-800 text-gray-200">
                <div className="pl-3 flex items-center">
                    <div className="w-40">
                        <img src={logo} />
                    </div>
                </div>

                {user && (
                    <div className="flex flex-row items-center px-4">
                        <span className="mr-4">{user.name}</span>
                        <CancelButton icon="sign-out-alt"
                            tooltip="Sign Out"
                            onClick={async () => await store.dispatch(removeCurrentOfflineUserAsync())} />
                        {/*
                        <OKButton icon="download"
                            tooltip="Download DB"
                            onClick={async () => {
                                const saveFile = async (blob: Blob) => {
                                    const a = document.createElement("a");
                                    a.download = "exported-db.json";
                                    a.href = URL.createObjectURL(blob);
                                    a.addEventListener("click", (e) => {
                                        setTimeout(() => URL.revokeObjectURL(a.href), 30 * 1000);
                                    });
                                    a.click();
                                };
                                const json = await DBService.db?.exportDatabase() ?? "";
                                const blob = new Blob([json], { type: "application/json" });
                                await saveFile(blob);
                            }} />
                        */}
                    </div>
                )}
            </div>
        );
    }
}