import { Series } from "danfojs";
import store from "../app/store";
import { getCurrentNotebook, getNodeById, getSeparateConnectionsByNodeId } from "../features/notebooks/notebookSlice";
import { PrimitiveTree, Primitives, NodeError, Slot, Property, PrimitiveObject, NodeErrorHandler } from "../types";
import NodeTypeManager from "./NodeTypeManager";

class NodeExecManager {
    private outputs: {
        [cellId: string]: {
            [nodeId: string]: PrimitiveTree
        }
    } = {};
    private variables: {
        [variableId: string]: Primitives | Series
    } = {};
    private errors: {
        [cellId: string]: NodeError[]
    } = {};
    private cellExecs: {
        [cellId: string]: () => Promise<void>;
    } = {};

    private currentNotebookId = "";

    constructor() {
        this.onStoreUpdate = this.onStoreUpdate.bind(this);
        store.subscribe(this.onStoreUpdate);
    }

    onStoreUpdate(): void {
        const currentNotebook = store.getState().notebook.currentNotebook;
        if (!currentNotebook)
            return;

        if (this.currentNotebookId !== currentNotebook.notebookId) {
            this.reset();
            this.currentNotebookId = currentNotebook.notebookId;
        }
    }

    reset() {
        this.outputs = {};
        this.variables = {};
        this.errors = {};
    }

    resetCellOutputs(cellId: string) {
        this.outputs[cellId] = {};
        this.errors[cellId] = [];
    }
    
    getVariable(variableId: string): Primitives | Series | undefined {
        let variable: Primitives | Series | undefined = this.variables[variableId];
        if (variable == null) {
            const notebook = getCurrentNotebook(store.getState());
            if (!notebook)
                return variable;
            
            const variableIndex = notebook.variables.findIndex(v => v.variableId === variableId);
            if (variableIndex < 0)
                return variable;

            const variableObj = notebook.variables[variableIndex];
            variable = variableObj.value;
        }

        return variable;
    }

    setVariable(variableId: string, value: Primitives | Series): void {
        this.variables[variableId] = value;
    }

    getErrors(cellId: string): NodeError[] {
        if (!this.errors[cellId])
            this.errors[cellId] = [];

        return this.errors[cellId];
    }

    addCellExec(cellId: string, exec: () => Promise<void>): void {
        this.cellExecs[cellId] = exec;
    }

    async runCells(notebookId: string) {
        const execs: (() => Promise<void>)[] = [];
        const notebook = getCurrentNotebook(store.getState());
        if (!notebook || notebook?.notebookId !== notebookId)
            return;

        notebook.cells.forEach(cell => {
            if (this.cellExecs[cell.cellId])
                execs.push(this.cellExecs[cell.cellId]);
        });

        this.variables = {};

        for (let i = 0; i < execs.length; i++) {
            const exec = execs[i];
            await exec();
        }
    }

    async runFunction(cellId: string, nodeId: string): Promise<PrimitiveTree> {
        // Reset the saved outputs and errors
        return await this.runFunc(cellId, nodeId);
    }

    async runFunctions(cellId: string, nodeIds: string[], runInSequence = false): Promise<PrimitiveTree[]> {
        // Reset the saved outputs and errors
        let results: PrimitiveTree[] = [];
        if (runInSequence) {
            for (const nodeId of nodeIds) {
                const result = await this.runFunc(cellId, nodeId);
                results.push(result);
            }
        } else {
            const actions = nodeIds.map(nodeId => this.runFunc(cellId, nodeId));
            results = await Promise.all(actions);
        }

        return results;
    }
    
    private async runFunc(cellId: string, nodeId: string): Promise<PrimitiveTree> {
        // Use cached output if found
        const savedOutput = this.outputs[cellId][nodeId];
        if (savedOutput != null)
            return savedOutput;

        const node = getNodeById(store.getState(), cellId, nodeId);
        if (!node)
            return {};

        const type = NodeTypeManager.getType(node.type);
        if (!type)
            return {};

        const connections = getSeparateConnectionsByNodeId(store.getState(), cellId, nodeId);

        type Result = {
            slotId: string;
            result: Primitives | Series
        }

        const connectionIndex = (slotId: string) => {
            return connections.toConnections.findIndex(conn => conn.toNodeId === nodeId && conn.toSlotId === slotId);
        };

        const getInputs = async (slot: Slot): Promise<Result | undefined> => {
            const connIndex = connectionIndex(slot.id);
            if (connIndex < 0) {
                // Get default value instead
                let nodeInputValue;
                const nodeInputValIndex = node.inputValues.findIndex(val => val.id === slot.id);
                if (nodeInputValIndex >= 0)
                    nodeInputValue = node.inputValues[nodeInputValIndex].value;
                else
                    nodeInputValue = slot.defaultValue;

                if (nodeInputValue == null)
                    return { slotId: slot.id, result: {} };

                return { slotId: slot.id, result: nodeInputValue };
            }

            const connection = connections.toConnections[connIndex];
            const output = await this.runFunc(cellId, connection.fromNodeId);

            return { slotId: slot.id, result: output[connection.fromSlotId]};
        };

        const actions = type.inputSlots.map(getInputs);
        const results = await Promise.all(actions);

        const input: PrimitiveTree = {};
        for (let i = 0; i < results.length; i++) {
            const result = results[i];
            if (!result)
                return {};

            // If no result but is still connected
            if (!result.result && connectionIndex(result.slotId) >= 0)
                return {};

            input[result.slotId] = result.result;
        }

        const properties: Property[] = [];
        if (type) {
            type.properties.forEach(property => {
                let prop: Property = property;
                if (node) {
                    const propIndex = node.properties.findIndex(prop => prop.id === property.id);
                    if (propIndex >= 0) {
                        const nodeProperty = node.properties[propIndex];
                        prop = { id: property.id, type: property.type, defaultValue: nodeProperty.value };
                    }
                }

                if (prop.defaultValue != null)
                    properties.push(prop);
            });
        }
        const propertyObj: PrimitiveObject = {};
        properties.forEach(property => {
            if (property.defaultValue != null)
                propertyObj[property.id] = property.defaultValue;
        });

        const errorFunctionCreator = (type: "debug" | "info" | "warning" | "error") => {
            return (message: string) => {
                this.errors[cellId].push({
                    type,
                    message: `'${node.name}' Node: ${message}`
                });
            };
        };

        const errorHandler: NodeErrorHandler = {
            debug: errorFunctionCreator("debug"),
            log: errorFunctionCreator("info"),
            warning: errorFunctionCreator("warning"),
            error: errorFunctionCreator("error")
        };

        try {
            const output = await type.nodeFunction(input, propertyObj === {} ? undefined : propertyObj, errorHandler);
            // Store function outputs in a lookup table
            this.outputs[cellId][nodeId] = output;

            return output;
        } catch(err) {
            console.error(err);
            return {};
        }
    }
}

export default new NodeExecManager();