const addNumbers: NodeType = {
    id: "add-numbers",
    name: "Add Numbers",
    description: Descriptions.NodeSpecific.AddNumbers.Description,
    //variableName: "addNumberResult",
    style: {
        width: 130
    },
    inputSlots: [
        {
            id: "addend1",
            description: Descriptions.NodeSpecific.AddNumbers.Addend1Input,
            type: Types.Number
        },
        {
            id: "addend2",
            description: Descriptions.NodeSpecific.AddNumbers.Addend2Input,
            type: Types.Number
        }
    ],
    outputSlots: [
        {
            id: "sum",
            description: Descriptions.Common.ResultOutput,
            type: Types.Number
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["addend1"]))
            errorHandler.error(Errors.MissingNodeInput("addend1"));

        if (Meta.isEmpty(input["addend2"]))
            errorHandler.error(Errors.MissingNodeInput("addend2"));

        if (Meta.isEmpty(input["addend1"]) || Meta.isEmpty(input["addend2"]))
            return {};
        
        const addend1 = input["addend1"] as number;
        const addend2 = input["addend2"] as number;
        const sum = addend1 + addend2;
        return {sum};
    }
};

