const logNumbers: NodeType = {
    id: "log-numbers",
    name: "Log Numbers",
    description: Descriptions.NodeSpecific.LogNumbers.Description,
    //variableName: "powerNumberResult",
    style: {
        width: 150,
    },
    inputSlots: [
        {
            id: "base",
            description: Descriptions.NodeSpecific.LogNumbers.BaseInput,
            type: Types.Number
        },
        {
            id: "number",
            description: Descriptions.NodeSpecific.LogNumbers.NumberInput,
            type: Types.Number
        }
    ],
    outputSlots: [
        {
            id: "log",
            description: Descriptions.Common.ResultOutput,
            type: Types.Number
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["base"]))
            errorHandler.error(Errors.MissingNodeInput("base"));

        if (Meta.isEmpty(input["number"]))
            errorHandler.error(Errors.MissingNodeInput("number"));

        if (Meta.isEmpty(input["base"]) || Meta.isEmpty(input["number"]))
            return {};
        
        const base = input["base"] as number;
        const number = input["number"] as number;
        const log = Math.log(number) / Math.log(base);
        return {log};
    }
};

