import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import cuid from "cuid";
import React, { useRef } from "react";
import TextareaAutosize from "react-textarea-autosize";
import ReactTooltip from "react-tooltip";
import Modal, { ModalHandle, ModalProps, RenderModalBackdropProps } from "react-overlays/Modal";
import { twMerge } from "tailwind-merge";
import Select, { ActionMeta, GroupBase, SingleValue, StylesConfig } from "react-select";
import Colors from "../utils/Colors";
import { DropdownItem } from "../types";

type ButtonProps = {
    className?: string;
    icon?: IconProp;
    text?: string;
    tooltip?: string;
    isDisabled?: boolean;
    onClick?: React.MouseEventHandler<HTMLButtonElement>;
}

export function PrimaryButton(props: ButtonProps): JSX.Element {
    const uniqueId = cuid();
    return (
        <>
            <button data-tip data-for={uniqueId} className={twMerge(`transition duration-200 ease-in-out p-2 flex items-center select-none ${props.isDisabled ? "bg-blue-800 text-blue-600 cursor-default" : "bg-blue-500 text-blue-100 hover:bg-blue-400 hover:text-blue-100"}`, props.className)}
                disabled={props.isDisabled}
                onClick={props.onClick}>
                {props.icon && (
                    <FontAwesomeIcon icon={props.icon} />
                )}
                {props.text && (
                    <span className={`${props.icon ? "ml-2" : ""}`}>{props.text}</span>
                )}
            
            </button>
            {props.tooltip && !props.isDisabled && (
                <ReactTooltip id={uniqueId} place="bottom" effect="float">
                    {props.tooltip}
                </ReactTooltip>
            )}
        </>
    );
}

