type FileButtonProps = ButtonProps & {
    accept?: string;
    onChange?: (file: File) => Promise<void>;
};
export function FileButton(props: FileButtonProps): JSX.Element {
    const input = useRef<HTMLInputElement>(null);
    const uniqueId = cuid();
    const onClick = () => {
        if (input.current)
            input.current.click();
    };

    return (
        <>
            <button data-tip data-for={uniqueId} className={twMerge("transition duration-200 ease-in-out p-2 flex items-center bg-orange-500 text-orange-100 hover:bg-orange-400 hover:text-orange-100 select-none", props.className)}
                disabled={props.isDisabled}
                onClick={onClick}>
                {props.icon && (
                    <FontAwesomeIcon icon={props.icon} />
                )}
                {props.text && (
                    <span className={`${props.icon ? "ml-2" : ""}`}>{props.text}</span>
                )}
            </button>
            {!props.isDisabled && (
                <input ref={input} type="file" accept={props.accept} style={{display: "none"}}
                    onChange={async event => {
                        if (event.target.files && props.onChange) {
                            const file = event.target.files[0];
                            await props.onChange(file);
                        }
                    }} />
            )}
            {props.tooltip && !props.isDisabled && (
                <ReactTooltip id={uniqueId} place="bottom" effect="float">
                    {props.tooltip}
                </ReactTooltip>
            )}
        </>
    );
}

type TextAreaProps = {
    className?: string;
    value?: string;
    label?: string;
    readonly?: boolean;
    regex?: RegExp;
    onChange?(event: React.ChangeEvent<HTMLTextAreaElement>): void;
    onKeyDown?(event: React.KeyboardEvent<HTMLTextAreaElement>): void;
}

export function TextArea(props: TextAreaProps): JSX.Element {
    let style: React.CSSProperties = {
        boxShadow: "inset 0 -1px 1px 0 rgb(0 0 0 / 15%)"
    };
    if (props.value != null && props.regex) {
        const match = props.value.match(props.regex);
        const isMatch = match != null && match.length > 0;
        style = {
            ...style,
            border: `2px solid ${isMatch ? Colors.Green[500] : Colors.Red[500]}`
        };
    }

    return (
        <div className="flex flex-col">
            {props.label && (
                <label className="text-xs text-gray-600 mb-2">{props.label}</label>
            )}
            {!props.readonly && (
                <TextareaAutosize minRows={2}
                    value={props.value} 
                    onChange={props.onChange}
                    onKeyDown={props.onKeyDown}
                    className={twMerge("w-full p-2 text-sm bg-gray-200 shadow-inner", props.className)} />
            ) ||
            props.readonly && (
                <div className={twMerge("p-2 text-sm bg-gray-500 text-gray-900 overflow-hidden w-full", props.className)}
                    style={{wordWrap: "break-word"}}>
                    {props.value}
                </div>
            )}
        </div>
    );
}

type CustomModalProps = ModalProps & React.RefAttributes<ModalHandle>;

export function CustomModal(props: CustomModalProps): JSX.Element {
    const renderBackdrop = (props: RenderModalBackdropProps) => {
        return (<div className="fixed top-0 right-0 bottom-0 left-0 bg-gray-900 opacity-50" {...props}></div>);
    };
    return (
        <Modal className="fixed flex justify-center items-center w-full h-full" 
            style={{top: "50%", left: "50%", transform: "translateX(-50%) translateY(-50%)"}} 
            renderBackdrop={renderBackdrop} 
            {...props}>
            {props.children}
        </Modal>
    );
}

type InputProps = {
    value?: string;
    label?: string;
    icon?: IconProp;
    type?: string;
    regex?: RegExp;
    placeholder?: string;
    readonly?: boolean;
    className?: string;
    onChange?: React.ChangeEventHandler<HTMLInputElement>;
}

export function Input(props: InputProps): JSX.Element {
    let style: React.CSSProperties = {
        boxShadow: "inset 0 -1px 1px 0 rgb(0 0 0 / 15%)"
    };
    if (props.value != null && props.regex) {
        const match = props.value.match(props.regex);
        const isMatch = match != null && match.length > 0;
        style = {
            ...style,
            border: `2px solid ${isMatch ? Colors.Green[500] : Colors.Red[500]}`
        };
    }

    return (
        <div className="flex flex-col overflow-hidden">
            {props.label && (
                <label className="text-xs text-gray-600">{props.label}</label>
            )}
            <div className="flex flex-row items-center"
                style={{boxShadow: "inset 0 -1px 1px 0 rgb(0 0 0 / 15%)"}}>
                {props.icon && (
                    <div className="px-2">
                        <FontAwesomeIcon icon={props.icon}
                            className="text-sm text-gray-600" />
                    </div>
                )}
                {!props.readonly && (
                    <input className={twMerge("p-2 text-sm bg-gray-100 text-gray-800 w-full", props.className)}
                        style={style} 
                        type={props.type} 
                        placeholder={props.placeholder}
                        value={props.value}
                        onChange={props.onChange} />
                ) ||
                props.readonly && (
                    <div className={twMerge("p-2 text-sm bg-gray-500 text-gray-900 overflow-hidden w-full", props.className)}
                        style={{textOverflow: "ellipsis"}}>
                        {props.value}
                    </div>
                )}
            </div>
        </div>
    );
}

type CheckboxProps = {
    value: boolean;
    label?: string;
    checkType?: IconProp;
    className?: string;
    onToggle?: () => Promise<void>;
};

export function Checkbox(props: CheckboxProps): JSX.Element {
    const icon = props.checkType ?? "check";
    return (
        <div className="flex flex-row items-center">
            {props.label && (
                <label className="text-xs text-gray-600 mr-4">{props.label}</label>
            )}
            <div className={twMerge("flex items-center justify-center w-4 h-4 shadow-inner bg-gray-100 cursor-pointer", props.className)}
                onClick={async () => {
                    if (props.onToggle)
                        await props.onToggle();
                }}>
                {props.value && (
                    <FontAwesomeIcon className="text-xs" icon={icon} />
                )}
            </div>
        </div>
    );
}

type DropdownProps = {
    options: DropdownItem[];
    label?: string;
    onChange?: ((newValue: SingleValue<DropdownItem>, actionMeta: ActionMeta<DropdownItem>) => void);
};

export function Dropdown(props: DropdownProps): JSX.Element {
    const style: StylesConfig<DropdownItem, false, GroupBase<DropdownItem>> = {
        control: (provided) => ({
            ...provided,
            borderRadius: 0,
            borderWidth: 0,
            backgroundColor: "#f7fafc",
            borderBottomWidth: 1,
            fontSize: "0.875rem"
        }),
        menu: (provided) => ({
            ...provided,
            borderRadius: 0
        }),
        option: (provided, state) => ({
            ...provided,
            backgroundColor: state.isSelected ? "#4299e1" : (state.isFocused ? "#bee3f8" : "#f7fafc")
        })
    };
    return (
        <div className="flex flex-col">
            {props.label && (
                <label className="text-xs text-gray-600">{props.label}</label>
            )}
            <Select styles={style}
                options={props.options}
                isSearchable={false}
                onChange={props.onChange} />
        </div>
    );
}

