import { Vector2d } from "konva/lib/types";
import React from "react";
import { Group, Image } from "react-konva";
import useImage from "use-image";

type GridArrayProps = {

}

type GridArrayState = {
    currentIndex: Vector2d;
    scale: number;
    containerWidth: number;
}

export default class GridArray extends React.Component<GridArrayProps, GridArrayState> {
    constructor(props: GridArrayProps) {
        super(props);

        this.state = {
            currentIndex: { x: 0, y: 0 },
            scale: 1,
            containerWidth: 0
        };

        this.setPos = this.setPos.bind(this);
        this.setScale = this.setScale.bind(this);
        this.setContainerWidth = this.setContainerWidth.bind(this);
    }

    setPos(x: number, y: number): void {
        const { currentIndex, scale } = this.state;

        const newIndex: Vector2d = {
            x: Math.round(x / WIDTH / scale),
            y: Math.round(y / WIDTH / scale)
        };

        if (currentIndex.x !== newIndex.x || currentIndex.y !== newIndex.y)
            this.setState({currentIndex: newIndex});
    }

    setScale(scale: number): void {
        this.setState({scale});
    }

    setContainerWidth(containerWidth: number): void {
        this.setState({containerWidth});
    }

    render(): JSX.Element {
        const { currentIndex, scale, containerWidth } = this.state;

        return (
            <GridArrayInner currentIndex={currentIndex} scale={scale} containerWidth={containerWidth} />
        );
    }
}

type GridArrayInnerProps = {
    currentIndex: Vector2d;
    scale: number;
    containerWidth: number;
}

const WIDTH = 1000;

const generateColumns = (scale: number, containerWidth: number): number[] => {
    const diff = scale * WIDTH * 2 - containerWidth;
    let count = 0;
    if (diff < 0)
        count = Math.ceil(-diff / 250);

    const cols: number[] = [];
    for (let i = -2; i > -2 - count; i--) {
        cols.push(i);
    }

    return cols;
};

const indexToCoord = (index: number, n: number) => {
    return -(index + n) * WIDTH;
};

function GridArrayInner(props: GridArrayInnerProps) {
    const [image] = useImage("/grid.png");

    return (
        <Group>
            <Image key={"-1-1"} opacity={0.2} x={indexToCoord(props.currentIndex.x, -1)} y={indexToCoord(props.currentIndex.y, -1)} width={WIDTH} height={WIDTH} image={image} />
            <Image key={"0-1"}  opacity={0.2} x={indexToCoord(props.currentIndex.x, 0)}  y={indexToCoord(props.currentIndex.y, -1)} width={WIDTH} height={WIDTH} image={image} />
            <Image key={"1-1"}  opacity={0.2} x={indexToCoord(props.currentIndex.x, 1)}  y={indexToCoord(props.currentIndex.y, -1)} width={WIDTH} height={WIDTH} image={image} />
            <Image key={"-10"}  opacity={0.2} x={indexToCoord(props.currentIndex.x, -1)} y={indexToCoord(props.currentIndex.y, 0)}  width={WIDTH} height={WIDTH} image={image} />
            <Image key={"00"}   opacity={0.2} x={indexToCoord(props.currentIndex.x, 0)}  y={indexToCoord(props.currentIndex.y, 0)}  width={WIDTH} height={WIDTH} image={image} />
            <Image key={"10"}   opacity={0.2} x={indexToCoord(props.currentIndex.x, 1)}  y={indexToCoord(props.currentIndex.y, 0)}  width={WIDTH} height={WIDTH} image={image} />
            <Image key={"-11"}  opacity={0.2} x={indexToCoord(props.currentIndex.x, -1)} y={indexToCoord(props.currentIndex.y, 1)}  width={WIDTH} height={WIDTH} image={image} />
            <Image key={"01"}   opacity={0.2} x={indexToCoord(props.currentIndex.x, 0)}  y={indexToCoord(props.currentIndex.y, 1)}  width={WIDTH} height={WIDTH} image={image} />
            <Image key={"11"}   opacity={0.2} x={indexToCoord(props.currentIndex.x, 1)}  y={indexToCoord(props.currentIndex.y, 1)}  width={WIDTH} height={WIDTH} image={image} />
            <>
                {generateColumns(props.scale, props.containerWidth).map((value, index) => (
                    <React.Fragment key={`${value}-group`}>
                        <Image key={`${value}-1`} opacity={0.2} x={indexToCoord(props.currentIndex.x, value)} y={indexToCoord(props.currentIndex.y, -1)} width={WIDTH} height={WIDTH} image={image} />
                        <Image key={`${value}0`}  opacity={0.2} x={indexToCoord(props.currentIndex.x, value)} y={indexToCoord(props.currentIndex.y, 0)}  width={WIDTH} height={WIDTH} image={image} />
                        <Image key={`${value}1`}  opacity={0.2} x={indexToCoord(props.currentIndex.x, value)} y={indexToCoord(props.currentIndex.y, 1)}  width={WIDTH} height={WIDTH} image={image} />
                    </React.Fragment>
                ))}
            </>
        </Group>
    );
}