import { DataFrame, Series } from "danfojs";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler } from "../types";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import Meta from "../utils/Meta";
import Regexes from "../utils/Regexes";
import { Types } from "../utils/Types";

const describeDataFrame: NodeType = {
    id: "describe-dataframe",
    name: "Describe DataFrame",
    description: Descriptions.NodeSpecific.DescribeDataFrame.Description,
    style: {
        width: 160
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.DataFrameDescriptive.DataFrameOutput,
            type: Types.DataFrame
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const nullCols: string[] = [];
        df.columns.forEach(col => {
            const column = df.column(col);
            if (column.count() !== column.size)
                nullCols.push(col);
        });

        if (nullCols.length > 0) {
            errorHandler.error(Errors.DataFrameNullColumnsCannotDescribe(nullCols));
            return {};
        }

        const newDf = df.describe();
        return {
            output: newDf
        };
    }
};

const histogram: NodeType = {
    id: "histogram",
    name: "Histogram",
    description: Descriptions.NodeSpecific.Histogram.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.SeriesInput,
            type: Types.Series
        }
    ],
    outputSlots: [
        {
            id: "labels",
            description: Descriptions.NodeSpecific.Histogram.LabelsOutput,
            type: Types.Series
        },
        {
            id: "count",
            description: Descriptions.NodeSpecific.Histogram.CountOutput,
            type: Types.Series
        }
    ],
    properties: [
        {
            id: "name",
            description: Descriptions.NodeSpecific.Histogram.NameProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelRegex, Regexes.emptyRegex)
        },
        {
            id: "bins",
            description: Descriptions.NodeSpecific.Histogram.BinsProperty,
            type: "number",
            defaultValue: 20
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const series = input["input"] as Series;
        const bins = properties["bins"] as number;
        const name = properties["name"] as string;

        if (bins <= 0) {
            errorHandler.error(Errors.NumberCannotBeZeroOrNegative("bins", "property"));
            return {};
        }

        let colName = name;
        if (!Regexes.isMatch(name, Regexes.labelRegex))
            colName = "Count";

        const min = series.min();
        const max = series.max();
        const range = max - min;
        const step = range / bins;

        const labels: number[] = [];
        const count: number[] = [];
        const digits = 4;
        const fraction = Math.pow(10, digits);
        for (let i = 0; i < bins; i++) {
            labels.push(Math.round((step * i + min) * fraction) / fraction);
            count.push(0);
        }

        const values = series.values as number[];
        values.forEach(value => {
            const index = Math.floor((value - min) / step);
            count[index] += 1;
        });

        return {
            labels: new Series(labels, { columns: ["Labels"] }),
            count: new Series(count, { columns: [colName] })
        };
    }
};

const headDataFrame: NodeType = {
    id: "head-dataframe",
    name: "DataFrame Head",
    description: Descriptions.NodeSpecific.HeadDataFrame.Description,
    //variableName: "dataFrameOutputResult",
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.DataFrameDescriptive.DataFrameOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "rowCount",
            description: Descriptions.Common.DataFrame.RowCountProperty,
            type: "number",
            defaultValue: 10
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const rowCount = properties["rowCount"] as number;
        const head = (df.shape[0] >= rowCount && rowCount > 0) ? df.head(rowCount) : df;
        return {
            output: head
        };
    }
};

