import React from "react";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { BrowserRouter, Link, Route, Switch, useParams } from "react-router-dom";
import OfflineApp from "./OfflineApp";
import OnlineApp from "./OnlineApp";
import { twMerge } from "tailwind-merge";

library.add(fas, fab);

type ConnectivityItemProps = {
    to: string;
    text: string;
    className?: string;
}

function ConnectivityItem(props: ConnectivityItemProps): JSX.Element {
    return (
        <Link className={twMerge("transition duration-200 ease-in-out w-full p-4 flex justify-center bg-gray-800 text-gray-200 hover:bg-indigo-500 hover:text-indigo-100", props.className)}
            to={props.to}>
            {props.text}
        </Link>
    );
}

function ConnectivityPage(): JSX.Element {
    return (
        <div className="w-screen h-screen flex justify-center items-center bg-gray-200">
            <div className="w-64 py-4 grid gap-2 bg-gray-900">
                <ConnectivityItem to="/offline" text="Offline" />
                <ConnectivityItem to="/" text="Online" />
            </div>
        </div>
    );
}

type AppProps = {

}

export default function App(props: AppProps): JSX.Element {
    return (
        <>
            <Switch>
                {/* TODO: Change to this once online mode is implemented
                <Route path="/:connectivity" component={AppWrapper} />
                <Route exact path="/" component={ConnectivityPage} />
                */}
                <Route path="/" component={AppWrapper} />
            </Switch>
        </>
    );
}

type Params = {
    connectivity: string;
}

function AppWrapper(): JSX.Element | null {
    const { connectivity } = useParams<Params>();
    
    {/* TODO: Remove this return once online mode is implemented */}
    return (
        <BrowserRouter basename="/node-ml/offline">
            <OfflineApp />
        </BrowserRouter>
    );
    
    if (connectivity.toLowerCase() === "offline") {
        return (
            <BrowserRouter basename="/node-ml/offline">
                <OfflineApp />
            </BrowserRouter>
        );
    } else if (connectivity.toLowerCase() === "online") {
        return (
            <BrowserRouter basename="/node-ml/online">
                <OnlineApp />
            </BrowserRouter>
        );
    }
    else {
        return null;
    }
}
