import React from "react";
import Dropdown from "react-overlays/esm/Dropdown";
import { connect } from "react-redux";
import store, { RootState } from "../app/store";
import { CancelButton, ContextMenuItem, CustomModal, Input, OKButton, PrimaryButton, SecondaryButton } from "../components/CustomComponents";
import { UserObject } from "../db/models";
import { createNewUserAsync, deleteOfflineUserAsync, fetchOfflineUsersAsync, getUserList, setCurrentOfflineUserAsync, updateUserNameAsync } from "../features/users/userSlice";

type UserItemProps = {
    user: UserObject;
}

type UserItemState = {
    isOptions: boolean;
    isRename: boolean;
    renameValue: string;
    isDelete: boolean;
}

class UserItem extends React.Component<UserItemProps, UserItemState> {
    constructor(props: UserItemProps) {
        super(props);

        this.state = {
            isOptions: false,
            isRename: false,
            renameValue: "",
            isDelete: false
        };

        this.showRenameModal = this.showRenameModal.bind(this);
        this.hideRenameModal = this.hideRenameModal.bind(this);
        this.renameUser = this.renameUser.bind(this);

        this.showDeleteModal = this.showDeleteModal.bind(this);
        this.hideDeleteModal = this.hideDeleteModal.bind(this);
        this.deleteUser = this.deleteUser.bind(this);
    }

    showRenameModal(): void {
        this.setState({isOptions: false, isRename: true, renameValue: ""});
    }

    hideRenameModal(): void {
        this.setState({isRename: false});
    }

    async renameUser(): Promise<void> {
        const { user } = this.props;
        const { renameValue } = this.state;
        if (!renameValue)
            return;

        await store.dispatch(updateUserNameAsync(user.userId, renameValue));
        this.hideRenameModal();
    }

    showDeleteModal(): void {
        this.setState({isOptions: false, isDelete: true});
    }

    hideDeleteModal(): void {
        this.setState({isDelete: false});
    }

    async deleteUser(): Promise<void> {
        const { user } = this.props;
        await store.dispatch(deleteOfflineUserAsync(user.userId));
        this.hideDeleteModal();
    }

    render(): JSX.Element {
        const { user } = this.props;
        const { isOptions, isRename, renameValue, isDelete } = this.state;

        return (
            <>
                <div className="transition duration-200 ease-in-out w-full p-4 flex justify-between bg-gray-700 text-gray-200 hover:bg-indigo-400 hover:text-indigo-100 cursor-pointer"
                    onClick={async () => await store.dispatch(setCurrentOfflineUserAsync(user.userId))}>
                    <div className="w-full flex justify-center select-none">{user.name}</div>
                    <div className="">
                        <Dropdown show={isOptions}
                            onToggle={next => this.setState({isOptions: next})}>
                            <div className="relative flex flex-row-reverse">
                                <Dropdown.Toggle>
                                    {(props, meta) => (
                                        <SecondaryButton icon="ellipsis-v" 
                                            className="w-full flex items-center justify-center h-6 text-sm bg-transparent hover:bg-indigo-600"
                                            onClick={event => {
                                                event.preventDefault();
                                                event.stopPropagation();
                                                meta.toggle(!isOptions, event);
                                            }} />
                                    )}
                                </Dropdown.Toggle>
                                <Dropdown.Menu show={isOptions} flip={true}>
                                    {(props) => (
                                        <>
                                            {isOptions && (
                                                <div className="absolute z-20 shadow-lg w-32" style={{top: "1.5rem"}}>
                                                    <div onClick={event => {
                                                        event.preventDefault();
                                                        event.stopPropagation();
                                                        this.showRenameModal();
                                                    }}>
                                                        <ContextMenuItem text="Change Name"
                                                            icon="edit" />
                                                    </div>
                                                    <div onClick={event => {
                                                        event.preventDefault();
                                                        event.stopPropagation();
                                                        this.showDeleteModal();
                                                    }}>
                                                        <ContextMenuItem text="Delete"
                                                            icon="trash-alt"
                                                            className="bg-red-300 text-red-700 hover:bg-red-800 hover:text-red-200" />
                                                    </div>
                                                </div>
                                            ) ||
                                                !isOptions && (
                                                    <div className=""></div>
                                                )}
                                        </>
                                    )}
                                </Dropdown.Menu>
                            </div>
                        </Dropdown>
                    </div>
                </div>

                <CustomModal show={isRename}
                    onHide={() => this.hideRenameModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Rename</h2>
                            <h2 className="text-lg font-bold">&apos;{user.name}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            <Input value={renameValue} 
                                label="Name" 
                                placeholder="Enter notebook name..."
                                onChange={event => this.setState({renameValue: event.target.value})} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.renameUser} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideRenameModal()} />
                        </div>
                    </div>
                </CustomModal>

                <CustomModal show={isDelete}
                    onHide={() => this.hideDeleteModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium text-red-600">Delete</h2>
                            <h2 className="text-lg font-bold text-red-600">&apos;{user.name}&apos;?</h2>
                        </div>
                        <div className="mb-4 w-full flex items-center">
                            <span className="text-center">Are you sure you want to delete the user &apos;{user.name}&apos;? This action is <span className="text-red-600 font-bold">irreversible.</span></span>
                        </div>
                        <div className="flex flex-row-reverse">
                            <CancelButton text="Delete" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.deleteUser} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideDeleteModal()} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}

type UsersPageProps = {
    users: UserObject[];
}

type UsersPageState = {
    isAddingUser: boolean;
    userName: string;
}

const mapStateToProps = (state: RootState) => {
    return {
        users: state.user.userList
    };
};

class UsersPageInner extends React.Component<UsersPageProps, UsersPageState> {
    constructor(props: UsersPageProps) {
        super(props);

        this.state = {
            isAddingUser: false,
            userName: ""
        };

        this.showAddUserModal = this.showAddUserModal.bind(this);
        this.hideAddUserModal = this.hideAddUserModal.bind(this);
        this.addUser = this.addUser.bind(this);
    }

    async componentDidMount(): Promise<void> {
        await store.dispatch(fetchOfflineUsersAsync());
    }

    showAddUserModal(): void {
        this.setState({isAddingUser: true});
    }

    hideAddUserModal(): void {
        this.setState({isAddingUser: false});
    }

    async addUser(): Promise<void> {
        const { userName } = this.state;
        if (!userName)
            return;

        await store.dispatch(createNewUserAsync(userName));
        this.hideAddUserModal();
    }

    render(): JSX.Element {
        const { users } = this.props;
        const { isAddingUser, userName } = this.state;
        const sortedUsers = users.slice().sort((a, b) => a.name.localeCompare(b.name));

        return (
            <div className="w-screen h-screen flex justify-center items-center bg-gray-200">
                <div className="flex flex-col">
                    <div className="w-64 py-4 mb-4 grid gap-2 bg-gray-900">
                        <span className="px-4 mb-2 text-xl text-gray-300">Users</span>
                        {sortedUsers.map((user, index) => (
                            <UserItem key={index} user={user} />
                        ))}
                    </div>
                    <OKButton icon="user-plus" text="Add User"
                        className="text-sm p-4 flex justify-center"
                        onClick={this.showAddUserModal} />
                </div>

                <CustomModal show={isAddingUser}
                    onHide={() => this.hideAddUserModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <h2 className="mb-4 text-lg font-medium">Create New User</h2>
                        <div className="mb-4">
                            <Input value={userName}
                                label="Name"
                                placeholder="Enter user name..."
                                onChange={event => this.setState({userName: event.target.value})} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Create"
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.addUser} />
                            <SecondaryButton text="Cancel"
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideAddUserModal()} />
                        </div>
                    </div>
                </CustomModal>
            </div>
        );
    }
}

const UsersPage = connect(mapStateToProps)(UsersPageInner);
export default UsersPage;