import React from "react";
import store, { RootState } from "../app/store";
import { fetchNotebookDetailsAsync } from "../features/notebooks/notebookSlice";
import { NotebookObject } from "../db/models";
import { connect } from "react-redux";
import { SecondaryButton } from "./CustomComponents";
import SourceSidebar from "./SourceSidebar";
import NotebookSidebar from "./NotebookSidebar";
import PropertiesSidebar from "./PropertiesSidebar";
import VariableSidebar from "./VariableSidebar";

enum SidebarTabState {
    Notebooks,
    Sources,
    Variables,
    Properties,
    None
}

type SidebarProps = {
    notebooks: NotebookObject[];
    currentNotebook: NotebookObject | undefined;
    selectedNode: {
        cellId: string;
        nodeId: string;
    } | undefined;
}

type SidebarState = {
    currentState: SidebarTabState;
}

const mapStateToProps = (state: RootState) => {
    return {
        notebooks: state.notebook.notebookDetails,
        currentNotebook: state.notebook.currentNotebook,
        selectedNode: state.notebook.selectedNode
    };
};

class SidebarInner extends React.Component<SidebarProps, SidebarState> {
    constructor(props: SidebarProps) {
        super(props);

        this.state = {
            currentState: SidebarTabState.Notebooks
        };

        this.onNotebookTabClick = this.onNotebookTabClick.bind(this);
        this.onSourcesTabClick = this.onSourcesTabClick.bind(this);
        this.onVariablesTabClick = this.onVariablesTabClick.bind(this);
        this.onPropertiesTabClick = this.onPropertiesTabClick.bind(this);
    }

    async componentDidMount(): Promise<void> {
        await store.dispatch(fetchNotebookDetailsAsync());
    }

    componentDidUpdate(prevProps: SidebarProps) {
        const { selectedNode } = this.props;
        if (selectedNode && (JSON.stringify(selectedNode) !== JSON.stringify(prevProps.selectedNode)) && (this.state.currentState !== SidebarTabState.Properties))
            this.setState({currentState: SidebarTabState.Properties});
    }

    onNotebookTabClick(): void {
        const { currentState } = this.state;
        const newState = currentState === SidebarTabState.Notebooks ? SidebarTabState.None : SidebarTabState.Notebooks;
        this.setState({currentState: newState});
    }

    onSourcesTabClick(): void {
        const { currentState } = this.state;
        const newState = currentState === SidebarTabState.Sources ? SidebarTabState.None : SidebarTabState.Sources;
        this.setState({currentState: newState});
    }

    onVariablesTabClick(): void {
        const { currentState } = this.state;
        const newState = currentState === SidebarTabState.Variables ? SidebarTabState.None : SidebarTabState.Variables;
        this.setState({currentState: newState});
    }

    onPropertiesTabClick(): void {
        const { currentState } = this.state;
        const newState = currentState === SidebarTabState.Properties ? SidebarTabState.None : SidebarTabState.Properties;
        this.setState({currentState: newState});
    }

    render(): JSX.Element {
        const { notebooks, currentNotebook } = this.props;
        const { currentState } = this.state;
        const sources = currentNotebook?.sources;
        const variables = currentNotebook?.variables;

        return (
            <div className="flex flex-row">
                <div className="w-10 h-full flex flex-col bg-gray-700">
                    <SecondaryButton icon="copy"
                        className={`justify-center mb-2 text-2xl p-0 px-2 py-4 hover:bg-gray-500 ${currentState === SidebarTabState.Notebooks ? "bg-gray-600" : ""}`}
                        onClick={this.onNotebookTabClick} />
                    {currentNotebook && (
                        <>
                            <SecondaryButton icon="sign-in-alt"
                                className={`justify-center text-2xl p-0 px-2 py-4 hover:bg-gray-500 ${currentState === SidebarTabState.Sources ? "bg-gray-600" : ""}`}
                                onClick={this.onSourcesTabClick} />
                            <SecondaryButton icon="superscript"
                                className={`justify-center text-2xl p-0 px-2 py-4 hover:bg-gray-500 ${currentState === SidebarTabState.Variables ? "bg-gray-600" : ""}`}
                                onClick={this.onVariablesTabClick} />
                            <SecondaryButton icon="cogs"
                                className={`justify-center text-2xl p-0 px-2 py-4 hover:bg-gray-500 ${currentState === SidebarTabState.Properties ? "bg-gray-600" : ""}`}
                                onClick={this.onPropertiesTabClick} />
                        </>
                    )}
                </div>
                {currentState === SidebarTabState.Notebooks && (
                    <NotebookSidebar notebooks={notebooks} />
                ) || 
                currentState === SidebarTabState.Sources && currentNotebook && (
                    <SourceSidebar sources={sources} />
                ) ||
                currentState === SidebarTabState.Variables && currentNotebook && (
                    <VariableSidebar variables={variables} />
                ) ||
                currentState === SidebarTabState.Properties && currentNotebook && (
                    <PropertiesSidebar />
                )}
            </div>
        );
    }
}

const Sidebar = connect(mapStateToProps)(SidebarInner);
export default Sidebar;