export default class Regexes {
    public static baseLabelRegex = /[a-zA-Z0-9-_]+/g;
    public static labelRegex = new RegExp(`^${Regexes.baseLabelRegex.source}$`);
    public static labelCommaRegex = new RegExp(`^(${Regexes.baseLabelRegex.source}(,|, ))*${Regexes.baseLabelRegex.source}$`);
    public static labelSliceRegex = new RegExp(`^${Regexes.baseLabelRegex.source}:${Regexes.baseLabelRegex.source}$`);

    public static baseValueRegex = /([a-zA-Z0-9-_.()?$]+|'[a-zA-Z0-9-_ ,.()?$]+')/g;
    public static valueRegex = new RegExp(`^${Regexes.baseValueRegex.source}$`);
    public static valueCommaRegex = new RegExp(`^(${Regexes.baseValueRegex.source}(,|, ))*${Regexes.baseValueRegex.source}$`);

    public static baseLabelMapRegex = new RegExp(`(${Regexes.baseLabelRegex.source}:${Regexes.baseLabelRegex.source})`, "g");
    public static labelMapRegex = new RegExp(`^(${Regexes.baseLabelMapRegex.source}(,|, ))*${Regexes.baseLabelMapRegex.source}$`);

    public static baseValueMapRegex = new RegExp(`(${Regexes.baseValueRegex.source}:${Regexes.baseValueRegex.source})`, "g");
    public static valueMapRegex = new RegExp(`^(${Regexes.baseValueMapRegex.source}(,|, ))*${Regexes.baseValueMapRegex.source}$`);

    public static baseLabelValueMapRegex = new RegExp(`(${Regexes.baseLabelRegex.source}:${Regexes.baseValueRegex.source})`, "g");
    public static labelValueMapRegex = new RegExp(`^(${Regexes.baseLabelValueMapRegex.source}(,|, ))*${Regexes.baseLabelValueMapRegex.source}$`);

    public static conditionRegex = new RegExp(`^${Regexes.baseLabelRegex.source} (==|>|>=|<|<=|!=|has|start|end) ${Regexes.baseValueRegex.source}$`);

    public static baseNumberRegex = /(\d+|-\d+)/g;
    public static numberRegex = new RegExp(`^${Regexes.baseNumberRegex.source}$`);
    public static numberCommaRegex = new RegExp(`^(${Regexes.baseNumberRegex.source}(,|, ))*${Regexes.baseNumberRegex.source}$`);
    public static numberSliceRegex = new RegExp(`^${Regexes.baseNumberRegex.source}:${Regexes.baseNumberRegex.source}$`);

    public static baseNumberMapRegex = new RegExp(`(${Regexes.baseNumberRegex.source}:${Regexes.baseNumberRegex.source})`, "g");
    public static numberMapRegex = new RegExp(`^(${Regexes.baseNumberMapRegex.source}(,|, ))*${Regexes.baseNumberMapRegex.source}$`);

    public static emptyRegex = /^$/;

    public static combine = (...regexes: RegExp[]): RegExp => {
        const map = regexes.map(regex => `(${regex.source})`);
        return new RegExp(map.join("|"));
    }

    public static isMatch = (value: string, regex: RegExp): boolean => {
        const match = value.match(regex);
        return match != null && match.length > 0;
    }

    public static processCommaDelimString = (value: string, baseRegex: RegExp): string[] => {
        const result = value.match(baseRegex);
        if (!result)
            return [];
        return result.map(val => val.trim());
    }
    
    public static processCommaDelimNumber = (value: string, baseRegex: RegExp): number[] => {
        const result = value.match(baseRegex);
        if (!result)
            return [];
        return result.map(val => Number(val.trim()));
    }
}