import React from "react";
import ReactDOM from "react-dom";
import "./assets/app.css";
import "./assets/main.css";
import "./assets/github.css";
import "./assets/github-markdown.css";
import "./assets/material.css";
import * as serviceWorker from "./serviceWorker";
import { BrowserRouter } from "react-router-dom";
import store from "./app/store";
import App from "./app/App";
import { Provider } from "react-redux";
import NodeTypeManager from "./managers/NodeTypeManager";
import MathNodes from "./nodeTypes/MathNodes";
import BaseNodes from "./nodeTypes/BaseNodes";
import ChartNodes from "./nodeTypes/ChartNodes";
import DataFrameNodes from "./nodeTypes/DataFrameNodes";
import SeriesNodes from "./nodeTypes/SeriesNodes";
import { Chart, registerables } from "chart.js";
import { ViolinController } from "@sgratzl/chartjs-chart-boxplot";
import ConverterNodes from "./nodeTypes/ConverterNodes";

// Initialize anything global
NodeTypeManager.addNodeCollections(
    MathNodes, 
    BaseNodes, 
    ChartNodes,
    DataFrameNodes,
    SeriesNodes,
    ConverterNodes
);

Chart.register(...registerables, ViolinController);

ReactDOM.render(
    <React.StrictMode>
        <BrowserRouter basename="/node-ml">
            <Provider store={store}>
                <App />
            </Provider>
        </BrowserRouter>
    </React.StrictMode>,
    document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register();
