import { DataFrame, Series } from "danfojs";
import { ArrayType1D, ArrayType2D } from "danfojs/types/shared/types";
import { GroupBy } from "danfojs/types/core/groupby";

type StringMapping = {
    [id: string]: string;
};

type Mapping = {
    [id: string]: string | number | boolean;
}

export default class DanfoUtils {
    public static unique(arr: ArrayType1D): ArrayType1D {
        const uniqueArr = new Set(arr);
        return Array.from(uniqueArr);
    }

    public static groupby(df: DataFrame, columns: string[]): GroupBy {
        /* eslint-disable @typescript-eslint/ban-ts-comment */
        // @ts-ignore
        return df.groupby(columns);
        /* eslint-enable @typescript-eslint/ban-ts-comment */
    }

    public static isEmpty(value: any): boolean {
        return value === undefined || value === null || (isNaN(value) && typeof value !== "string") || (value === "" && typeof value === "string");
    }

    public static mapColumnsToTypes(columns: string[], types: string[]): StringMapping {
        if (columns.length <= 0 || types.length <= 0)
            throw ReferenceError("Either columns or types parameter is empty");

        if (columns.length !== types.length)
            throw ReferenceError("The length of both columns and types must be equal");

        const mapping: StringMapping = {};
        for (let i = 0; i < columns.length; i++)
            mapping[columns[i]] = types[i];

        return mapping;
    }

    public static fillna(df: DataFrame, columns: string[], values: (string | number | boolean)[]): DataFrame {
        if (columns.length <= 0 || values.length <= 0)
            throw Error("ParamError: either columns or values parameter is empty");

        if (values.length !== columns.length)
            throw Error("ParamError: specified column and values must have the same length");

        columns.forEach(col => {
            if (!df.columns.includes(col)) {
                throw Error(`ValueError: Specified column "${col}" must be one of ${df.columns}`);
            }
        });
    
        const newData = [];
        const oldValues = [...df.values] as (string[] | (string | number | boolean)[] | number[] | boolean[])[];

        for (let i = 0; i < oldValues.length; i++) {
            const valueArr = oldValues[i];

            for (let i = 0; i < columns.length; i++) {
                const columnIndex = df.columns.indexOf(columns[i]);
                const replaceWith = values[i];
                valueArr[columnIndex] = DanfoUtils.isEmpty(valueArr[columnIndex]) ? replaceWith : valueArr[columnIndex];
            }

            newData.push(valueArr);
        }
    
        const newDf = new DataFrame(newData, {
            index: [...df.index],
            columns: [...df.columns],
            dtypes: [...df.dtypes],
            config: { ...df.config }
        });
        return newDf;
    }

    // Types are string, float32, int32, boolean
    public static rebuildDataFrame(df: DataFrame, types: string[]): DataFrame {
        let newDf = df.copy();
        newDf = newDf.apply((col: string[]) => {
            let i = 0;
            return col.map(value => {
                const type = types[i];
                i++;

                if (type === "float32" || type === "int32")
                    return value !== "" ? Number(value) : NaN;
                else if (type === "boolean")
                    return value.toLowerCase() === "true" || value === "1";
                else
                    return value === "" ? "" : value;
            }) as ArrayType1D;
        }, { axis: 0 }) as DataFrame;
        return newDf;
    }

    public static map(series: Series, mappings: Mapping, nanByDefault = false): Series {
        const data = series.values.map(val => {
            const value = `${val}`;
            if (value in mappings) {
                return mappings[value];
            } else {
                if (nanByDefault)
                    return NaN;
                else
                    return val as string | number | boolean;
            }
        });
    
        let sf = series.copy();
        sf.$setValues(data);
        sf = sf.astype(sf.dtype as ("string" | "boolean" | "undefined" | "float32" | "int32")) as Series;
        return sf;
    }

    public static loc(df: DataFrame, columns?: string[], rows?: Series | ArrayType1D): DataFrame {
        let _rowIndexes;
        let _columnIndexes;

        const _data = df.values;
        const _index = df.index;

        const _rows: ArrayType1D | undefined = rows instanceof Series ? rows.values as ArrayType1D : rows;

        if (_rows !== undefined && !Array.isArray(_rows)) {
            throw new Error("rows parameter must be an Array. For example: rows: [1,2] or rows: [\"0:10\"]");
        }

        if (columns !== undefined && !Array.isArray(columns)) {
            throw new Error("columns parameter must be an Array. For example: columns: [\"a\",\"b\"] or columns: [\"a:c\"]");
        }

        if (!_rows) {
            _rowIndexes = _index.map((indexValue) => _index.indexOf(indexValue)); // Return all row index
        } else if (_rows.length === 1 && typeof _rows[0] === "string") {

            if (_rows[0].indexOf(":") === -1) { // Input type ==> ["1"] or [`"1"`]
                let temp;
                if (_rows[0].startsWith("\"") || _rows[0].startsWith("'") || _rows[0].startsWith("`")) {
                    temp = _index.indexOf(_rows[0].replace(/['"`]/g, ""));
                } else if (typeof _rows[0] === "string") {
                    temp = _index.indexOf(_rows[0]);
                } else {
                    temp = _index.indexOf(Number(_rows[0]));
                }

                if (temp === -1) {
                    throw new Error(`IndexError: Specified index (${_rows[0]}) not found`);
                }

                _rowIndexes = [temp];

            } else {
                // Input type ==> ["1:2"] or [`"1":"4"`]
                const rowSplit = _rows[0].split(":");

                if (rowSplit.length != 2) {
                    throw new Error("Invalid row split parameter: If using row split string, it must be of the form; rows: [\"start:end\"]");
                }

                let start;
                let end;

                if (rowSplit[0] === "") {
                    start = _index.indexOf(_index[0]);
                } else {
                    if (rowSplit[0].startsWith("\"") || rowSplit[0].startsWith("'") || rowSplit[0].startsWith("`")) {
                        start = _index.indexOf(rowSplit[0].replace(/['"`]/g, ""));
                    } else {
                        start = _index.indexOf(Number(rowSplit[0]));
                    }
                }

                if (rowSplit[1] === "") {
                    end = _index.indexOf(_index[_index.length - 1]) + 1;
                } else {
                    if (rowSplit[1].startsWith("\"") || rowSplit[1].startsWith("'") || rowSplit[1].startsWith("`")) {
                        end = _index.indexOf(rowSplit[1].replace(/['"`]/g, ""));
                    } else {
                        end = _index.indexOf(Number(rowSplit[1]));
                    }

                }

                if (start === -1) {
                    throw new Error("IndexError: Specified start index not found");
                }

                if (end === -1) {
                    throw new Error("IndexError: Specified end index not found");
                }

                _rowIndexes = _index.slice(start, (end + 1) >= _index.length ? undefined : end + 1)
                    .map((indexValue) => _index.indexOf(indexValue));
            }

        } else {
            // Input type ==> ["1", "2"] or [1, 5] or [true, false]
            const rowsIndexToUse = [];
            for (let i = 0; i < _rows.length; i++) {
                const isBoolean = typeof _rows[i] === "boolean";
                if (isBoolean && _rows[i]) {
                    rowsIndexToUse.push(_index.indexOf(_index[i]));
                }

                if (!isBoolean) {
                    const rowIndex = _index.indexOf(_rows[i] as string | number);
                    if (rowIndex === -1) {
                        throw new Error(`IndexError: Specified index (${_rows[i]}) not found`);
                    }
                    rowsIndexToUse.push(rowIndex);
                }
            }

            _rowIndexes = rowsIndexToUse;
        }

        const _columnNames = df.columns;

        if (!columns) {
            _columnIndexes = _columnNames.map((columnName) => _columnNames.indexOf(columnName));// Return all column index

        } else if (columns.length == 1) {
            if (typeof columns[0] !== "string") {
                throw new Error("ColumnIndexError: columns parameter must be an array of a string name. For example: columns: [\"b\"]");
            }

            if (columns[0].indexOf(":") == -1) { // Input type ==> ["A"]
                _columnIndexes = [_columnNames.indexOf(columns[0])];

            } else { // Input type ==> ["a:b"] or [`"col1":"col5"`]
                const columnSplit = columns[0].split(":");

                if (columnSplit.length != 2) {
                    throw new Error("ColumnIndexError: Invalid row split parameter. If using row split string, it must be of the form; rows: [\"start:end\"]");
                }

                const start = columnSplit[0] == "" ? _columnNames.indexOf(_columnNames[0]) : _columnNames.indexOf(columnSplit[0]);
                const end = columnSplit[1] == "" ? _columnNames.indexOf(_columnNames[_columnNames.length - 1]) : _columnNames.indexOf(columnSplit[1]);

                if (start === -1) {
                    throw new Error("ColumnIndexError: Specified start index not found");
                }

                if (end === -1) {
                    throw new Error("ColumnIndexError: Specified end index not found");
                }
                _columnIndexes = _columnNames.slice(start, (end + 1) >= _columnNames.length ? undefined : end + 1)
                    .map((columnName) => _columnNames.indexOf(columnName));

            }
        } else { // Input type ==> ["A", "B"] or ["col1", "col2"]
            for (let i = 0; i < columns.length; i++) {
                if (_columnNames.indexOf(columns[i]) === -1) {
                    throw new Error(`ColumnIndexError: Specified column (${columns[i]}) not found`);
                }
            }
            _columnIndexes = columns.map((columnName) => _columnNames.indexOf(columnName));
        }

        const newData = [];
        const newIndex = [];
        const newColumnNames = [];
        const newDtypes = [];

        for (let i = 0; i < _rowIndexes.length; i++) {
            const rowIndx = _rowIndexes[i];
            const rowData = _data[rowIndx] as ArrayType1D;
            const newRowDataWithRequiredCols: ArrayType1D = [];

            for (let j = 0; j < _columnIndexes.length; j++) {
                const colIndx = _columnIndexes[j];
                newRowDataWithRequiredCols.push(rowData[colIndx]);
            }
            newData.push(newRowDataWithRequiredCols);
            newIndex.push(_index[rowIndx]);
        }

        for (let i = 0; i < _columnIndexes.length; i++) {
            const colIndx = _columnIndexes[i];
            newColumnNames.push(df.columns[colIndx]);
            newDtypes.push(df.dtypes[colIndx]);

        }

        const newDf = new DataFrame(
            newData,
            {
                index: newIndex,
                columns: newColumnNames,
                dtypes: newDtypes,
                config: df.config
            });
        return newDf;
    }

    public static count(df: DataFrame, axis?: number): Series {
        const transposeArray = (arr: ArrayType2D) => {
            if (arr.length === 0) return arr;

            const rowLen = arr.length;
            if (Array.isArray(arr[0])) {
                const colLen = arr[0].length;
                const newArr = [];

                for (let i = 0; i <= colLen - 1; i++) {
                    const temp = [];
                    for (let j = 0; j < rowLen; j++) {
                        const _elem = (arr)[j][i];
                        temp.push(_elem);
                    }
                    newArr.push(temp);
                }
                return newArr;
            } else {
                return arr;
            }
        };

        const getDataByAxis = (axis?: number) => {
            axis = axis === 0 ? 1 : 0;
            if (axis === 1) {
                return df.values;
            } else {
                let dfValues;
                if (df.config.isLowMemoryMode) {
                    dfValues = transposeArray(df.values as ArrayType2D);
                } else {
                    /* eslint-disable @typescript-eslint/ban-ts-comment */
                    // @ts-ignore
                    dfValues = df.$dataIncolumnFormat;
                    /* eslint-enable @typescript-eslint/ban-ts-comment */
                }
                return dfValues;
            }
        };

        const removeMissingValues = (arr: ArrayType1D) => {
            const values = arr.filter((val) => {
                return !(DanfoUtils.isEmpty(val));
            });
            return values;
        };

        const getData = (axis?: number) => {
            const oldValues = getDataByAxis(axis);
            const cleanValues = [];
            for (let i = 0; i < oldValues.length; i++) {
                const values = oldValues[i];
                cleanValues.push(removeMissingValues(values as ArrayType1D));
            }
            return cleanValues;
        };

        const newData = getData(axis);

        const countArr = newData.map((arr) => arr.length);
        if (axis === 1) {
            return new Series(countArr, { index: df.columns });
        } else {
            return new Series(countArr, { index: df.index });
        }
    }
}