import React from "react";
import { Group, Label, Rect, Tag, Text } from "react-konva";
import { KonvaEventObject } from "konva/lib/Node";
import KonvaUtils from "../utils/KonvaUtils";
import Konva from "konva";
import Colors from "../utils/Colors";

export class KonvaForm {
    public static cancel(): void {
        Input.cancelInput();
        Dropdown.cancelDropdown();
    }
}

type InputProps = {
    x: number;
    y: number;
    width: number;
    height: number;
    fontSize: number;
    fontStyle: string;
    padding: number;
    textColor: string;
    bgColor: string;
    hoverColor: string;
    borderRadius: number;
    zIndex: number;
    value: string;
    type: string;
    isEditable: boolean;
    onChange: (value: string) => Promise<boolean>;
}

type InputState = {
    value: string;
}

export class Input extends React.Component<InputProps, InputState> {
    private static inputElement: HTMLInputElement;
    
    static defaultProps: InputProps = {
        x: 0,
        y: 0,
        width: 100,
        height: 20,
        fontSize: 12,
        fontStyle: "normal",
        padding: 0,
        textColor: "#000000",
        bgColor: "#ffffff",
        hoverColor: "#cccccc",
        borderRadius: 0,
        zIndex: 0,
        value: "",
        type: "text",
        isEditable: true,
        onChange: async () => { return false; }
    }

    constructor(props: InputProps) {
        super(props);

        this.state = {
            value: ""
        };

        this.showInput = this.showInput.bind(this);
        this.onHover = this.onHover.bind(this);
        this.onHoverExit = this.onHoverExit.bind(this);
    }

    showInput(event: KonvaEventObject<Event>): void {
        Input.cancelInput();

        const { width, height, fontSize, padding, textColor, hoverColor, borderRadius, zIndex, type, isEditable, onChange } = this.props;
        const value = this.state.value === "" ? this.props.value : this.state.value;

        if (!isEditable)
            return;

        const stage = event.target.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }
        const scale = stage.scaleX();
        const container = stage.container();
        const boundingRect = container.getBoundingClientRect();

        const top = event.target.absolutePosition().y + boundingRect.top;
        const left = event.target.absolutePosition().x + boundingRect.left;

        const input = document.createElement("input");
        document.body.appendChild(input);

        input.value = value;
        input.type = type;
        input.style.position = "absolute";
        input.style.top = `${top}px`;
        input.style.left = `${left + Math.max(0, boundingRect.left - left)}px`;
        input.style.width = `${Math.min(width * scale, 
            boundingRect.right - left,
            (width * scale) - (boundingRect.left - left))}px`;
        input.style.height = `${height * scale}px`;
        input.style.paddingLeft = `${padding * scale}px`;
        input.style.fontSize = `${fontSize * scale}px`;
        input.style.color = textColor;
        input.style.backgroundColor = hoverColor;
        input.style.borderRadius = `${borderRadius * scale}px`;
        input.style.verticalAlign = "center";
        input.style.zIndex = `${zIndex}`;
        input.focus();
        input.onchange = async (ev: Event) => {
            const target = ev.target as HTMLInputElement;

            if (!target) {
                console.error("Cannot cast target to 'HTMLInputElement'.");
                Input.cancelInput();
                return;
            }

            if (!target.value) {
                console.error("No value typed in while editing the text.");
                Input.cancelInput();
                return;
            }

            const result = await onChange(target.value);
            if (!result) {
                console.error("Input value requirements not met.");
                Input.cancelInput();
                return;
            }

            this.setState({value: target.value});
            Input.cancelInput();
        };

        Input.inputElement = input;
    }

    public static cancelInput(): void {
        const input = Input.inputElement;
        if (!input)
            return;
        
        input.onchange = null;
        input.parentNode?.removeChild(input);
    }

    onHover(event: KonvaEventObject<MouseEvent>): void {
        if (!this.props.isEditable)
            return;

        KonvaUtils.setCursor(event, "text");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error("The Konva layer could not be found.");
            return;
        }

        const label = event.currentTarget as Konva.Label;
        if (!label || !label.children)
            return;

        const textBG = label.children[0];
        textBG.setAttr("fill", this.props.hoverColor);
        layer.draw();
    }

    onHoverExit(event: KonvaEventObject<Event>): void {
        if (!this.props.isEditable)
            return;
            
        KonvaUtils.setCursor(event, "default");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error("The Konva layer could not be found.");
            return;
        }

        const label = event.currentTarget as Konva.Label;
        if (!label || !label.children)
            return;

        const textBG = label.children[0];
        textBG.setAttr("fill", this.props.bgColor);
        layer.draw();
    }

    render(): JSX.Element {
        const { x, y, width, height, fontSize, fontStyle, padding, textColor, bgColor, borderRadius } = this.props;
        const value = this.state.value === "" ? this.props.value : this.state.value;

        return (
            <Label name="top-bar-label"
                x={x}
                y={y}
                onMouseEnter={this.onHover}
                onMouseLeave={this.onHoverExit} 
                onClick={event => {
                    event.cancelBubble = true;
                    this.showInput(event);
                }}
                onTap={event => {
                    event.cancelBubble = true;
                    this.showInput(event);
                }}>
                <Tag fill={bgColor}
                    cornerRadius={borderRadius} />
                <Text name="top-bar-text"
                    width={width}
                    height={height}
                    fontStyle={fontStyle}
                    fontSize={fontSize}
                    verticalAlign="middle"
                    padding={padding}
                    fill={textColor}
                    text={value} />
            </Label>
        );
    }
}

type DropdownProps = {
    x: number;
    y: number;
    width: number;
    height: number;
    fontSize: number;
    fontStyle: string;
    padding: number;
    textColor: string;
    bgColor: string;
    hoverColor: string;
    borderRadius: number;
    zIndex: number;
    values: string[];
    value: string;
    onChange: (value: string) => Promise<boolean>;
}

type DropdownState = {
    selectedIndex: number;
}

export class Dropdown extends React.Component<DropdownProps, DropdownState> {
    private static dropdownElement: HTMLSelectElement;
    
    static defaultProps: DropdownProps = {
        x: 0,
        y: 0,
        width: 100,
        height: 20,
        fontSize: 12,
        fontStyle: "normal",
        padding: 0,
        textColor: "#000000",
        bgColor: "#ffffff",
        hoverColor: "#cccccc",
        borderRadius: 0,
        zIndex: 0,
        values: [],
        value: "",
        onChange: async () => { return false; }
    }

    constructor(props: DropdownProps) {
        super(props);

        this.state = {
            selectedIndex: 0
        };

        this.showDropdown = this.showDropdown.bind(this);
        this.onHover = this.onHover.bind(this);
        this.onHoverExit = this.onHoverExit.bind(this);
    }

    componentDidUpdate(): void {
        const { value, values } = this.props;
        const { selectedIndex } = this.state;

        if (value != null && values.includes(value) && selectedIndex !== values.indexOf(value))
            this.setState({selectedIndex: values.indexOf(value)});
    }

    showDropdown(event: KonvaEventObject<Event>): void {
        Dropdown.cancelDropdown();

        const { width, height, padding, fontSize, textColor, hoverColor, borderRadius, zIndex, values, onChange } = this.props;

        const stage = event.target.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }
        const scale = stage.scaleX();
        const container = stage.container();
        const boundingRect = container.getBoundingClientRect();

        const top = event.target.absolutePosition().y + boundingRect.top;
        const left = event.target.absolutePosition().x + boundingRect.left;

        const dropdown = document.createElement("select");
        document.body.appendChild(dropdown);

        // Add classes here
        //dropdown.classList.add("block", "appearance-none", "w-full", "bg-white", "border", "border-gray-400", "hover:border-gray-500", "px-4", "py-2", "pr-8", "rounded", "shadow", "leading-tight", "focus:outline-none", "focus:shadow-outline");
        values.forEach(value => {
            const option = document.createElement("option");
            option.value = value;
            option.innerText = value;
            dropdown.add(option);
        });

        dropdown.selectedIndex = this.state.selectedIndex;
        dropdown.style.position = "absolute";
        dropdown.style.top = `${top}px`;
        dropdown.style.left = `${left + Math.max(0, boundingRect.left - left)}px`;
        dropdown.style.width = `${Math.min(width * scale, 
            boundingRect.right - left,
            (width * scale) - (boundingRect.left - left))}px`;
        dropdown.style.height = `${height * scale}px`;
        dropdown.style.paddingLeft = `${padding * scale}px`;
        dropdown.style.fontSize = `${fontSize * scale}px`;
        dropdown.style.color = textColor;
        dropdown.style.backgroundColor = hoverColor;
        dropdown.style.borderRadius = `${borderRadius * scale}px`;
        dropdown.style.zIndex = `${zIndex}`;
        dropdown.focus();
        dropdown.onchange = async (ev: Event) => {
            const target = ev.target as HTMLSelectElement;

            if (!target) {
                console.error("Cannot cast target to 'HTMLSelectElement'.");
                Dropdown.cancelDropdown();
                return;
            }

            if (!target.value) {
                console.error("No value selected.");
                Dropdown.cancelDropdown();
                return;
            }

            const result = await onChange(target.value);
            if (!result) {
                console.error("Input value requirements not met.");
                Dropdown.cancelDropdown();
                return;
            }

            this.setState({selectedIndex: target.selectedIndex});
            Dropdown.cancelDropdown();
        };

        Dropdown.dropdownElement = dropdown;
    }

    public static cancelDropdown(): void {
        const dropdown = Dropdown.dropdownElement;
        if (!dropdown)
            return;
        
        dropdown.onchange = null;
        dropdown.parentNode?.removeChild(dropdown);
    }

    onHover(event: KonvaEventObject<Event>): void {
        KonvaUtils.setCursor(event, "pointer");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error("The Konva layer could not be found.");
            return;
        }

        const label = event.currentTarget as Konva.Label;
        if (!label || !label.children)
            return;

        const textBG = label.children[0];
        textBG.setAttr("fill", this.props.hoverColor);
        layer.draw();
    }

    onHoverExit(event: KonvaEventObject<Event>): void {
        KonvaUtils.setCursor(event, "default");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error("The Konva layer could not be found.");
            return;
        }

        const label = event.currentTarget as Konva.Label;
        if (!label || !label.children)
            return;

        const textBG = label.children[0];
        textBG.setAttr("fill", this.props.bgColor);
        layer.draw();
    }

    render(): JSX.Element {
        const { x, y, width, height, fontSize, fontStyle, padding, textColor, bgColor, borderRadius, values, value } = this.props;

        return(
            <Label name="top-bar-label"
                x={x}
                y={y}
                onMouseEnter={this.onHover}
                onMouseLeave={this.onHoverExit} 
                onClick={event => {
                    event.cancelBubble = true;
                    this.showDropdown(event);
                }}
                onTap={event => {
                    event.cancelBubble = true;
                    this.showDropdown(event);
                }}>
                <Tag fill={bgColor}
                    cornerRadius={borderRadius} />
                <Text name="top-bar-text"
                    width={width}
                    height={height}
                    fontStyle={fontStyle}
                    fontSize={fontSize}
                    verticalAlign="middle"
                    padding={padding}
                    fill={textColor}
                    text={value != null && values.includes(value) ? value : values[this.state.selectedIndex]} />
            </Label>
        );
    }
}

type ToggleProps = {
    value: boolean;
    x?: number;
    y?: number;
    width?: number;
    height?: number;
    handleOffset?: number;
    bgTrueColor?: string;
    bgFalseColor?: string;
    handleTrueColor?: string;
    handleFalseColor?: string;
    onToggle?: (newValue: boolean) => Promise<void>;
}

type ToggleState = {

}

export class Toggle extends React.Component<ToggleProps, ToggleState> {
    constructor(props: ToggleProps) {
        super(props);

        this.state = {

        };
    }

    render(): JSX.Element {
        const { value, x = 0, y = 0, width = 75, height = 25, handleOffset = 10, bgTrueColor = Colors.Green[500], bgFalseColor = Colors.Red[500], handleTrueColor = Colors.Green[800], handleFalseColor = Colors.Red[800], onToggle } = this.props;

        return (
            <Group x={x}
                y={y}
                onMouseEnter={event => {
                    KonvaUtils.setCursor(event, "pointer");
                }}
                onMouseLeave={event => {
                    KonvaUtils.setCursor(event, "default");
                }}
                onClick={async (event) => {
                    event.cancelBubble = true;
                    if (onToggle) {
                        await onToggle(!value);
                    }
                }}>
                <Rect name="background"
                    x={0}
                    y={0}
                    width={width}
                    height={height}
                    fill={value ? bgTrueColor : bgFalseColor} />
                <Rect name="handle"
                    x={value ? width - height : -(handleOffset / 2)}
                    y={-(handleOffset / 2)}
                    width={height + handleOffset}
                    height={height + handleOffset}
                    fill={value ? handleTrueColor : handleFalseColor} />
            </Group>
        );
    }
}