import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { DataFrame } from "danfojs";
import React from "react";
import Scrollbars from "react-custom-scrollbars-2";
import Dropdown from "react-overlays/esm/Dropdown";
import store from "../app/store";
import { CSVSource } from "../data/DataService";
import { VariableObject } from "../db/models";
import { createNewVariableAsync, deleteVariableAsync, setIsDraggingVariableAsync, updateVariableNameAsync, updateVariableValueAsync } from "../features/notebooks/notebookSlice";
import NodeExecManager from "../managers/NodeExecManager";
import { DataFrameOutput, DropdownItem } from "../types";
import { isTypeEqual, Types, VariableKeys, VariableType } from "../utils/Types";
import { OKButton, CustomModal, PrimaryButton, SecondaryButton, Input, ContextMenuItem, Dropdown as CustomDropdown, TextArea, Checkbox, CancelButton } from "./CustomComponents";
import { ShowResult } from "./NodeCell";

type VariableItemProps = {
    variable: VariableObject;
}

type VariableItemState = {
    isOptions: boolean;
    isRename: boolean;
    renameValue: string;
    isDelete: boolean;
    isChangeValue: boolean;
    isPreview: boolean;
    preview: DataFrameOutput;
    newValue: string | number | boolean;
}

class VariableItem extends React.Component<VariableItemProps, VariableItemState> {
    constructor(props: VariableItemProps) {
        super(props);

        this.state = {
            isOptions: false,
            isRename: false,
            renameValue: "",
            isDelete: false,
            isChangeValue: false,
            newValue: 0,
            isPreview: false,
            preview: {
                type: "dataframe",
                outputOrder: 0,
                columns: [],
                indexes: [],
                values: []
            }
        };

        this.onDragVariableStart = this.onDragVariableStart.bind(this);

        this.showRenameModal = this.showRenameModal.bind(this);
        this.hideRenameModal = this.hideRenameModal.bind(this);
        this.renameVariable = this.renameVariable.bind(this);

        this.showDeleteModal = this.showDeleteModal.bind(this);
        this.hideDeleteModal = this.hideDeleteModal.bind(this);
        this.deleteVariable = this.deleteVariable.bind(this);

        this.showChangeValueModal = this.showChangeValueModal.bind(this);
        this.hideChangeValueModal = this.hideChangeValueModal.bind(this);
        this.onStringValueChange = this.onStringValueChange.bind(this);
        this.onNumberValueChange = this.onNumberValueChange.bind(this);
        this.onBooleanValueToggle = this.onBooleanValueToggle.bind(this);
        this.changeValue = this.changeValue.bind(this);

        this.showPreviewModal = this.showPreviewModal.bind(this);
        this.hidePreviewModal = this.hidePreviewModal.bind(this);
    }

    async onDragVariableStart(event: React.DragEvent<HTMLDivElement>, type: "get" | "set", variable: VariableObject): Promise<void> {
        event.stopPropagation();
        await store.dispatch(setIsDraggingVariableAsync(true));

        const data = { dataType: "variable", type, variable };
        event.dataTransfer.setData("text/plain", JSON.stringify(data));
    }

    showRenameModal(): void {
        this.setState({isRename: true, isOptions: false, renameValue: ""});
    }

    hideRenameModal(): void {
        this.setState({isRename: false});
    }

    async renameVariable(): Promise<void> {
        const { variable } = this.props;
        const { renameValue } = this.state;
        if (renameValue !== "") {
            await store.dispatch(updateVariableNameAsync(variable.variableId, renameValue));
            this.hideRenameModal();
        }
    }

    showDeleteModal(): void {
        this.setState({isDelete: true, isOptions: false});
    }

    hideDeleteModal(): void {
        this.setState({isDelete: false});
    }

    async deleteVariable(): Promise<void> {
        const { variable } = this.props;
        this.setState({isOptions: false});
        await store.dispatch(deleteVariableAsync(variable.variableId));
        this.hideDeleteModal();
    }

    showChangeValueModal(): void {
        const { variable } = this.props;
        console.log(`${variable.type} ${Types.String}`);
        if (!isTypeEqual(variable.type, Types.String) &&
            !isTypeEqual(variable.type, Types.Number) &&
            !isTypeEqual(variable.type, Types.Boolean))
            return;
            
        let newValue: string | number | boolean | undefined = variable.value;
        if (newValue == null) {
            if (isTypeEqual(variable.type, Types.String))
                newValue = "";
            else if (isTypeEqual(variable.type, Types.Number))
                newValue = 0;
            else if (isTypeEqual(variable.type, Types.Boolean))
                newValue = false;
        }

        if (newValue == null)
            return;

        this.setState({isChangeValue: true, isOptions: false, newValue});
    }

    hideChangeValueModal(): void {
        this.setState({isChangeValue: false});
    }

    async onStringValueChange(newValue: string): Promise<void> {
        this.setState({ newValue });
    }

    async onNumberValueChange(newValue: string): Promise<void> {
        this.setState({ newValue: Number(newValue) ?? 0 });
    }

    async onBooleanValueToggle(): Promise<void> {
        this.setState(prevState => ({ newValue: !prevState.newValue }));
    }

    async changeValue(): Promise<void> {
        const { variable } = this.props;
        const { newValue } = this.state;
        
        await store.dispatch(updateVariableValueAsync(variable.variableId, newValue));
        this.hideChangeValueModal();
    }

    showPreviewModal(): void {
        const { variable } = this.props;
        if (!isTypeEqual(variable.type, Types.DataFrame))
            return;

        const output: DataFrameOutput = {
            type: "dataframe",
            outputOrder: 0,
            columns: [],
            indexes: [],
            values: []
        };

        const rowCount = 0;
        const varDf = NodeExecManager.getVariable(variable.variableId);
        if (!varDf)
            return;

        const df =  varDf as DataFrame;
        const head = (df.shape[0] >= rowCount && rowCount > 0) ? df.head(rowCount) : df;
        let values: string[][];
        if (head.columns.length > 1) {
            values = head.values.map(value1 => {
                const val1 = value1 as string[] | (string | number | boolean)[] | number[] | boolean[];
                return val1.map(value2 => `${value2}`);
            });
        } else {
            values = head.values.map(value => {
                const val = value as string | number | boolean;
                return [`${val}`];
            });
        }

        output.columns = df.columns,
        output.values = values;

        this.setState({isPreview: true, isOptions: false, preview: output});
    }

    hidePreviewModal(): void {
        const output: DataFrameOutput = {
            type: "dataframe",
            outputOrder: 0,
            columns: [],
            indexes: [],
            values: []
        };

        this.setState({isPreview: false, preview: output});
    }

    render(): JSX.Element {
        const { variable } = this.props;
        const { isOptions, isRename, renameValue, isDelete, isChangeValue, newValue, isPreview, preview } = this.state;
        return (
            <>
                <div className="transition duration-200 ease-in-out flex flex-row items-center justify-between p-2 text-sm text-gray-200 hover:bg-indigo-400 hover:text-indigo-100 select-none"
                    onMouseLeave={() => this.setState({isOptions: false})}>
                    <div className="flex flex-col">
                        <span className="font-medium">{variable.name}</span>
                        <span className="text-xs" style={{marginTop: "-0.25rem"}}>{variable.type}</span>
                    </div>
                    <div className="flex flex-row items-center">
                        <div className="flex flex-col">
                            <div className="transition duration-200 ease-in-out flex items-center text-sm px-1 mr-2 mb-1 bg-transparent hover:bg-indigo-600 cursor-move"
                                draggable
                                onDragStart={async event => await this.onDragVariableStart(event, "get", variable)}
                                onDragEnd={async () => await store.dispatch(setIsDraggingVariableAsync(false))}>
                                <FontAwesomeIcon icon="arrows-alt"
                                    className="mr-1" />
                                <span className="">Get</span>
                            </div>
                            <div className="transition duration-200 ease-in-out flex items-center text-sm px-1 mr-2 bg-transparent hover:bg-indigo-600 cursor-move"
                                draggable
                                onDragStart={async event => await this.onDragVariableStart(event, "set", variable)}
                                onDragEnd={async () => await store.dispatch(setIsDraggingVariableAsync(false))}>
                                <FontAwesomeIcon icon="arrows-alt"
                                    className="mr-1" />
                                <span className="">Set</span>
                            </div>
                        </div>
                        <Dropdown show={isOptions}
                            onToggle={next => this.setState({isOptions: next})}>
                            <div className="relative flex flex-row-reverse">
                                <Dropdown.Toggle>
                                    {(props, meta) => (
                                        <SecondaryButton icon="ellipsis-v" 
                                            className="w-full flex items-center justify-center h-6 text-sm bg-transparent hover:bg-indigo-600"
                                            onClick={event => meta.toggle(!isOptions, event)} />
                                    )}
                                </Dropdown.Toggle>
                                <Dropdown.Menu show={isOptions} flip={true}>
                                    {(props) => (
                                        <>
                                            {isOptions && (
                                                <div className="absolute z-20 shadow-lg w-32" style={{top: "1.5rem"}}>
                                                    {isTypeEqual(variable.type, Types.DataFrame) && (
                                                        <div onClick={this.showPreviewModal}>
                                                            <ContextMenuItem text="Preview"
                                                                icon="search" />
                                                        </div>
                                                    )}
                                                    <div onClick={this.showRenameModal}>
                                                        <ContextMenuItem text="Change Name"
                                                            icon="edit" />
                                                    </div>
                                                    {(isTypeEqual(variable.type, Types.String) || isTypeEqual(variable.type, Types.Number) || isTypeEqual(variable.type, Types.Boolean)) && (
                                                        <div onClick={this.showChangeValueModal}>
                                                            <ContextMenuItem text="Change Value"
                                                                icon="pencil-alt" />
                                                        </div>
                                                    )}
                                                    <div onClick={this.showDeleteModal}>
                                                        <ContextMenuItem text="Delete"
                                                            icon="trash-alt"
                                                            className="bg-red-300 text-red-700 hover:bg-red-800 hover:text-red-200" />
                                                    </div>
                                                </div>
                                            ) || 
                                            !isOptions && (
                                                <div className=""></div>
                                            )}
                                        </>
                                    )}
                                </Dropdown.Menu>
                            </div>
                        </Dropdown>
                    </div>
                </div>

                <CustomModal show={isRename}
                    onHide={() => this.hideRenameModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Rename</h2>
                            <h2 className="text-lg font-bold">&apos;{variable.name}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            <Input value={renameValue} 
                                label="Name" 
                                placeholder="Enter variable name..."
                                onChange={event => this.setState({renameValue: event.target.value})} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.renameVariable} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideRenameModal()} />
                        </div>
                    </div>
                </CustomModal>

                <CustomModal show={isDelete}
                    onHide={() => this.hideDeleteModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium text-red-600">Delete</h2>
                            <h2 className="text-lg font-bold text-red-600">&apos;{variable.name}&apos;?</h2>
                        </div>
                        <div className="mb-4 w-full flex items-center">
                            <span className="text-center">Are you sure you want to delete the variable &apos;{variable.name}&apos;? This action is <span className="text-red-600 font-bold">irreversible</span> and might break any nodes using this variable.</span>
                        </div>
                        <div className="flex flex-row-reverse">
                            <CancelButton text="Delete" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.deleteVariable} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideDeleteModal()} />
                        </div>
                    </div>
                </CustomModal>

                <CustomModal show={isChangeValue}
                    onHide={() => this.hideChangeValueModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Change Value</h2>
                            <h2 className="text-lg font-bold">&apos;{variable.name}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            {isTypeEqual(variable.type, Types.String) && (
                                <TextArea value={newValue as string}
                                    className=""
                                    label="New Value"
                                    onChange={async event => {
                                        const input = event.target.value;
                                        await this.onStringValueChange(input);
                                    }} />
                            ) ||
                            isTypeEqual(variable.type, Types.Number) && (
                                <Input value={`${newValue as number}`}
                                    className=""
                                    label="New Value"
                                    placeholder="Number"
                                    onChange={async event => {
                                        const input = event.target.value;
                                        await this.onNumberValueChange(input);
                                    }} />
                            ) ||
                            isTypeEqual(variable.type, Types.Boolean) && (
                                <Checkbox value={newValue as boolean}
                                    label="New Value"
                                    onToggle={async () => await this.onBooleanValueToggle()} />
                            )}
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.changeValue} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideChangeValueModal()} />
                        </div>
                    </div>
                </CustomModal>
                
                <CustomModal show={isPreview}
                    onHide={() => this.hidePreviewModal()}>
                    <div className="p-4 bg-gray-100" 
                        style={{width: "75%"}}>
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Preview</h2>
                            <h2 className="text-lg font-bold">&apos;{variable.name}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            <ShowResult result={preview} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <SecondaryButton text="Close" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hidePreviewModal()} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}

type VariableSidebarProps = {
    variables?: VariableObject[];
}

type VariableSidebarState = {
    isAddingVariable: boolean;
    variableName: string;
    variableType: VariableType | undefined;
}

export default class VariableSidebar extends React.Component<VariableSidebarProps, VariableSidebarState> {
    constructor(props: VariableSidebarProps) {
        super(props);

        this.state = {
            isAddingVariable: false,
            variableName: "",
            variableType: undefined
        };

        this.showAddVariableModal = this.showAddVariableModal.bind(this);
        this.hideAddVariableModal = this.hideAddVariableModal.bind(this);

        this.onAddVariable = this.onAddVariable.bind(this);
    }

    showAddVariableModal(): void {
        this.setState({isAddingVariable: true, variableName: "", variableType: undefined});
    }

    hideAddVariableModal(): void {
        this.setState({isAddingVariable: false});
    }

    async onAddVariable(): Promise<void> {
        const { variableName, variableType } = this.state;
        if (!variableName || !variableType)
            return;

        const variable = await store.dispatch(createNewVariableAsync(variableName, variableType));
        if (!variable)
            return;

        this.hideAddVariableModal();
    }

    render(): JSX.Element {
        const { variables } = this.props;
        const { isAddingVariable, variableName } = this.state;
        const variableOptions: DropdownItem[] = [];
        VariableKeys.forEach(key => {
            variableOptions.push({ value: key, label: key });
        });
        return (
            <>
                <div className="w-48 h-full flex flex-col bg-gray-600 justify-center">
                    <div className="p-2 text-md font-medium text-gray-300">Variables</div>
                    <Scrollbars>
                        <div className="grid gap-2">
                            <div className="grid gap-2">
                                {variables && variables.map((variable, index) => (
                                    <VariableItem key={index} variable={variable} />
                                ))}
                            </div>
                            <OKButton icon="plus" text="Add Variable"
                                className="text-xs w-full"
                                onClick={this.showAddVariableModal} />
                        </div>
                    </Scrollbars>
                </div>
                
                <CustomModal show={isAddingVariable}
                    onHide={this.hideAddVariableModal}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Add Variable</h2>
                        </div>
                        <div className="mb-4">
                            <Input value={variableName}
                                label="Name"
                                placeholder="Enter variable name..."
                                onChange={event => this.setState({variableName: event.target.value})} />
                        </div>
                        <div className="mb-4">
                            <CustomDropdown label="Type"
                                options={variableOptions}
                                onChange={newValue => {
                                    if (!newValue)
                                        return;

                                    this.setState({variableType: newValue.value as VariableType});
                                }} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save"
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.onAddVariable} />
                            <SecondaryButton text="Cancel"
                                className="px-2 py-1 text-xs"
                                onClick={this.hideAddVariableModal} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}