import { Layer } from "konva/lib/Layer";
import store from "../app/store";
import NodeConnection from "../components/NodeConnection";
import { NodeConnectionObject } from "../db/models";
import { getConnectionById, getNodeById } from "../features/notebooks/notebookSlice";
import { Vector2 } from "../types";

class NodeConnectionManager {
    private connectionRefs: {
        [cellId: string]: {
            [connectionId: string]: NodeConnection | undefined
        }
    } = {};
    private layerRef: Layer | undefined;
    private currentNotebookId = "";

    constructor() {
        this.onStoreUpdate = this.onStoreUpdate.bind(this);
        store.subscribe(this.onStoreUpdate);
    }

    onStoreUpdate(): void {
        const currentNotebook = store.getState().notebook.currentNotebook;
        if (!currentNotebook)
            return;

        if (this.currentNotebookId !== currentNotebook.notebookId) {
            this.reset();
            this.currentNotebookId = currentNotebook.notebookId;
        }
    }

    setLayerRef(ref: Layer) {
        this.layerRef = ref;
    }

    addRef(cellId: string, connectionId: string, ref: NodeConnection) {
        if (!this.connectionRefs[cellId])
            this.connectionRefs[cellId] = {};

        if (this.connectionRefs[cellId][connectionId])
            return;

        this.connectionRefs[cellId][connectionId] = ref;
        this.moveToInitialPos(cellId, connectionId);
    }

    removeRef(cellId: string, connectionId: string) {
        this.connectionRefs[cellId][connectionId] = undefined;
    }

    private moveToInitialPos(cellId: string, connectionId: string) {
        const connection = getConnectionById(store.getState(), cellId, connectionId);
        if (!connection)
            return;

        const fromNode = getNodeById(store.getState(), cellId, connection.fromNodeId);
        const toNode = getNodeById(store.getState(), cellId, connection.toNodeId);
        if (!fromNode || !toNode)
            return;

        const connRef = this.connectionRefs[cellId][connectionId];
        if (!connRef)
            return;

        connRef.setFromCoords(fromNode.coords);
        connRef.setToCoords(toNode.coords);
    }

    moveConnections(cellId: string, fromConnections: NodeConnectionObject[], toConnections: NodeConnectionObject[], coords: Vector2) {
        if (!this.layerRef)
            return;

        fromConnections.forEach(conn => {
            const connRef = this.connectionRefs[cellId][conn.connectionId];
            if (!connRef || !this)
                return;

            connRef.setFromCoords(coords);
        });

        toConnections.forEach(conn => {
            const connRef = this.connectionRefs[cellId][conn.connectionId];
            if (!connRef || !this)
                return;

            connRef.setToCoords(coords);
        });

        this.layerRef.getStage().batchDraw();
    }

    reset(): void {
        this.connectionRefs = {};
    }
}

export default new NodeConnectionManager();