import React from "react";
import Scrollbars from "react-custom-scrollbars-2";
import { connect } from "react-redux";
import store, { RootState } from "../app/store";
import { NodeConnectionObject } from "../db/models";
import { setNodePropertyAsync, getNodeById, getSeparateConnectionsByNodeId, setNodeInputValueAsync, getSelectedNode, selectNodeTypeAsync } from "../features/notebooks/notebookSlice";
import NodeTypeManager from "../managers/NodeTypeManager";
import { Property, Slot } from "../types";
import Regexes from "../utils/Regexes";
import { Checkbox, Input, OKButton, PrimaryButton, SecondaryButton, TextArea } from "./CustomComponents";

type PropertiesSidebarProps = {
    selectedNode: {
        cellId: string;
        nodeId: string;
    } | undefined;
    connections: {
        fromConnections: NodeConnectionObject[];
        toConnections: NodeConnectionObject[];
    };
}

type PropertiesSidebarState = {
    properties: Property[];
    newPropertyValues: {
        [id: string]: {
            isInvalid: boolean;
            value: string | number | boolean;
        }
    };
    inputs: Slot[];
    newInputValues: {
        [id: string]: {
            isInvalid: boolean;
            value: string | number | boolean;
        }
    }
}

const mapStateToProps = (state: RootState) => {
    const selectedNode = getSelectedNode(state);
    const connections = selectedNode != null ? 
        getSeparateConnectionsByNodeId(state, selectedNode.cellId, selectedNode.nodeId) : 
        { fromConnections: [], toConnections: [] };
    return {
        selectedNode,
        connections
    };
};

class PropertiesSidebarInner extends React.Component<PropertiesSidebarProps, PropertiesSidebarState> {
    constructor(props: PropertiesSidebarProps) {
        super(props);

        this.state = {
            properties: [],
            newPropertyValues: {},
            inputs: [],
            newInputValues: {}
        };

        this.onStringPropertyChange = this.onStringPropertyChange.bind(this);
        this.onNumberPropertyChange = this.onNumberPropertyChange.bind(this);
        this.onBooleanPropertyToggle = this.onBooleanPropertyToggle.bind(this);

        this.saveAllProperties = this.saveAllProperties.bind(this);
        this.resetAllProperties = this.resetAllProperties.bind(this);

        this.saveAllInputs = this.saveAllInputs.bind(this);
        this.resetAllInputs = this.resetAllInputs.bind(this);
    }

    componentDidMount(): void {
        this.resetAllInputs();
        this.resetAllProperties();
    }

    componentDidUpdate(prevProps: PropertiesSidebarProps): void {
        const { selectedNode, connections } = this.props;
        if (selectedNode && (JSON.stringify(selectedNode) !== JSON.stringify(prevProps.selectedNode))) {
            this.resetAllInputs();
            this.resetAllProperties();
        }

        if (JSON.stringify(connections) !== JSON.stringify(prevProps.connections))
            this.resetAllInputs();
    }

    async onStringInputChange(input: Slot, newValue: string): Promise<void> {
        this.setState(prevProps => ({newInputValues: {
            ...prevProps.newInputValues,
            [input.id]: {
                isInvalid: input.regex != null && !Regexes.isMatch(newValue, input.regex),
                value: newValue
            }
        }}));
    }

    async onNumberInputChange(input: Slot, newValue: string): Promise<void> {
        this.setState(prevProps => ({newInputValues: {
            ...prevProps.newInputValues,
            [input.id]: {
                isInvalid: !Regexes.isMatch(newValue, Regexes.numberRegex),
                value: Number(newValue)
            }
        }}));
    }

    async onBooleanInputToggle(input: Slot): Promise<void> {
        const { newInputValues } = this.state;

        const newValue = !((newInputValues[input.id].value ?? input.defaultValue) as boolean);
        this.setState(prevProps => ({newInputValues: {
            ...prevProps.newInputValues,
            [input.id]: {
                isInvalid: false,
                value: newValue
            }
        }}));
    }

    async saveAllInputs(): Promise<void> {
        const { selectedNode } = this.props;
        const { newInputValues } = this.state;
        if (!selectedNode)
            return;
        const invalidValues = Object.values(newInputValues).filter(value => value.isInvalid);
        if (invalidValues.length > 0)
            return;

        const updateValues = (keyVal: [string, { isInvalid: boolean, value: string | number | boolean }]): Promise<void> => {
            return store.dispatch(setNodeInputValueAsync(selectedNode.cellId, selectedNode.nodeId, keyVal[0], keyVal[1].value));
        };
        const actions = Object.entries(newInputValues).map(updateValues);
        await Promise.all(actions);
        this.resetAllInputs();
    }

    resetAllInputs(): void { 
        const { selectedNode, connections } = this.props;

        if (!selectedNode)
            return;

        const node = getNodeById(store.getState(), selectedNode.cellId, selectedNode.nodeId);
        if (!node)
            return;
        
        const nodeType = NodeTypeManager.getType(node.type);
        if (!nodeType)
            return;

        const inputs: Slot[] = [];
        nodeType.inputSlots.forEach(input => {
            let inputObj: Slot = input;
            // Hide input values from the sidebar if that slot is connected
            const connIndex = connections.toConnections.findIndex(conn => conn.toSlotId === input.id);
            if (connIndex >= 0)
                return;

            const valIndex = node.inputValues.findIndex(val => val.id === input.id);
            if (valIndex >= 0) {
                const nodeSlot = node.inputValues[valIndex];
                if (typeof nodeSlot.value !== "string" &&
                    typeof nodeSlot.value !== "number" && 
                    typeof nodeSlot.value !== "boolean")
                    return;
                        
                inputObj = { 
                    ...input,
                    defaultValue: nodeSlot.value
                };
            }

            if (inputObj.defaultValue != null)
                inputs.push(inputObj);
        });
        this.setState({inputs, newInputValues: {}});
    }

    async onStringPropertyChange(property: Property, newValue: string): Promise<void> {
        this.setState(prevProps => ({newPropertyValues: {
            ...prevProps.newPropertyValues,
            [property.id]: {
                isInvalid: property.regex != null && !Regexes.isMatch(newValue, property.regex),
                value: newValue
            }
        }}));
    }

    async onNumberPropertyChange(property: Property, newValue: string): Promise<void> {
        this.setState(prevProps => ({newPropertyValues: {
            ...prevProps.newPropertyValues,
            [property.id]: {
                isInvalid: !Regexes.isMatch(newValue, Regexes.numberRegex),
                value: Number(newValue)
            }
        }}));
    }

    async onBooleanPropertyToggle(property: Property): Promise<void> {
        const { newPropertyValues } = this.state;

        const newValue = !((newPropertyValues[property.id]?.value ?? property.defaultValue) as boolean);
        this.setState(prevProps => ({newPropertyValues: {
            ...prevProps.newPropertyValues,
            [property.id]: {
                isInvalid: false,
                value: newValue
            }
        }}));
    }

    async saveAllProperties(): Promise<void> {
        const { selectedNode } = this.props;
        const { newPropertyValues } = this.state;
        if (!selectedNode)
            return;
        const invalidValues = Object.values(newPropertyValues).filter(value => value.isInvalid);
        if (invalidValues.length > 0)
            return;

        const updateValues = (keyVal: [string, { isInvalid: boolean, value: string | number | boolean }]): Promise<void> => {
            return store.dispatch(setNodePropertyAsync(selectedNode.cellId, selectedNode.nodeId, keyVal[0], keyVal[1].value));
        };
        const actions = Object.entries(newPropertyValues).map(updateValues);
        await Promise.all(actions);
        this.resetAllProperties();
    }

    resetAllProperties(): void {
        const { selectedNode } = this.props;

        if (!selectedNode)
            return;

        const node = getNodeById(store.getState(), selectedNode.cellId, selectedNode.nodeId);
        if (!node)
            return;
        
        const nodeType = NodeTypeManager.getType(node.type);
        if (!nodeType)
            return;

        const properties: Property[] = [];
        nodeType.properties.forEach(property => {
            let prop: Property = property;
            const propIndex = node.properties.findIndex(prop => prop.id === property.id);
            if (propIndex >= 0) {
                const nodeProperty = node.properties[propIndex];
                prop = { 
                    ...property,
                    defaultValue: nodeProperty.value
                };
            }

            if (prop.defaultValue != null && !prop.isHidden)
                properties.push(prop);
        });
        this.setState({properties, newPropertyValues: {}});
    }

    render(): JSX.Element {
        const { selectedNode } = this.props;
        const { inputs, newInputValues, properties, newPropertyValues } = this.state;
        const propertyInvalidValues = Object.values(newPropertyValues).filter(value => value.isInvalid);
        const isPropertyDisabled = propertyInvalidValues.length > 0 || Object.keys(newPropertyValues).length === 0;
        const inputInvalidValues = Object.values(newInputValues).filter(value => value.isInvalid);
        const isInputDisabled = inputInvalidValues.length > 0 || Object.keys(newInputValues).length === 0;

        const node = selectedNode != null ? getNodeById(store.getState(), selectedNode.cellId, selectedNode.nodeId) : undefined;
        const type = node != null ? NodeTypeManager.getType(node.type) : undefined;
        return (
            <div className="w-48 h-full flex flex-col bg-gray-600">
                {node != null && type != null && !type.isHiddenFromList && (
                    <div className="p-2 w-full">
                        <PrimaryButton icon="question"
                            text="Help"
                            className="w-full justify-center text-sm p-1"
                            onClick={async () => await store.dispatch(selectNodeTypeAsync(node.type))} />
                    </div>
                )}
                <div className="p-2 text-md font-medium text-gray-300">Node Inputs</div>
                <Scrollbars autoHeight autoHeightMax="100%">
                    <div className="">
                        {selectedNode && (
                            <>
                                {inputs.map((input, index) => (
                                    <div key={`${selectedNode.nodeId}-${input.id}`} className="flex flex-row items-center justify-between px-2 pb-3">
                                        <div className="pr-4 flex flex-col justify-center text-sm text-gray-200">
                                            <span className="font-medium">{input.id}</span>
                                            <span className="text-xs" style={{marginTop: "-0.25rem"}}>{input.type}</span>
                                        </div>
                                        {input.type === "string" && (
                                            <TextArea value={(newInputValues[input.id]?.value != null ? newInputValues[input.id].value : input.defaultValue) as string}
                                                className="py-0"
                                                regex={input.regex}
                                                onChange={async event => {
                                                    const value = event.target.value;
                                                    await this.onStringInputChange(input, value);
                                                }} />
                                        ) ||
                                        input.type === "number" && (
                                            <Input value={`${(newInputValues[input.id]?.value ?? input.defaultValue) as number}`}
                                                className="py-0"
                                                regex={input.regex ?? Regexes.numberRegex}
                                                placeholder="Number"
                                                onChange={async event => {
                                                    const value = event.target.value;
                                                    await this.onNumberInputChange(input, value);
                                                }} />
                                        ) ||
                                        input.type === "boolean" && (
                                            <Checkbox value={(newInputValues[input.id]?.value ?? input.defaultValue) as boolean}
                                                onToggle={async () => await this.onBooleanInputToggle(input)} />
                                        )}
                                    </div>
                                ))}
                                {inputs.length > 0 && (
                                    <div className="flex flex-row justify-center text-xs px-4 mt-1">
                                        <OKButton icon="save" text="Save All"
                                            className="mr-2"
                                            isDisabled={isInputDisabled}
                                            onClick={this.saveAllInputs} />

                                        <SecondaryButton icon="redo-alt"
                                            className="px-3 hover:bg-gray-500"
                                            onClick={this.resetAllInputs} />
                                    </div>
                                )}
                            </>
                        )}
                    </div>
                </Scrollbars>
                <div className="mt-4 p-2 text-md font-medium text-gray-300">Node Properties</div>
                <Scrollbars autoHeight autoHeightMax="100%">
                    <div className="">
                        {selectedNode && (
                            <>
                                {properties.map((property, index) => (
                                    <div key={`${selectedNode.nodeId}-${property.id}`} className="flex flex-row items-center justify-between px-2 pb-3">
                                        <div className="pr-4 flex flex-col justify-center text-sm text-gray-200">
                                            <span className="font-medium">{property.id}</span>
                                            <span className="text-xs" style={{marginTop: "-0.25rem"}}>{property.type}</span>
                                        </div>
                                        {property.type === "string" && (
                                            <TextArea value={(newPropertyValues[property.id]?.value != null ? newPropertyValues[property.id].value : property.defaultValue) as string}
                                                className="py-0"
                                                readonly={property.isReadOnly}
                                                regex={property.regex}
                                                onChange={async event => {
                                                    const input = event.target.value;
                                                    await this.onStringPropertyChange(property, input);
                                                }} />
                                        ) ||
                                        property.type === "number" && (
                                            <Input value={`${(newPropertyValues[property.id]?.value ?? property.defaultValue) as number}`}
                                                className="py-0"
                                                readonly={property.isReadOnly}
                                                regex={Regexes.numberRegex}
                                                placeholder="Number"
                                                onChange={async event => {
                                                    const input = event.target.value;
                                                    await this.onNumberPropertyChange(property, input);
                                                }} />
                                        ) ||
                                        property.type === "boolean" && (
                                            <Checkbox value={(newPropertyValues[property.id]?.value ?? property.defaultValue) as boolean}
                                                onToggle={async () => await this.onBooleanPropertyToggle(property)} />
                                        )}
                                    </div>
                                ))}
                                {properties.length > 0 && (
                                    <div className="flex flex-row justify-center text-xs px-4 mt-1">
                                        <OKButton icon="save" text="Save All"
                                            className="mr-2"
                                            isDisabled={isPropertyDisabled}
                                            onClick={this.saveAllProperties} />

                                        <SecondaryButton icon="redo-alt"
                                            className="px-3 hover:bg-gray-500"
                                            onClick={this.resetAllProperties} />
                                    </div>
                                )}
                            </>
                        )}
                    </div>
                </Scrollbars>
            </div>
        );
    }
}

const PropertiesSidebar = connect(mapStateToProps)(PropertiesSidebarInner);
export default PropertiesSidebar;