import Konva from "konva";
import { Vector2d } from "konva/lib/types";
import React, { useRef, useState } from "react";
import { Path, Rect, Text } from "react-konva";
import store from "../app/store";
import { NodeConnectionObject } from "../db/models";
import { deleteConnectionWithIdAsync, getNodeById } from "../features/notebooks/notebookSlice";
import NodeConnectionManager from "../managers/NodeConnectionManager";
import NodeTypeManager from "../managers/NodeTypeManager";
import Colors from "../utils/Colors";
import KonvaUtils from "../utils/KonvaUtils";

type NodeConnectionProps = {
    cellId: string;
    connection: NodeConnectionObject;
}

type NodeConnectionState = {
    fromNodeWidth: number;
    fromSlotHeight: number;
    toSlotHeight: number;
    fromCoords: Vector2d;
    toCoords: Vector2d;
}

export default class NodeConnection extends React.Component<NodeConnectionProps, NodeConnectionState> {
    constructor(props: NodeConnectionProps) {
        super(props);

        this.state = {
            fromNodeWidth: 200,
            fromSlotHeight: 40,
            toSlotHeight: 40,
            fromCoords: { x: 0, y: 0 },
            toCoords: { x: 0, y: 0 }
        };

        this.setFromCoords = this.setFromCoords.bind(this);
        this.setToCoords = this.setToCoords.bind(this);

        this.onDeleteConnection = this.onDeleteConnection.bind(this);
    }

    componentDidMount(): void {
        const { cellId, connection } = this.props;
        const fromNode = getNodeById(store.getState(), cellId, connection.fromNodeId);
        const toNode = getNodeById(store.getState(), cellId, connection.toNodeId);
        const fromNodeType = NodeTypeManager.getType(fromNode?.type ?? "");
        const toNodeType = NodeTypeManager.getType(toNode?.type ?? "");
        const topBarHeight = 20;
        const slotHeight = 40;

        const fromNodeWidth = fromNodeType?.style?.width ?? 200;
        let fromSlotIndex = fromNodeType?.outputSlots?.findIndex(slot => slot.id === connection.fromSlotId);
        if (!fromSlotIndex || fromSlotIndex < 0)
            fromSlotIndex = 0;
        const fromSlotHeight = topBarHeight + slotHeight * (fromSlotIndex + 1) - (slotHeight / 2);

        let toSlotIndex = toNodeType?.inputSlots?.findIndex(slot => slot.id === connection.toSlotId);
        if (!toSlotIndex || toSlotIndex < 0)
            toSlotIndex = 0;
        const toSlotHeight = topBarHeight + slotHeight * (toSlotIndex + 1) - (slotHeight / 2);
        this.setState({ fromNodeWidth, fromSlotHeight, toSlotHeight });
    }

    setFromCoords(fromCoords: Vector2d): void {
        this.setState({fromCoords});
    }

    setToCoords(toCoords: Vector2d): void {
        this.setState({toCoords});
    }

    async onDeleteConnection(): Promise<void> {
        const { cellId, connection } = this.props;
        NodeConnectionManager.removeRef(cellId, connection.connectionId);
        await store.dispatch(deleteConnectionWithIdAsync(cellId, connection.connectionId));
    }

    render(): JSX.Element {
        const { fromNodeWidth, fromSlotHeight, toSlotHeight, fromCoords, toCoords } = this.state;

        return (
            <NodeConnectionInner strokeWidth={3}
                lineSlack={30}
                fromNodeWidth={fromNodeWidth}
                fromSlotHeight={fromSlotHeight}
                toSlotHeight={toSlotHeight}
                fromCoords={fromCoords} 
                toCoords={toCoords}
                onClick={this.onDeleteConnection} />
        );
    }
}

type NodeConnectionInnerProps = {
    strokeWidth: number;
    lineSlack: number;
    fromNodeWidth: number;
    fromSlotHeight: number;
    toSlotHeight: number;
    fromCoords: Vector2d;
    toCoords: Vector2d;
    onClick: () => Promise<void>;
}

function NodeConnectionInner(props: NodeConnectionInnerProps) {
    const { strokeWidth, lineSlack, fromNodeWidth, fromSlotHeight, toSlotHeight, fromCoords, toCoords, onClick } = props;
    const [isHovered, setIsHovered] = useState<boolean>(false);
    const path = useRef<Konva.Path>(null);

    //console.log(`Length: ${path.current?.getLength()}, Dash: ${path.current?.dash()}, Dash Offset: ${path.current?.dashOffset()}`);

    const midPoint: Vector2d = {
        x: ((toCoords.x - fromCoords.x - fromNodeWidth) / 2) + (fromCoords.x + fromNodeWidth),
        y: ((toCoords.y + toSlotHeight - fromCoords.y - fromSlotHeight) / 2) + (fromCoords.y + fromSlotHeight)
    };

    const pathData = `M${fromCoords.x + fromNodeWidth} ${fromCoords.y + fromSlotHeight} ` +
                     `Q${fromCoords.x + fromNodeWidth + lineSlack} ${fromCoords.y + fromSlotHeight} ` + 
                         `${midPoint.x} ${midPoint.y} ` +
                     `Q${toCoords.x - lineSlack} ${toCoords.y + toSlotHeight} ` + 
                         `${toCoords.x} ${toCoords.y + toSlotHeight}`;

    return (
        <>
            <Path ref={path}
                data={pathData} 
                stroke={Colors.Gray[900]}
                strokeWidth={strokeWidth}
                listening={false} />
            <Path data={pathData}
                stroke="transparent"
                strokeWidth={50}
                onMouseEnter={event => {
                    KonvaUtils.setCursor(event, "pointer");
                    setIsHovered(true);
                }}
                onMouseLeave={event => {
                    KonvaUtils.setCursor(event, "default");
                    setIsHovered(false);
                }}
                onClick={async (event) => {
                    KonvaUtils.setCursor(event, "default");
                    await onClick();
                }} />
            <Rect visible={isHovered}
                x={midPoint.x - 10}
                y={midPoint.y - 6}
                width={20}
                height={12}
                fill={Colors.Gray[900]}
                listening={false} />
            <Text visible={isHovered}
                x={midPoint.x - 10}
                y={midPoint.y - 6}
                width={20}
                height={12}
                text="&#215;"
                fontSize={14}
                offsetY={-1}
                align="center"
                verticalAlign="middle"
                fill={Colors.Gray[100]}
                listening={false} />
        </>
    );
}