import { DataFrame, Series } from "danfojs";
import * as Danfo from "danfojs";
import Errors from "../utils/Errors";
import Regexes from "../utils/Regexes";
import { Types } from "../utils/Types";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler } from "../types";
import DanfoUtils from "../utils/DanfoUtils";
import Meta from "../utils/Meta";
import Descriptions from "../utils/Descriptions";
import { ArrayType1D } from "danfojs/types/shared/types";

const mapDataFrame: NodeType = {
    id: "map-dataframe",
    name: "Map DataFrame",
    description: Descriptions.NodeSpecific.MapDataFrame.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "column",
            description: Descriptions.NodeSpecific.MapDataFrame.ColumnProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelRegex, Regexes.emptyRegex)
        },
        {
            id: "mappings",
            description: Descriptions.NodeSpecific.MapDataFrame.MappingsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.valueMapRegex, Regexes.emptyRegex)
        },
        {
            id: "replaceColumn",
            description: Descriptions.NodeSpecific.MapDataFrame.ReplaceColumnProperty,
            type: "boolean",
            defaultValue: false
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const column = properties["column"] as string;
        const replaceColumn = properties["replaceColumn"] as boolean;
        const mappingsVal = properties["mappings"] as string;
        
        let mappings: string[] | undefined;
        if (Regexes.isMatch(mappingsVal, Regexes.valueMapRegex))
            mappings = Regexes.processCommaDelimString(mappingsVal, Regexes.baseValueMapRegex);

        if (!Regexes.isMatch(column, Regexes.labelRegex))
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("column"));

        if (!mappings || mappings.length <= 0)
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("mappings"));

        if (!Regexes.isMatch(column, Regexes.labelRegex) || !mappings || mappings.length <= 0)
            return {
                output: df
            };

        const columnIndex = df.columns.indexOf(column);
        if (columnIndex < 0) {
            errorHandler.warning(Errors.ValueNotExists(column, "column"));
            return {
                output: df
            };
        }
            
        const columnType = df.dtypes[columnIndex];
        const mappingsObj: {
            [id: string]: string | number | boolean;
        } = {};
        mappings.forEach(map => {
            const split = map.split(":");
            const left: string = split[0].replaceAll("'", "");
            let right: string | number | boolean = split[1].replaceAll("'", "");
            if (right === "$mean") {
                const tempSeries = df.column(column).astype("float32") as Series;
                right = tempSeries.mean();
            }

            if (columnType === "float32" || columnType === "int32")
                right = Number(right);
            else if (columnType === "boolean")
                right = right === "true" || right === "True" || right === "1";

            mappingsObj[left] = right;
        });

        let series = df.column(column);
        series = DanfoUtils.map(series, mappingsObj);
        
        // For renaming column if replaceColumn is not ticked,
        // will prevent name collisions
        let newCol = column;
        if (!replaceColumn) {
            let i = 1;
            while (df.columns.includes(newCol) && i < 10000)
                newCol = `${column}_${i++}`;
        }

        const newDf = df.copy();
        newDf.addColumn({
            column: newCol,
            values: replaceColumn ? series.values as ArrayType1D : series,
            inplace: true
        }) as DataFrame;

        return {
            output: newDf
        };
    }
};

const renameColumns: NodeType = {
    id: "rename-columns",
    name: "Rename Columns",
    description: Descriptions.NodeSpecific.RenameColumns.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "mappings",
            description: Descriptions.Common.DataFrameModify.MappingsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.valueMapRegex, Regexes.emptyRegex)
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const mappingsVal = properties["mappings"] as string;
        
        let mappings: string[] | undefined;
        if (Regexes.isMatch(mappingsVal, Regexes.valueMapRegex))
            mappings = Regexes.processCommaDelimString(mappingsVal, Regexes.baseValueMapRegex);

        if (!mappings || mappings.length <= 0)
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("mappings"));

        if (!mappings || mappings.length <= 0)
            return {
                output: df
            };
            
        const mappingsObj: {
            [id: string]: string;
        } = {};
        mappings.forEach(map => {
            const split = map.split(":");
            const left: string = split[0];
            let hasError = false;
            if (!df.columns.includes(left)) {
                errorHandler.error(Errors.ValueNotExists(left, "column"));
                hasError = true;
            }

            const right: string = split[1];
            if (df.columns.includes(right)) {
                errorHandler.error(Errors.ValueExists(right, "column"));
                hasError = true;
            }

            if (hasError)
                return;

            mappingsObj[left] = right;
        });

        if (!mappingsObj)
            return {
                output: df
            };

        const newDf = df.rename({
            mapper: mappingsObj
        }) as DataFrame;

        return {
            output: newDf
        };
    }
};

const removeColumns: NodeType = {
    id: "remove-columns",
    name: "Remove Columns",
    description: Descriptions.NodeSpecific.RemoveColumns.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "columns",
            description: Descriptions.Common.DataFrameModify.ColumnsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelCommaRegex, Regexes.emptyRegex)
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const columnPattern = properties["columns"] as string;

        if (!Regexes.isMatch(columnPattern, Regexes.labelCommaRegex))
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("columns"));

        const columns = Regexes.processCommaDelimString(columnPattern, Regexes.baseLabelRegex);
        if (columns.length <= 0)
            return {
                output: df
            };

        let hasError = false;
        columns.forEach(col => {
            if (!df.columns.includes(col)) {
                errorHandler.error(Errors.ValueNotExists(col, "column"));
                hasError = true;
            }
        });
        if (hasError)
            return {
                output: df
            };


        const newDf = df.drop({ columns }) as DataFrame;
        return {
            output: newDf
        };
    }
};

const fillNaDataFrame: NodeType = {
    id: "fillna-dataframe",
    name: "Fill Null DataFrame",
    description: Descriptions.NodeSpecific.FillNaDataFrame.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "mappings",
            description: Descriptions.NodeSpecific.FillNaDataFrame.MappingsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelValueMapRegex, Regexes.emptyRegex)
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const mappingsVal = properties["mappings"] as string;
        
        let mappings: string[] | undefined;
        if (Regexes.isMatch(mappingsVal, Regexes.labelValueMapRegex))
            mappings = Regexes.processCommaDelimString(mappingsVal, Regexes.baseLabelValueMapRegex);

        if (!mappings || mappings.length <= 0)
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("mappings"));

        if (!mappings || mappings.length <= 0)
            return {
                output: df
            };
            
        const mappingsObj: {
            [id: string]: string | number | boolean;
        } = {};
        mappings.forEach(map => {
            const split = map.split(":");
            const left: string = split[0];
            const columnIndex = df.columns.indexOf(left);
            if (columnIndex < 0) {
                errorHandler.error(Errors.ValueNotExists(left, "column"));
                return;
            }

            const right: string = split[1].replaceAll("'", "");
            const type = df.dtypes[columnIndex];
            let newValue: string | number | boolean;
            if (type === "float32" || type === "int32") {
                if (right === "$mean") {
                    newValue = df.column(left).mean();
                    newValue = type === "int32" ? Math.round(newValue) : newValue;
                }
                else
                    newValue = Number(right);
            }
            else if (type === "boolean")
                newValue = right.toLowerCase() === "true" || right === "1";
            else
                newValue = right;
            
            mappingsObj[left] = newValue;
        });

        if (!mappingsObj)
            return {
                output: df
            };

        const newDf = DanfoUtils.fillna(df, Object.keys(mappingsObj), Object.values(mappingsObj));
        return {
            output: newDf
        };
    }
};

const sortDataFrame: NodeType = {
    id: "sort-dataframe",
    name: "Sort DataFrame",
    description: Descriptions.NodeSpecific.SortDataFrame.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "sortByColumn",
            description: Descriptions.NodeSpecific.SortDataFrame.SortByColumnProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelRegex, Regexes.emptyRegex)
        },
        {
            id: "isAscending",
            description: Descriptions.NodeSpecific.SortDataFrame.IsAscendingProperty,
            type: "boolean",
            defaultValue: true
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const sortByColumn = properties["sortByColumn"] as string;
        const isAscending = properties["isAscending"] as boolean;

        if (!Regexes.isMatch(sortByColumn, Regexes.labelRegex)) {
            errorHandler.warning(Errors.EmptyNodePropertyNoEffect("sortByColumn"));
            return {
                output: df
            };
        }

        if (!df.columns.includes(sortByColumn)) {
            errorHandler.warning(Errors.ValueNotExists(sortByColumn, "column"));
            return {
                output: df
            };
        }

        const newDf = df.sort_values({
            by: sortByColumn,
            ascending: isAscending,
            inplace: false
        }) as DataFrame;

        return {
            output: newDf
        };
    }
};

const mergeDataFrame: NodeType = {
    id: "merge-dataframe",
    name: "Merge DataFrames",
    description: Descriptions.NodeSpecific.MergeDataFrames.Description,
    style: {
        width: 180
    },
    inputSlots: [
        {
            id: "left",
            description: Descriptions.NodeSpecific.MergeDataFrames.LeftInput,
            type: Types.OR(Types.DataFrame, Types.Series)
        },
        {
            id: "right",
            description: Descriptions.NodeSpecific.MergeDataFrames.RightInput,
            type: Types.OR(Types.DataFrame, Types.Series)
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "onColumns",
            description: Descriptions.NodeSpecific.MergeDataFrames.OnColumnsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelCommaRegex, Regexes.emptyRegex)
        },
        {
            id: "joinType",
            description: Descriptions.NodeSpecific.MergeDataFrames.JoinTypeProperty,
            type: "string",
            regex: Regexes.combine(/^(left|right|outer|inner)$/, Regexes.emptyRegex)
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["left"]))
            errorHandler.error(Errors.MissingNodeInput("left"));

        if (Meta.isEmpty(input["right"]))
            errorHandler.error(Errors.MissingNodeInput("right"));

        if (Meta.isEmpty(input["left"]) || Meta.isEmpty(input["right"]))
            return {};

        const left = input["left"] as DataFrame | Series;
        const right = input["right"] as DataFrame | Series;
        const onColumnsPattern = properties["onColumns"] as string;
        let joinType = properties["joinType"] as string;

        let onColumns: string[] | undefined;
        if (Regexes.isMatch(onColumnsPattern, Regexes.labelCommaRegex))
            onColumns = Regexes.processCommaDelimString(onColumnsPattern, Regexes.baseLabelRegex);

        // Should simply concat on the column axis or if either are Series
        if (!onColumns || onColumns.length <= 0 || left instanceof Series || right instanceof Series) {
            const newDf = Danfo.concat({
                df_list: [left, right],
                axis: 1
            });
            return {
                output: newDf
            };
        }

        const invalidValues: string[] = [];
        onColumns.forEach((col) => {
            if (!left.columns.includes(col) || !right.columns.includes(col)) {
                errorHandler.error(`The column '${col}' must exist on both the left and right dataframes.`);
                invalidValues.push(col);
            }
        });

        if (invalidValues.length > 0)
            return {
                result: left
            };

        if (!Regexes.isMatch(joinType, /^(left|right|outer|inner)$/))
            joinType = "inner";

        const newDf = Danfo.merge({
            left,
            right,
            on: onColumns,
            how: joinType
        });

        return {
            output: newDf
        };
    }
};

const appendDataFrame: NodeType = {
    id: "append-dataframe",
    name: "Append DataFrames",
    description: Descriptions.NodeSpecific.AppendDataFrame.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input1",
            description: Descriptions.NodeSpecific.AppendDataFrame.Input1Input,
            type: Types.OR(Types.DataFrame, Types.Series)
        },
        {
            id: "input2",
            description: Descriptions.NodeSpecific.AppendDataFrame.Input2Input,
            type: Types.OR(Types.DataFrame, Types.Series)
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input1"]))
            errorHandler.error(Errors.MissingNodeInput("input1"));

        if (Meta.isEmpty(input["input2"]))
            errorHandler.error(Errors.MissingNodeInput("input2"));

        if (Meta.isEmpty(input["input1"]) || Meta.isEmpty(input["input2"]))
            return {};

        const input1 = input["input1"] as DataFrame | Series;
        const input2 = input["input2"] as DataFrame | Series;

        const newDf = Danfo.concat({
            df_list: [input1, input2],
            axis: 0
        });

        return {
            output: newDf
        };
    }
};

const ModifyNodes = [
    mapDataFrame,
    renameColumns,
    removeColumns,
    fillNaDataFrame,
    sortDataFrame,
    mergeDataFrame,
    appendDataFrame
];

export default ModifyNodes;