import cuid from "cuid";
import React from "react";
import Scrollbars from "react-custom-scrollbars-2";
import Dropdown from "react-overlays/esm/Dropdown";
import { Link } from "react-router-dom";
import { twMerge } from "tailwind-merge";
import store from "../app/store";
import { NotebookObject } from "../db/models";
import { updateNotebookNameAsync, closeNotebookAsync, deleteNotebookAsync, createNewNotebookAsync } from "../features/notebooks/notebookSlice";
import { ContextMenuItem, CustomModal, PrimaryButton, SecondaryButton, CancelButton, Input, OKButton } from "./CustomComponents";

type NotebookItemProps = {
    notebookId: string;
    text: string;
    className?: string;
}

type NotebookItemState = {
    id: string;
    isOptions: boolean;
    isRename: boolean;
    isDelete: boolean;
    renameValue: string;
}

class NotebookItem extends React.Component<NotebookItemProps, NotebookItemState> {
    constructor(props: NotebookItemProps) {
        super(props);

        this.state = {
            id: cuid(),
            isOptions: false,
            isRename: false,
            isDelete: false,
            renameValue: ""
        };

        this.showRenameModal = this.showRenameModal.bind(this);
        this.hideRenameModal = this.hideRenameModal.bind(this);
        this.renameNotebook = this.renameNotebook.bind(this);

        this.showDeleteModal = this.showDeleteModal.bind(this);
        this.hideDeleteModal = this.hideDeleteModal.bind(this);
        this.deleteNotebook = this.deleteNotebook.bind(this);
    }

    showRenameModal(): void {
        this.setState({isRename: true, isOptions: false, renameValue: ""});
    }

    hideRenameModal(): void {
        this.setState({isRename: false});
    }

    showDeleteModal(): void {
        this.setState({isDelete: true, isOptions: false});
    }

    hideDeleteModal(): void {
        this.setState({isDelete: false});
    }

    async renameNotebook(): Promise<void> {
        const { notebookId } = this.props;
        const { renameValue } = this.state;
        if (renameValue !== "") {
            await store.dispatch(updateNotebookNameAsync(notebookId, renameValue));
            this.hideRenameModal();
        }
    }

    async deleteNotebook(): Promise<void> {
        const { notebookId } = this.props;
        
        await store.dispatch(closeNotebookAsync());
        await store.dispatch(deleteNotebookAsync(notebookId));
        this.hideDeleteModal();
    }

    render() {
        const { notebookId, text, className } = this.props;
        const { id, isOptions, isRename, renameValue, isDelete } = this.state;
        return (
            <>
                <Link className={twMerge("transition duration-200 ease-in-out p-2 text-sm flex items-center justify-between w-full bg-gray-600 text-gray-200 hover:bg-indigo-400 hover:text-indigo-100", className)}
                    to={`/main/${notebookId}`}
                    onMouseLeave={() => this.setState({isOptions: false})}>
                    <span className="">{text}</span>
                    <div className="flex flex-row">
                        <Dropdown show={isOptions}
                            onToggle={next => this.setState({isOptions: next})}>
                            <div className="relative flex flex-row-reverse">
                                <Dropdown.Toggle>
                                    {(props, meta) => (
                                        <SecondaryButton icon="ellipsis-v" 
                                            className="w-full flex items-center justify-center h-6 text-sm bg-transparent hover:bg-indigo-600"
                                            onClick={event => {
                                                event.preventDefault();
                                                meta.toggle(!isOptions, event);
                                            }} />
                                    )}
                                </Dropdown.Toggle>
                                <Dropdown.Menu show={isOptions} flip={true}>
                                    {(props) => (
                                        <>
                                            {isOptions && (
                                                <div className="absolute z-20 shadow-lg w-32" style={{top: "1.5rem"}}>
                                                    <div onClick={event => {
                                                        event.preventDefault();
                                                        this.showRenameModal();
                                                    }}>
                                                        <ContextMenuItem text="Change Name"
                                                            icon="edit" />
                                                    </div>
                                                    <div onClick={event => {
                                                        event.preventDefault();
                                                        this.showDeleteModal();
                                                    }}>
                                                        <ContextMenuItem text="Delete"
                                                            icon="trash-alt"
                                                            className="bg-red-300 text-red-700 hover:bg-red-800 hover:text-red-200" />
                                                    </div>
                                                </div>
                                            ) || 
                                            !isOptions && (
                                                <div className=""></div>
                                            )}
                                        </>
                                    )}
                                </Dropdown.Menu>
                            </div>
                        </Dropdown>
                    </div>
                </Link>

                <CustomModal show={isRename}
                    onHide={() => this.hideRenameModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium">Rename</h2>
                            <h2 className="text-lg font-bold">&apos;{text}&apos;</h2>
                        </div>
                        <div className="mb-4">
                            <Input value={renameValue} 
                                label="Name" 
                                placeholder="Enter notebook name..."
                                onChange={event => this.setState({renameValue: event.target.value})} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Save" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.renameNotebook} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideRenameModal()} />
                        </div>
                    </div>
                </CustomModal>

                <CustomModal show={isDelete}
                    onHide={() => this.hideDeleteModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <div className="mb-4 flex flex-row">
                            <h2 className="mr-1 text-lg font-medium text-red-600">Delete</h2>
                            <h2 className="text-lg font-bold text-red-600">&apos;{text}&apos;?</h2>
                        </div>
                        <div className="mb-4 w-full flex items-center">
                            <span className="text-center">Are you sure you want to delete &apos;{text}&apos;? This action is <span className="text-red-600 font-bold">irreversible.</span></span>
                        </div>
                        <div className="flex flex-row-reverse">
                            <CancelButton text="Delete" 
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.deleteNotebook} />
                            <SecondaryButton text="Cancel" 
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideDeleteModal()} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}

type NotebookSidebarProps = {
    notebooks: NotebookObject[];
}

type NotebookSidebarState = {
    isAddingNotebook: boolean;
    notebookName: string;
}

export default class NotebookSidebar extends React.Component<NotebookSidebarProps, NotebookSidebarState> {
    constructor(props: NotebookSidebarProps) {
        super(props);

        this.state = {
            isAddingNotebook: false,
            notebookName: ""
        };

        this.showAddNotebookModal = this.showAddNotebookModal.bind(this);
        this.hideAddNotebookModal = this.hideAddNotebookModal.bind(this);
        this.addNotebook = this.addNotebook.bind(this);
    }

    showAddNotebookModal(): void {
        this.setState({isAddingNotebook: true, notebookName: ""});
    }

    hideAddNotebookModal(): void {
        this.setState({isAddingNotebook: false});
    }

    async addNotebook(): Promise<void> {
        const { notebookName } = this.state;

        await store.dispatch(createNewNotebookAsync(notebookName));
        this.hideAddNotebookModal();
    }

    render(): JSX.Element {
        const { notebooks } = this.props;
        const { isAddingNotebook, notebookName } = this.state;
        const sortedNotebooks = notebooks.slice().sort((a, b) => a.name.localeCompare(b.name));
        return (
            <>
                <div className="w-48 h-full flex flex-col bg-gray-600">
                    <div className="p-2 text-md font-medium text-gray-300">Notebooks</div>
                    <Scrollbars>
                        <div className="grid gap-2">
                            <div className="grid gap-2">
                                {sortedNotebooks.map((notebook, index) => (
                                    <NotebookItem key={index} notebookId={notebook.notebookId} text={notebook.name} />
                                ))}
                            </div>
                            <OKButton icon="plus" text="Add Notebook"
                                className="text-xs w-full"
                                onClick={this.showAddNotebookModal} />
                        </div>
                    </Scrollbars>
                </div>
                <CustomModal show={isAddingNotebook}
                    onHide={() => this.hideAddNotebookModal()}>
                    <div className="p-4 bg-gray-100 w-64">
                        <h2 className="mb-4 text-lg font-medium">Create New Notebook</h2>
                        <div className="mb-4">
                            <Input value={notebookName}
                                label="Name"
                                placeholder="Enter notebook name..."
                                onChange={event => this.setState({notebookName: event.target.value})} />
                        </div>
                        <div className="flex flex-row-reverse">
                            <PrimaryButton text="Create"
                                className="px-2 py-1 ml-2 text-xs"
                                onClick={this.addNotebook} />
                            <SecondaryButton text="Cancel"
                                className="px-2 py-1 text-xs"
                                onClick={() => this.hideAddNotebookModal()} />
                        </div>
                    </div>
                </CustomModal>
            </>
        );
    }
}