import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Vector2d } from "konva/lib/types";
import React from "react";
import Scrollbars from "react-custom-scrollbars-2";
import store from "../app/store";
import { createNewNodeAsync, selectNodeTypeAsync } from "../features/notebooks/notebookSlice";
import NodeTypeManager from "../managers/NodeTypeManager";
import { DefinedNodeType } from "../types";
import { OKButton, PrimaryButton, SecondaryButton } from "./CustomComponents";

type AddNodeItemProps = {
    typeId: string;
    onAddNode: (nodeType: DefinedNodeType) => Promise<void>;
}

type AddNodeItemState = {

}

class AddNodeItem extends React.Component<AddNodeItemProps, AddNodeItemState> {
    constructor(props: AddNodeItemProps) {
        super(props);

        this.state = {

        };
    }

    render(): JSX.Element | null {
        const { typeId, onAddNode } = this.props;
        const nodeType = NodeTypeManager.getType(typeId);
        if (!nodeType)
            return null;
        return (
            <div className="px-4 py-2 text-gray-800 hover:bg-gray-200">
                <div className="flex flex-row items-center justify-between">
                    <div className="flex flex-col mr-4">
                        <span className="font-bold text-sm">{nodeType.name}</span>
                        {nodeType.description && (
                            <span className="text-xs text-gray-600">{nodeType.description}</span>
                        )}
                    </div>
                    <div className="flex flex-row">
                        <OKButton icon="plus" 
                            text="Create"
                            className="px-2 py-1 mr-1 text-xs"
                            onClick={async () => await onAddNode(nodeType)} />
                        <PrimaryButton icon="question"
                            className="px-2 py-1 text-xs"
                            onClick={async () => await store.dispatch(selectNodeTypeAsync(typeId))} />
                    </div>
                </div>
            </div>
        );
    }
}

type NodeGroup = {
    [id: string]: NodeGroup | string;
}

type AddNodeGroupProps = {
    group: NodeGroup;
    onAddNode: (nodeType: DefinedNodeType) => Promise<void>;
}

type AddNodeGroupState = {
    isOpen: {
        [id: string]: boolean
    };
}

export default class AddNodeGroup extends React.Component<AddNodeGroupProps, AddNodeGroupState> {
    constructor(props: AddNodeGroupProps) {
        super(props);

        this.state = {
            isOpen: {}
        };

        this.toggleGroup = this.toggleGroup.bind(this);
    }

    toggleGroup(id: string): void {
        this.setState(prevState => ({isOpen: {...prevState.isOpen, [id]: !prevState.isOpen[id]}}));
    }

    render(): JSX.Element {
        const { group, onAddNode } = this.props;
        const { isOpen } = this.state;
        const children = Object.entries(group);
        return (
            <>
                {children.map((child, index) => {
                    if (typeof child[1] === "string")
                        return (
                            <AddNodeItem key={`${child[0]}-${index}`} typeId={child[1]} onAddNode={onAddNode} />
                        );
                    else
                        return (
                            <div key={`${child[0]}-${index}`} style={{marginLeft: "1rem", width: "calc(100%-1rem)"}}>
                                <div className="px-2 py-1 bg-gray-300 text-gray-900 text-xs hover:bg-gray-400"
                                    onClick={() => {
                                        this.toggleGroup(`${child[0]}-${index}`);
                                    }}>
                                    <div className="flex flex-row items-center">
                                        {isOpen[`${child[0]}-${index}`] && (
                                            <FontAwesomeIcon style={{marginRight: "5px"}} icon="caret-down" />
                                        ) ||
                                        !isOpen[`${child[0]}-${index}`] && (
                                            <FontAwesomeIcon style={{marginRight: "8px"}} icon="caret-right" />
                                        )}
                                        <span className="">{child[0]}</span>
                                    </div>
                                </div>
                                {isOpen[`${child[0]}-${index}`] && (
                                    <AddNodeGroup group={child[1]} onAddNode={onAddNode} />
                                )}
                            </div>
                        );
                })}
            </>
        );
    }
}

type AddNodeModalProps = {
    cellId: string;
    stagePos: Vector2d;
    show: boolean;
    onHide?: () => void;
}

type AddNodeModalState = {
    nextPosIndex: number;
}

class AddNodeModal extends React.Component<AddNodeModalProps, AddNodeModalState> {
    constructor(props: AddNodeModalProps) {
        super(props);

        this.state = {
            nextPosIndex: 1
        };

        this.onHideModal = this.onHideModal.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);

        this.addNode = this.addNode.bind(this);
    }

    componentDidMount(): void {
        window.addEventListener("keydown", this.onKeyDown);
    }

    componentDidUpdate(prevProps: AddNodeModalProps): void {
        if (prevProps.show !== this.props.show)
            this.setState({nextPosIndex: 1});
    }

    componentWillUnmount(): void {
        window.removeEventListener("keydown", this.onKeyDown);
    }

    onKeyDown(event: KeyboardEvent): void {
        if (event.key === "Escape")
            this.onHideModal();
    }

    onHideModal(): void {
        const { onHide } = this.props;
        if (onHide)
            onHide();
    }

    async addNode(nodeType: DefinedNodeType): Promise<void> {
        const { cellId, stagePos } = this.props;
        const { nextPosIndex } = this.state;

        const nextPos: Vector2d = {
            x: stagePos.x + (50 * nextPosIndex),
            y: stagePos.y + (50 * nextPosIndex)
        };
        await store.dispatch(createNewNodeAsync(cellId, nodeType.name, nodeType.id, nextPos));
        this.setState(prevState => ({
            nextPosIndex: prevState.nextPosIndex + 1
        }));
    }

    render(): JSX.Element {
        const { show } = this.props;
        const typeIds = NodeTypeManager.getVisibleTypeIds();

        const objCreator = (partialId: string, partialGroup: NodeGroup, fullId: string): NodeGroup => {
            const ids = partialId.split(":");
            if (ids.length <= 1)
                partialGroup[partialId] = fullId;
            else {
                let partial = partialGroup[ids[0]] as NodeGroup;
                if (!partial)
                    partial = {};
                const obj = objCreator(ids.slice(1).join(":"), partial, fullId);
                partialGroup[ids[0]] = obj;
            }
            return partialGroup;
        };

        let typeObj: NodeGroup = {};
        typeIds.forEach(typeId => {
            typeObj = objCreator(typeId, typeObj, typeId);
        });

        return (
            <>
                {show && (
                    <>
                        <div className="absolute top-0 right-0 bottom-0 left-0 bg-gray-900 opacity-50"
                            onClick={this.onHideModal}></div>
                        <div className="absolute top-0 right-0 bottom-0 left-0 flex flex-col p-4 bg-gray-100"
                            style={{minWidth: "32rem", width: "40%", height: "20rem", top: "50%", left: "50%", transform: "translateX(-50%) translateY(-50%)"}}>
                            {/*
                            <div className="mb-4">
                                <Input placeholder="Search..." icon="search" />
                            </div>
                            */}

                            <div className="grid gap-2 h-full mb-4">
                                <Scrollbars>
                                    <div style={{marginLeft: "-1rem"}}>
                                        <AddNodeGroup group={typeObj} onAddNode={this.addNode} />
                                    </div>
                                </Scrollbars>
                            </div>
                            
                            <div className="flex flex-row-reverse">
                                <PrimaryButton text="Done"
                                    className="px-2 py-1 text-xs"
                                    onClick={this.onHideModal} />
                            </div>
                        </div>
                    </>
                )}
            </>
        );
    }
}

type NodeControlsProps = {
    cellId: string;
    stagePos: Vector2d;
    onRunCell: () => Promise<void>;
}

type NodeControlsState = {
    showModal: boolean;
}

export class NodeControls extends React.Component<NodeControlsProps, NodeControlsState> {
    constructor(props: NodeControlsProps) {
        super(props);

        this.state = {
            showModal: false
        };
    }

    render(): JSX.Element {
        const { cellId, stagePos, onRunCell } = this.props;
        const { showModal } = this.state;
        return (
            <>
                <div className="absolute flex flex-row" style={{top: "1rem", left: "1rem"}}>
                    <PrimaryButton icon="plus" 
                        text="Add Node"
                        className="text-xs px-2 py-1 mr-2"
                        onClick={() => this.setState({showModal: true})} />
                    <OKButton icon="play"
                        text="Run"
                        className="text-xs px-2 py-1"
                        onClick={async () => await onRunCell()} />
                    
                    <div className="relative">
                        
                    </div>
                </div>
                
                <AddNodeModal cellId={cellId}
                    show={showModal}
                    stagePos={stagePos}
                    onHide={() => this.setState({showModal: false})} />
            </>
        );
    }
}

type NodeCellControlsProps = {
    onZoomIn: React.MouseEventHandler<HTMLButtonElement>;
    onZoomOut: React.MouseEventHandler<HTMLButtonElement>;
    onReset: React.MouseEventHandler<HTMLButtonElement>;
}

export function NodeCellControls(props: NodeCellControlsProps): JSX.Element {
    return (
        <div className="absolute flex flex-col items-end" style={{top: "1rem", right: "1rem"}}>
            <SecondaryButton icon="plus"
                className="text-sm p-1 mb-2"
                onClick={props.onZoomIn} />
            <SecondaryButton icon="minus"
                className="text-sm p-1 mb-2"
                onClick={props.onZoomOut} />
            <SecondaryButton text="Reset"
                className="text-xs px-2 py-1"
                onClick={props.onReset} />
        </div>
    );
}