import cuid from "cuid";
import { Vector2d } from "konva/lib/types";
import React from "react";
import { connect } from "react-redux";
import store, { RootState } from "../app/store";
import { DataSourceObject, NodeObject, VariableObject } from "../db/models";
import { addNodeAsync, setIsDraggingSourceAsync, setIsDraggingVariableAsync } from "../features/notebooks/notebookSlice";
import { SourceType, SourceTypes, Types, VariableType } from "../utils/Types";

type NodeCellDropZoneProps = {
    cellId: string;
    stagePos: Vector2d;
    isDraggingSource: boolean;
    isDraggingVariable: boolean;
}

type NodeCellDropZoneState = {
    isHovering: boolean;
}

const mapStateToProps = (state: RootState) => {
    return {
        isDraggingSource: state.notebook.isDraggingSource,
        isDraggingVariable: state.notebook.isDraggingVariable
    };
};

class NodeCellDropZoneInner extends React.Component<NodeCellDropZoneProps, NodeCellDropZoneState> {
    constructor(props: NodeCellDropZoneProps) {
        super(props);

        this.state = {
            isHovering: false
        };

        this.onDrop = this.onDrop.bind(this);
    }

    async onDrop(event: React.DragEvent<HTMLDivElement>): Promise<void> {
        const { cellId, stagePos } = this.props;
        event.preventDefault();
        const data = JSON.parse(event.dataTransfer.getData("text/plain"));
        const dataType = data["dataType"];
        if (dataType === "source") {
            await store.dispatch(setIsDraggingSourceAsync(false));

            // Add new node representing the data source
            const source = data["source"] as DataSourceObject;
            const pos: Vector2d = {
                x: stagePos.x + 50,
                y: stagePos.y + 50
            };
            const node: NodeObject = {
                nodeId: cuid(),
                name: source.name,
                type: `base:${SourceTypes[source.type as SourceType]}-source`,
                coords: pos,
                properties: [
                    {
                        id: "sourceId",
                        value: source.sourceId
                    }
                ],
                inputValues: []
            };
            await store.dispatch(addNodeAsync(cellId, node));
        } else if (dataType === "variable") {
            await store.dispatch(setIsDraggingVariableAsync(false));

            const type = data["type"] as "get" | "set";
            const variable = data["variable"] as VariableObject;
            const pos: Vector2d = {
                x: stagePos.x + 50,
                y: stagePos.y + 50
            };
            const node: NodeObject = {
                nodeId: cuid(),
                name: `${type === "get" ? "Get" : "Set"} ${variable.name}`,
                type: `base:Variables:${Types[variable.type as VariableType]}-variable-${type}ter`,
                coords: pos,
                properties: [
                    {
                        id: "variableId",
                        value: variable.variableId
                    }
                ],
                inputValues: []
            };
            await store.dispatch(addNodeAsync(cellId, node));
        }

        this.setState({isHovering: false});
    }

    render(): JSX.Element | null {
        const { isDraggingSource, isDraggingVariable } = this.props;
        const { isHovering } = this.state;
        if (!isDraggingSource && !isDraggingVariable)
            return null;

        return (
            <div className="absolute top-0 left-0 right-0 bottom-0"
                onDragEnter={event => {
                    event.preventDefault();
                    this.setState({isHovering: true});
                }}
                onDragOver={event => {
                    event.preventDefault();
                }}
                onDragLeave={event => {
                    event.preventDefault();
                    this.setState({isHovering: false});
                }}
                onDrop={this.onDrop}>
                {isHovering && (
                    <div className="flex w-full h-full items-center justify-center bg-gray-900 text-gray-100 opacity-50 pointer-events-none">
                        Drop here...
                    </div>
                )}
            </div>
        );
    }
}

const NodeCellDropZone = connect(mapStateToProps)(NodeCellDropZoneInner);
export default NodeCellDropZone;