import { NodeInputValue, NodeProperty, Primitives, Vector2 } from "../types";

interface IObjectConvertible<TModel, TObject> {
    toObject(): TObject;
}

export type UserObject = {
    userId: string;
    name: string;
    notebooks: NotebookObject[];
}

export class UserModel implements IObjectConvertible<UserModel, UserObject> {
    public userId = "";
    public name: string;
    public notebooks?: {
        [key: string]: NotebookModel;
    }

    constructor(
        name: string
    ) {
        this.name = name;
    }

    static fromObject(obj: UserObject): UserModel {
        const model = new UserModel(obj.name);
        model.userId = obj.userId;
        
        const notebooks: {
            [key: string]: NotebookModel;
        } = {};
        obj.notebooks.forEach(notebook => {
            const notebookModel = NotebookModel.fromObject(notebook);
            notebooks[notebookModel.notebookId] = notebookModel;
        });

        model.notebooks = notebooks;
        return model;
    }

    toObject(): UserObject {
        const userObj: UserObject = {
            userId: this.userId,
            name: this.name,
            notebooks: []
        };

        if (this.notebooks) {
            const notebooks: NotebookObject[] = [];
            Object.entries(this.notebooks).forEach(([key, value]) => {
                notebooks.push(value.toObject());
            });
            userObj.notebooks = notebooks;
        }


        return userObj;
    }
}

export type NotebookObject = {
    notebookId: string;
    name: string; // sort
    dateCreated: number; // sort
    dateModified: number; // sort
    cells: CellObject[];
    sources: DataSourceObject[];
    variables: VariableObject[];
}

export class NotebookModel implements IObjectConvertible<NotebookModel, NotebookObject> {
    public notebookId = "";
    public name: string; // sort
    public dateCreated: number; // sort
    public dateModified: number; // sort
    public cells?: {
        [key: string]: CellModel;
    };
    public sources?: {
        [key: string]: DataSourceModel;
    };
    public variables?: {
        [key: string]: VariableModel;
    };

    constructor(
        name: string,
        dateCreated: number, 
        dateModified: number
    ) {
        this.name = name;
        this.dateCreated = dateCreated;
        this.dateModified = dateModified;
    }
    
    static fromObject(obj: NotebookObject): NotebookModel {
        const model = new NotebookModel(obj.name, obj.dateCreated, obj.dateModified);
        model.notebookId = obj.notebookId;
        
        const cells: {
            [key: string]: CellModel;
        } = {};
        obj.cells.forEach(cell => {
            const cellModel = CellModel.fromObject(cell);
            cells[cellModel.cellId] = cellModel;
        });

        const sources: {
            [key: string]: DataSourceModel;
        } = {};
        obj.sources.forEach(source => {
            const sourceModel = DataSourceModel.fromObject(source);
            sources[sourceModel.sourceId] = sourceModel;
        });

        const variables: {
            [key: string]: VariableModel;
        } = {};
        obj.variables.forEach(variable => {
            const variableModel = VariableModel.fromObject(variable);
            variables[variableModel.variableId] = variableModel;
        });

        model.cells = cells;
        model.sources = sources;
        model.variables = variables;
        return model;
    }
    
    toObject(): NotebookObject {
        const notebookObj: NotebookObject = {
            notebookId: this.notebookId,
            name: this.name,
            dateCreated: this.dateCreated,
            dateModified: this.dateModified,
            cells: [],
            sources: [],
            variables: []
        };

        if (this.cells) {
            const cells: CellObject[] = [];
            Object.entries(this.cells).forEach(([key, value]) => {
                cells.push(value.toObject());
            });
            notebookObj.cells = cells;
        }

        if (this.sources) {
            const sources: DataSourceObject[] = [];
            Object.entries(this.sources).forEach(([key, value]) => {
                sources.push(value.toObject());
            });
            notebookObj.sources = sources;
        }

        if (this.variables) {
            const variables: VariableObject[] = [];
            Object.entries(this.variables).forEach(([key, value]) => {
                variables.push(value.toObject());
            });
            notebookObj.variables = variables;
        }

        return notebookObj;
    }
}

export type DataSourceObject = {
    sourceId: string;
    name: string;
    type: string;
}

// TODO: Eventually add details like source URL depending on the type of data source
export class DataSourceModel implements IObjectConvertible<DataSourceModel, DataSourceObject> {
    public sourceId = "";
    public name: string;
    public type: string;

    constructor(name: string, type: string) {
        this.name = name;
        this.type = type;
    }
    
    static fromObject(obj: DataSourceObject): DataSourceModel {
        const model = new DataSourceModel(obj.name, obj.type);
        model.sourceId = obj.sourceId;
        return model;
    }
    
    toObject(): DataSourceObject {
        return {
            sourceId: this.sourceId,
            name: this.name,
            type: this.type
        };
    }
}

export type VariableObject = {
    variableId: string;
    name: string;
    type: string;
    value?: string | number | boolean;
}

export class VariableModel implements IObjectConvertible<VariableModel, VariableObject> {
    public variableId = "";
    public name: string;
    public type: string;
    public value?: string | number | boolean;

    constructor(name: string, type: string, value?: string | number | boolean) {
        this.name = name;
        this.type = type;
        this.value = value;
    }
    
    static fromObject(obj: VariableObject): VariableModel {
        const model = new VariableModel(obj.name, obj.type, obj.value);
        model.variableId = obj.variableId;
        return model;
    }
    
    toObject(): VariableObject {
        return {
            variableId: this.variableId,
            name: this.name,
            type: this.type,
            value: this.value
        };
    }
}

export type CellObject = {
    cellId: string;
    nextCell: string;
    type: string;
    value?: string;
    nodes: NodeObject[];
    connections: NodeConnectionObject[];
}

export class CellModel implements IObjectConvertible<CellModel, CellObject> {
    public cellId = "";
    public nextCell = "";
    public type: string;
    public value?: string;
    public nodes?: {
        [key: string]: NodeModel;
    };
    public connections?: {
        [key: string]: NodeConnectionModel;
    };

    constructor(  
        type: string
    ) {
        this.type = type;
        if (type === "markdown")
            this.value = "";
    }
    
    static fromObject(obj: CellObject): CellModel {
        const model = new CellModel(obj.type);
        model.cellId = obj.cellId;
        model.nextCell = obj.nextCell;
        model.value = obj.value;

        const nodes: {
            [key: string]: NodeModel;
        } = {};
        obj.nodes.forEach(node => {
            const nodeModel = NodeModel.fromObject(node);
            nodes[nodeModel.nodeId] = nodeModel;
        });

        const connections: {
            [key: string]: NodeConnectionModel;
        } = {};
        obj.connections.forEach(connection => {
            const connectionModel = NodeConnectionModel.fromObject(connection);
            connections[connectionModel.connectionId] = connectionModel;
        });

        model.nodes = nodes;
        model.connections = connections;
        return model;
    }
    
    toObject(): CellObject {
        const cellObj: CellObject = {
            cellId: this.cellId,
            nextCell: this.nextCell,
            type: this.type,
            value: this.value,
            nodes: [],
            connections: []
        };

        if (this.nodes) {
            const nodes: NodeObject[] = [];
            Object.entries(this.nodes).forEach(([key, value]) => {
                nodes.push(value.toObject());
            });
            cellObj.nodes = nodes;
        }

        if (this.connections) {
            const connections: NodeConnectionObject[] = [];
            Object.entries(this.connections).forEach(([key, value]) => {
                connections.push(value.toObject());
            });
            cellObj.connections = connections;
        }

        return cellObj;
    }
}

export type NodeConnectionObject = {
    connectionId: string;
    fromNodeId: string;
    fromSlotId: string;
    toNodeId: string;
    toSlotId: string;
}

export class NodeConnectionModel implements IObjectConvertible<NodeConnectionModel, NodeConnectionObject> {
    public connectionId = "";
    public fromNodeId: string;
    public fromSlotId: string;
    public toNodeId: string;
    public toSlotId: string;

    constructor(
        fromNodeId: string, 
        fromSlotId: string, 
        toNodeId: string, 
        toSlotId: string
    ) {
        this.fromNodeId = fromNodeId;
        this.fromSlotId = fromSlotId;
        this.toNodeId = toNodeId;
        this.toSlotId = toSlotId;
    }
    
    static fromObject(obj: NodeConnectionObject): NodeConnectionModel {
        const model = new NodeConnectionModel(obj.fromNodeId, obj.fromSlotId, obj.toNodeId, obj.toSlotId);
        model.connectionId = obj.connectionId;
        return model;
    }
    
    toObject(): NodeConnectionObject {
        return {
            connectionId: this.connectionId,
            fromNodeId: this.fromNodeId,
            fromSlotId: this.fromSlotId,
            toNodeId: this.toNodeId,
            toSlotId: this.toSlotId
        };
    }
}

export type NodeObject = {
    nodeId: string;
    name: string; // update
    type: string; // used to infer entry and exit slots
    coords: Vector2; // update
    properties: NodeProperty[];
    inputValues: NodeInputValue[];
}

export class NodeModel implements IObjectConvertible<NodeModel, NodeObject> {
    public nodeId = "";
    public name: string; // update
    public type: string; // used to infer entry and exit slots
    public coords: Vector2; // update
    public properties: NodeProperty[] = [];
    public inputValues: NodeInputValue[] = [];

    constructor(
        name: string, 
        type: string,
        coords?: Vector2
    ) {
        this.name = name;
        this.type = type;
        this.coords = coords ?? { x: 0, y: 0 };
    }

    static fromObject(obj: NodeObject): NodeModel {
        const model = new NodeModel(obj.name, obj.type, obj.coords);
        model.nodeId = obj.nodeId;
        model.properties = obj.properties;
        model.inputValues = obj.inputValues;
        return model;
    }

    toObject(): NodeObject {
        return {
            nodeId: this.nodeId,
            name: this.name,
            type: this.type,
            coords: this.coords,
            properties: this.properties,
            inputValues: this.inputValues
        };
    }
}