import { DataFrame, Series } from "danfojs";
import { ArrayType1D, ArrayType2D } from "danfojs/types/shared/types";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler, ChartOutput, NodeCollection } from "../types";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import Meta from "../utils/Meta";
import Regexes from "../utils/Regexes";
import { Types } from "../utils/Types";

const lineChartOutputNode: NodeType = {
    id: "line-chart-output",
    name: "Line Chart",
    //variableName: "lineChartOutputResult",
    description: Descriptions.NodeSpecific.LineChart.Description,
    style: {
        width: 120
    },
    inputSlots: [
        {
            id: "labels",
            description: Descriptions.Common.Chart.LabelsInput,
            type: Types.Series
        },
        {
            id: "data",
            description: Descriptions.Common.Chart.DataInput,
            type: Types.OR(Types.DataFrame, Types.Series)
        }
    ],
    properties: [
        {
            id: "outputOrder",
            description: Descriptions.Common.OutputOrderProperty,
            type: "number"
        },
        {
            id: "title",
            description: Descriptions.Common.Chart.TitleProperty,
            type: "string"
        },
        {
            id: "isSmooth",
            description: Descriptions.NodeSpecific.LineChart.IsSmoothProperty,
            type: "boolean",
            defaultValue: true
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["labels"]))
            errorHandler.error(Errors.MissingNodeInput("labels"));

        if (Meta.isEmpty(input["data"]))
            errorHandler.error(Errors.MissingNodeInput("data"));

        if (Meta.isEmpty(input["labels"]) || Meta.isEmpty(input["data"]))
            return {};

        const labels = input["labels"] as Series;
        const data = input["data"] as DataFrame | Series;
        const title = properties["title"] as string;
        const isSmooth = properties["isSmooth"] as boolean;
        if (labels.shape[0] <= 0)
            errorHandler.error(Errors.EmptyArray("labels", "property"));

        if (data.shape[0] <= 0)
            errorHandler.error(Errors.EmptyArray("data", "property"));

        if (labels.shape[0] <= 0 || data.shape[0] <= 0)
            return {};

        let invalidColumns: string[] = [];
        if (data instanceof DataFrame)
            invalidColumns = data.dtypes.map((type, index) => {
                if (type === "string" || type === "boolean")
                    return data.columns[index];
            }).filter((col): col is string => !!col);
        else
            invalidColumns = (data.dtype === "string" || data.dtype === "boolean") ? ["Series"] : [];
    
        if (invalidColumns.length > 0) {
            errorHandler.error(Errors.DataFrameNumericalColumnsOnly("input2", invalidColumns));
            return {};
        }

        const output: ChartOutput = {
            type: "chart",
            outputOrder: properties["outputOrder"] as number,
            chartType: "multi-line", 
            title: title,
            labels: labels.values as ArrayType1D, 

            /* eslint-disable @typescript-eslint/ban-ts-comment */
            // @ts-ignore
            data: data instanceof Series ? [data.values as ArrayType1D] : data.$dataIncolumnFormat as ArrayType2D,
            /* eslint-enable @typescript-eslint/ban-ts-comment */
            
            dataLabels: data.columns,
            options: {
                isSmooth: isSmooth
            }
        };
        return output;
    }
};

