const violinPlotOutputNode: NodeType = {
    id: "violin-plot-output",
    name: "Violin Plot",
    description: Descriptions.NodeSpecific.ViolinPlot.Description,
    //variableName: "lineChartOutputResult",
    style: {
        width: 120
    },
    inputSlots: [
        {
            id: "data",
            description: Descriptions.Common.Chart.DataWithLabelInput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "outputOrder",
            description: Descriptions.Common.OutputOrderProperty,
            type: "number"
        },
        {
            id: "title",
            description: Descriptions.Common.Chart.TitleProperty,
            type: "string"
        },
        {
            id: "labelsColumn",
            description: Descriptions.Common.Chart.LabelsColumnProperty,
            type: "string",
            regex: Regexes.labelRegex
        },
        {
            id: "ignoreDenseLabels",
            description: Descriptions.Common.Chart.IgnoreDenseLabelsProperty,
            type: "boolean"
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["data"])) {
            errorHandler.error(Errors.MissingNodeInput("data"));
            return {};
        }

        const data = input["data"] as DataFrame;
        const title = properties["title"] as string;
        const labelsColumn = properties["labelsColumn"] as string;
        const ignoreDenseLabels = properties["ignoreDenseLabels"] as boolean;

        if (data.shape[0] <= 0)
            errorHandler.error(Errors.EmptyArray("data", "property"));

        if (!Regexes.isMatch(labelsColumn, Regexes.labelRegex))
            errorHandler.error(Errors.CannotBeEmpty(labelsColumn, "property"));

        if (data.shape[0] <= 0 || !Regexes.isMatch(labelsColumn, Regexes.labelRegex))
            return {};

        if (!data.columns.includes(labelsColumn)) {
            errorHandler.error(Errors.ValueNotExists(labelsColumn, "column"));
            return {};
        }

        const labels = data.column(labelsColumn);
        const dfNoLabel = data.drop({ columns: labelsColumn }) as DataFrame;

        const invalidColumns: string[] = dfNoLabel.dtypes.map((type, index) => {
            if (type === "string" || type === "boolean")
                return data.columns[index];
        }).filter((col): col is string => !!col);
    
        if (invalidColumns.length > 0) {
            errorHandler.error(Errors.DataFrameOnlyOneNonNumericalColumn("data", invalidColumns));
            return {};
        }

        const uniqueLabels = labels.unique().values as ArrayType1D;
        if (uniqueLabels.length > 30 && !ignoreDenseLabels) {
            errorHandler.error(Errors.DenseUniqueLabels(labelsColumn));
            return {};
        }

        // Get the values in one column depending on the current unique value from the labels column
        const resultData: number[][][] = [];
        dfNoLabel.columns.forEach(col => {
            const columns: number[][] = [];
            uniqueLabels.forEach(label => {
                const labelDf = data.loc({ rows: data[labelsColumn].eq(label) });
                const column = labelDf.column(col);
                columns.push(column.values as number[]);
            });
            resultData.push(columns);
        });

        const output: ChartOutput = {
            type: "chart",
            outputOrder: properties["outputOrder"] as number,
            chartType: "violin-plot", 
            title: title,
            labels: uniqueLabels, 
            data: resultData,
            dataLabels: dfNoLabel.columns
        };
        return output;
    }
};

