import React from "react";
import { CellObject } from "../db/models";
import MarkdownCell from "./MarkdownCell";
import NodeCell from "./NodeCell";
import Dropdown from "react-overlays/Dropdown";
import { CancelButton, ContextMenuItem, CustomModal, SecondaryButton } from "./CustomComponents";
import store from "../app/store";
import { deleteCellAsync, setIsDraggingCellAsync, updateCellTypeAsync } from "../features/notebooks/notebookSlice";
import InsertCellDropZone from "./InsertCellDropZone";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

type ParentCellProps = {
    cell: CellObject;
}

type ParentCellState = {
    isHover: boolean;
    isOptions: boolean;
    isDelete: boolean;
}

export default class ParentCell extends React.Component<ParentCellProps, ParentCellState> {
    constructor(props: ParentCellProps) {
        super(props);

        this.state = {
            isHover: false,
            isOptions: false,
            isDelete: false
        };

        this.changeCellType = this.changeCellType.bind(this);

        this.showDeleteModal = this.showDeleteModal.bind(this);
        this.hideDeleteModal = this.hideDeleteModal.bind(this);
        this.deleteCell = this.deleteCell.bind(this);

        this.onDragStart = this.onDragStart.bind(this);
    }

    async changeCellType(type: "node" | "markdown"): Promise<void> {
        const { cell } = this.props;

        await store.dispatch(updateCellTypeAsync(cell.cellId, type));
        this.setState({isOptions: false});
    }

    showDeleteModal(): void {
        this.setState({isDelete: true, isOptions: false});
    }

    hideDeleteModal(): void {
        this.setState({isDelete: false});
    }

    async deleteCell(): Promise<void> {
        const { cell } = this.props;

        this.setState({isOptions: false});
        await store.dispatch(deleteCellAsync(cell.cellId));
        this.hideDeleteModal();
    }

    onDragStart(event: React.DragEvent<HTMLDivElement>): void {
        const { cell } = this.props;
        event.stopPropagation();

        const data = { cellId: cell.cellId };
        event.dataTransfer.setData("text/plain", JSON.stringify(data));
    }

    render(): JSX.Element {
        const { cell } = this.props;
        const { isHover, isOptions, isDelete } = this.state;
        return (
            <div className="relative">
                <div className="w-full h-full pl-6 pr-6 py-2 flex flex-row hover:bg-gray-200"
                    onMouseEnter={() => this.setState({isHover: true})}
                    onMouseLeave={() => this.setState({isHover: false, isOptions: false})}>
                    {isHover && (
                        <div className="flex flex-col w-6" style={{ minWidth: "1.5rem" }}>
                            <div className="transition duration-200 ease-in-out flex items-center text-md p-1 mb-2 bg-gray-700 text-gray-200 hover:bg-gray-600 hover:text-gray-100 cursor-move"
                                onDragStart={async (event) => {
                                    this.onDragStart(event);
                                    await store.dispatch(setIsDraggingCellAsync(true));
                                }}
                                onDragEnd={async () => await store.dispatch(setIsDraggingCellAsync(false))}
                                draggable>
                                <FontAwesomeIcon icon="arrows-alt" />
                            </div>
                            <Dropdown show={isOptions}
                                onToggle={next => this.setState({isOptions: next})}>
                                <div className="relative">
                                    <Dropdown.Toggle>
                                        {(props, meta) => (
                                            <SecondaryButton icon="ellipsis-v" className="w-full flex items-center justify-center h-6 text-sm"
                                                onClick={event => meta.toggle(!isOptions, event)} />
                                        )}
                                    </Dropdown.Toggle>
                                    <Dropdown.Menu show={isOptions} flip={true}>
                                        {(props) => (
                                            <>
                                                {isOptions && (
                                                    <div className="absolute z-20 shadow-lg" style={{width: `${cell.type === "node" ? "11rem" : "9rem"}`}}>
                                                        <div onClick={async () => await this.changeCellType(cell.type === "node" ? "markdown" : "node")}>
                                                            <ContextMenuItem text={`Change To ${cell.type === "node" ? "Markdown" : "Node"}`}
                                                                icon="exchange-alt"
                                                                className="bg-gray-600 text-gray-100 hover:bg-gray-800 hover:text-gray-200" />
                                                        </div>
                                                        <div onClick={this.showDeleteModal}>
                                                            <ContextMenuItem text="Delete"
                                                                icon="trash-alt"
                                                                className="bg-red-300 text-red-700 hover:bg-red-800 hover:text-red-200" />
                                                        </div>
                                                    </div>
                                                ) ||
                                                !isOptions && (
                                                    <div className=""></div>
                                                )}
                                            </>
                                        )}
                                    </Dropdown.Menu>
                                </div>
                            </Dropdown>
                        </div>
                    ) ||
                    !isHover && (
                        <div className="w-6 h-full" style={{minWidth: "1.5rem"}}></div>
                    )}
                    {cell.type === "markdown" && (
                        <MarkdownCell key={`${cell.cellId}-${cell.type}`} cell={cell} />
                    ) ||
                    cell.type === "node" && (
                        <NodeCell key={`${cell.cellId}-${cell.type}`} cell={cell} />
                    )}
                    <CustomModal show={isDelete}
                        onHide={() => this.hideDeleteModal()}>
                        <div className="p-4 bg-gray-100 w-64">
                            <div className="mb-4 flex flex-row">
                                <h2 className="mr-1 text-lg font-medium text-red-600">Delete Cell?</h2>
                            </div>
                            <div className="mb-4 w-full flex items-center">
                                <span className="text-center">Are you sure you want to delete this cell? This action is <span className="text-red-600 font-bold">irreversible.</span></span>
                            </div>
                            <div className="flex flex-row-reverse">
                                <CancelButton text="Delete"
                                    className="px-2 py-1 ml-2 text-xs"
                                    onClick={this.deleteCell} />
                                <SecondaryButton text="Cancel"
                                    className="px-2 py-1 text-xs"
                                    onClick={() => this.hideDeleteModal()} />
                            </div>
                        </div>
                    </CustomModal>
                </div>
                <InsertCellDropZone cellId={cell.cellId} />
            </div>
        );
    }
}