const VisibleTypes = {
    "String": "string",
    "Number": "number",
    "Boolean": "boolean",
    "DataFrame": "DataFrame",
    "Series": "Series"
};

const HiddenTypes = {
    "Output": "Output"
};

const InternalTypes = {
    "NoType": "_noType_"
};

const Functions = {
    OR: (...types: string[]): string => {
        if (types.length < 1)
            return InternalTypes.NoType;
        return types.join(" | ");
    },
    ARRAY: (type: string): string => {
        if (type.includes("|"))
            return `(${type})[]`;
        return `${type}[]`;
    }
};

export const Types = {
    ...VisibleTypes,
    ...HiddenTypes,
    ...Functions
};

export type VariableType = keyof typeof VisibleTypes;
export const VariableKeys = Object.keys(VisibleTypes);

export const SourceTypes = {
    "CSV": "csv",
};

export type SourceType = keyof typeof SourceTypes;
export const SourceKeys = Object.keys(SourceTypes);

// This should only be used when the keyType is something like 'Number'
// but the valueType is something like 'number'
export function isTypeEqual(keyType: string, valueType: string): boolean {
    const entries = Object.entries(Types);
    for (let i = 0; i < entries.length; i++) {
        const entry = entries[i];
        if (entry[0] === keyType && entry[1] === valueType)
            return true;
    }

    return false;
}