import { DataFrame, Series } from "danfojs";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler, DataFrameOutput } from "../types";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import MathUtils from "../utils/MathUtils";
import Meta from "../utils/Meta";
import { Types } from "../utils/Types";

const dataFrameOutput: NodeType = {
    id: "dataframe-output",
    name: "DataFrame Output",
    description: Descriptions.NodeSpecific.DataFrameOutput.Description,
    //variableName: "dataFrameOutputResult",
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.NodeSpecific.DataFrameOutput.Input,
            type: Types.OR(Types.DataFrame, Types.Series)
        }
    ],
    properties: [
        {
            id: "outputOrder",
            description: Descriptions.Common.OutputOrderProperty,
            type: "number"
        },
        {
            id: "rowCount",
            description: Descriptions.NodeSpecific.DataFrameOutput.RowCountProperty,
            type: "number",
            defaultValue: 10
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const rowCount = properties["rowCount"] as number;
        const df = input["input"] as DataFrame | Series;
        const head = (df.shape[0] >= rowCount && rowCount > 0) ? df.head(rowCount) : df;
        let values: string[][];
        if (head.columns.length > 1) {
            values = head.values.map(value1 => {
                const val1 = value1 as string[] | (string | number | boolean)[] | number[] | boolean[];
                return val1.map(value2 => {
                    if (typeof value2 === "number")
                        return `${MathUtils.toDecimal(value2, 4)}`;

                    return `${value2}`;
                });
            });
        } else {
            values = head.values.map(value => {
                const val = value as string | number | boolean;
                return [`${val}`];
            });
        }
        
        const output: DataFrameOutput = {
            type: "dataframe",
            outputOrder: properties["outputOrder"] as number,
            columns: head.columns,
            indexes: head.index,
            values
        };
        return output;
    }
};

const OutputNodes = [
    dataFrameOutput
];

export default OutputNodes;