import { CSVUrlSource } from "../data/DataService";
import { UserModel } from "./models";

const titanicSourceId = "ky7ukwew00003f61vd3yuohz";

export async function loadTitanic(url: string) {
    const csv = new CSVUrlSource(titanicSourceId, url);
    const loadResult = await csv.loadToDf();
    const saveResult = loadResult ? await csv.saveToDB() : false;
}

export async function loadTutorialUser(): Promise<UserModel> {
    const response = await fetch("/exported-db.json");
    const jsonStr = await response.text(); 
    const user = JSON.parse(jsonStr) as UserModel;
    return user;
}