import cuid from "cuid";
import { DefinedNodeType, NodeCollection, NodeType, PrimitiveTree } from "../types";
import Meta from "../utils/Meta";

const defaultValues: DefinedNodeType = {
    id: cuid(),
    name: cuid(),
    description: "",
    inputSlots: [],
    outputSlots: [],
    properties: [],
    isDeletable: true,
    isHiddenFromList: false,
    style: {
        width: 200,
        height: -1
    },
    nodeFunction: async input => { return {}; }
};

function assignDefaults(type: Record<string, unknown>, defaults: Record<string, unknown>): Record<string, unknown> {
    const result = {...defaults};
    for (const key of Object.keys(type)) {
        if (result[key] == null || type[key] == null)
            continue;

        if (!Meta.isPrimitive(type[key]) && !Meta.isFunction(type[key])) {
            if (Array.isArray(type[key])) {
                result[key] = type[key];
                continue;
            }

            const innerResult = assignDefaults(type[key] as Record<string, unknown>, result[key] as Record<string, unknown>);
            if (innerResult == undefined)
                continue;

            result[key] = innerResult;
            continue;
        }

        result[key] = type[key];
    }

    return result;
}

const defaultTypeValues: {
    [id: string]: string | number | boolean | string[] | number[] | boolean[]
} = {
    string: "",
    number: 0,
    boolean: false,
    "string[]": [] as string[],
    "number[]": [] as number[],
    "boolean[]": [] as boolean[]
};

class NodeTypeManager {
    private collections: {
        [id: string]: NodeCollection
    } = {};
    private types: {
        [id: string]: DefinedNodeType
    } = {};

    addNodeCollection(collection: NodeCollection): void {
        collection.nodeGroups.forEach(group => {
            group.types.forEach(type => this.addNodeType(group.name === "" ? collection.id : `${collection.id}:${group.name}`, type, collection.defaultValues));
        });
        this.collections[collection.id] = collection;
    }

    addNodeCollections(...collections: NodeCollection[]): void {
        collections.forEach(coll => {
            this.addNodeCollection(coll);
        });
    }

    private addNodeType(collId: string, type: NodeType, defaultVals?: PrimitiveTree): void {
        //console.log(`Adding '${type.id}' type...`);
        const result = assignDefaults(type, defaultValues) as DefinedNodeType;
        if (!result)
            return;
        result.id = `${collId}:${result.id}`;

        // Initialize input slots default value if null
        for (let i = 0; i < result.inputSlots.length; i++) {
            if (result.inputSlots[i].defaultValue != null)
                continue;

            const typeName = result.inputSlots[i].type;
            const customDefaultVal = defaultVals != null ? defaultVals[typeName] : undefined;
            const defaultVal = defaultTypeValues[typeName];
            if (customDefaultVal == null && defaultVal == null)
                continue;

            result.inputSlots[i].defaultValue = customDefaultVal ?? defaultVal;
        }

        // Initialize properties default value if null
        for (let i = 0; i < result.properties.length; i++) {
            if (result.properties[i].defaultValue != null)
                continue;

            const typeName = result.properties[i].type;
            const customDefaultVal = defaultVals != null ? defaultVals[typeName] : undefined;
            const defaultVal = defaultTypeValues[typeName];
            if (customDefaultVal == null && defaultVal == null)
                continue;

            result.properties[i].defaultValue = (customDefaultVal ?? defaultVal) as string | number | boolean;
        }

        this.types[result.id] = result;
    }

    getType(id: string): DefinedNodeType | undefined {
        if (id === "")
            return;

        const type = this.types[id];
        if (!type)
            return;
        
        return type;
    }

    getVisibleTypeIds(): string[] {
        return Object.values(this.types).filter(type => !type.isHiddenFromList).map(type => type.id);
    }
}

export default new NodeTypeManager();