export default class Errors {
    public static MissingNodeProperties = "Somehow, the properties of this node are missing.";
    public static MissingNodeInput = (inputName: string): string => `The input '${inputName}' of this node is missing. Perhaps this input is not connected?`;

    public static EmptyNodePropertyNoEffect = (propertyName: string): string => `The property '${propertyName}' of this node is empty, making this node have no effect.`;
    public static EmptyArray = (name: string, modifier = "value"): string => `The ${modifier} '${name}' is empty and might not have any effect.`;

    public static ValueNotExists = (value: string, modifier = "value"): string => `The ${modifier} '${value}' does not exist.`;
    public static ValueExists = (value: string, modifier = "value"): string => `The ${modifier} '${value}' already exists.`;

    public static CannotBeEmpty = (value: string, modifier = "value"): string => `The ${modifier} '${value}' cannot be empty.`;
    public static NumberCannotBeZero = (value: string, modifier = "value"): string => `The ${modifier} '${value}' cannot be zero.`;
    public static NumberCannotBeNegative = (value: string, modifier = "value"): string => `The ${modifier} '${value}' cannot be negative.`;
    public static NumberCannotBeZeroOrNegative = (value: string, modifier = "value"): string => `The ${modifier} '${value}' cannot be zero or negative.`;

    public static ArrayLengthsNotEqual = (arr1: string, arr2: string): string => `The length of '${arr1}' is not the same as the length of '${arr2}'.`;

    public static ColumnValueWrongType = (column: string, value: string, type: string): string => `The value '${value}' of column '${column}' is of the wrong type, should be '${type}'.`;
    public static DataFrameNullColumnsCannotDescribe = (columns: string[]): string => `Could not describe this DataFrame, it contains null values on these columns: [${columns}]`;
    public static DataFrameNumericalColumnsOnly = (input: string, columns: string[]): string => `The columns [${columns}] of the input '${input}' are not allowed for this operation. Perhaps use the 'Get Numerical Columns' Query node to filter out non-numerical columns?`
    public static DataFrameOnlyOneNonNumericalColumn = (dfName: string, columns: string[]): string => `The dataframe '${dfName}' cannot have more than one non-numerical column. The columns [${columns}] are found to be non-numerical.`;
    public static DataFrameMismatchShape = (df1Name: string, df1Shape: number[], df2Name: string, df2Shape: number[]): string => `The dataframe '${df2Name}' with shape [${df2Shape}] does not match the dataframe '${df1Name}' with shape [${df1Shape}].`;
    public static DataFrameSeriesMismatchShape = (dfName: string, dfSize: number, seriesName: string, seriesSize: number, modifier = "column"): string => `The series '${seriesName}' of length ${seriesSize} does not match the dataframe '${dfName}' with ${dfSize} ${modifier}s. Maybe the 'isRowAxis' property is not correct?`;

    public static DenseUniqueLabels = (columnName: string): string => `The column '${columnName}' may have too many unique labels (> 30). If this is intended, turn on 'ignoreDenseLabels' on the node properties.`;

    public static DoesNotMatchPattern = (name: string, value: string, modifier = "value"): string => `The ${modifier} '${name}' with value '${value}' does not match the pattern.`;

    public static MinCannotBeGreaterThanMax = (pair: string): string => `For the min-max pair '${pair}', the min value cannot be greater than the max value.`;

    public static InvalidStartStopStepConfig = (start: number, stop: number, step: number): string => `The start of '${start}', stop of '${stop}', and step of '${step}' is an invalid configuration.`;
}