import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import Scrollbars from "react-custom-scrollbars-2";
import { connect } from "react-redux";
import store, { RootState } from "../app/store";
import { CellObject, NotebookObject } from "../db/models";
import { clearSelectedNodeTypeAsync, createNewCellAsync, openNotebookAsync } from "../features/notebooks/notebookSlice";
import CodeMirrorManager from "../managers/CodeMirrorManager";
import NodeExecManager from "../managers/NodeExecManager";
import NodeTypeManager from "../managers/NodeTypeManager";
import { CustomModal, OKButton, PrimaryButton } from "./CustomComponents";
import InsertCellDropZone from "./InsertCellDropZone";
import ParentCell from "./ParentCell";

type NotebookProps = {
    id: string;
    notebook: NotebookObject | undefined;
    cells: CellObject[] | undefined;
    selectedNodeTypeHelp: string | undefined;
    isDraggingCell: boolean;
}

type NotebookState = {

}

const mapStateToProps = (state: RootState) => {
    return {
        notebook: state.notebook.currentNotebook,
        cells: state.notebook.currentNotebook?.cells,
        selectedNodeTypeHelp: state.notebook.selectedNodeTypeHelp,
        isDraggingCell: state.notebook.isDraggingCell
    };
};

class NotebookInner extends React.Component<NotebookProps, NotebookState> {
    private scrollbarRef: React.RefObject<Scrollbars>;
    constructor(props: NotebookProps) {
        super(props);

        this.scrollbarRef = React.createRef<Scrollbars>();

        this.state = {
            
        };

        this.updateNotebook = this.updateNotebook.bind(this);

        this.createNodeCell = this.createNodeCell.bind(this);
        this.createMarkdownCell = this.createMarkdownCell.bind(this);

        this.cellsToLinkedList = this.cellsToLinkedList.bind(this);
    }

    async componentDidMount(): Promise<void> {
        CodeMirrorManager.reset();
        await this.updateNotebook();
    }

    async componentDidUpdate(prevProps: NotebookProps, prevState: NotebookState): Promise<void> {
        if (prevProps.id !== this.props.id) {
            await this.updateNotebook();
        }
    }

    async updateNotebook(): Promise<void> {
        const { id } = this.props;
        await store.dispatch(openNotebookAsync(id));
    }

    async createNodeCell(): Promise<void> {
        await store.dispatch(createNewCellAsync("node"));
    }

    async createMarkdownCell(): Promise<void> {
        await store.dispatch(createNewCellAsync("markdown"));
    }

    onHoverScroller(direction: "up" | "down") {
        const scrollbar = this.scrollbarRef.current;
        if (!scrollbar)
            return;

        console.log(direction);
    }

    cellsToLinkedList(cells: CellObject[]): CellObject[] {
        // Recreate the linked list as an array
        const linkedCells: CellObject[] = [];
        let currentKey: string | undefined = "";
        for(let i = 0; i < cells.length; i++) {
            const currentCell = cells.find(cell => cell.nextCell === currentKey);
            if (!currentCell)
                return []; // Some cells are not linked

            linkedCells.push(currentCell);
            currentKey = currentCell.cellId;
        }
        linkedCells.reverse();
        return linkedCells;
    }

    render(): JSX.Element | null {
        const { notebook, cells, selectedNodeTypeHelp, isDraggingCell } = this.props;
        if (!notebook || !cells) {
            return null;
        }
        
        const linkedCells = this.cellsToLinkedList(cells);
        const selectedNodeType = NodeTypeManager.getType(selectedNodeTypeHelp ?? "");
        return (
            <div className="w-full h-full">
                <div className="flex flex-row p-2"
                    style={{height: "5%"}}>
                    <OKButton icon="play"
                        text="Run All"
                        className="text-xs px-2 py-1"
                        onClick={async () => await NodeExecManager.runCells(notebook.notebookId)} />
                </div>
                <div className="relative pb-4"
                    style={{height: "95%"}}>
                    <Scrollbars ref={this.scrollbarRef}>
                        <div className="pt-2">
                            <div className="relative w-full z-50">
                                <InsertCellDropZone cellId="" />
                            </div>
                            {linkedCells.map((cell, index) => (
                                <div key={`cell-${cell.cellId}`} className="w-full">
                                    <ParentCell cell={cell} />
                                </div>
                            ))}
                            <div className="mt-4 flex flex-row justify-center">
                                <PrimaryButton icon="plus" text="Create Node Cell"
                                    className="text-sm px-3 py-2 mr-4"
                                    onClick={this.createNodeCell} />
                                <OKButton icon="plus" text="Create Markdown Cell"
                                    className="text-sm px-3 py-2"
                                    onClick={this.createMarkdownCell} />
                            </div>
                            <div className="w-full" style={{height: "24rem"}}></div>
                        </div>
                    </Scrollbars>
                    {isDraggingCell && (
                        <>
                            <div className="pointer-events-none absolute top-0 left-0 w-12 py-1 flex justify-center text-lg bg-gray-900 text-gray-100 opacity-50">
                                <FontAwesomeIcon icon="caret-up" />
                            </div>
                            <div className="pointer-events-none absolute left-0 w-12 py-1 flex justify-center text-lg bg-gray-900 text-gray-100 opacity-50"
                                style={{bottom: "1rem"}}>
                                <FontAwesomeIcon icon="caret-down" />
                            </div>
                        </>
                    )}
                </div>

                <CustomModal show={selectedNodeType != null}
                    onHide={() => clearSelectedNodeTypeAsync()}>
                    <div className="relative flex flex-col p-8 bg-gray-100"
                        style={{
                            minWidth: "32rem",
                            width: "40%",
                            maxHeight: "80%",
                            height: "80%"
                        }}>
                        <div className="mb-4 flex flex-col">
                            <h2 className="mb-2 text-lg font-bold text-gray-900">{selectedNodeType?.name}</h2>
                            <p className="pl-2 text-sm text-gray-700">{selectedNodeType?.description}</p>
                        </div>
                        <Scrollbars className="mb-4" autoHeight autoHeightMax="100%">
                            {selectedNodeType?.inputSlots && selectedNodeType?.inputSlots.length > 0 && (
                                <div className="mb-4">
                                    <h4 className="mb-2 text-md font-bold text-gray-900">Inputs</h4>
                                    <table className="">
                                        <thead>
                                            <tr>
                                                <th>Name</th>
                                                <th>Type</th>
                                                <th>Description</th>
                                                <th>Default Value</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {selectedNodeType?.inputSlots.map(input => (
                                                <tr key={`input-${input.id}`}>
                                                    <td className="pl-2 text-sm font-bold text-gray-700">{input.id}</td>
                                                    <td className="pl-2 text-sm text-gray-700">{input.type.replaceAll("|", "or")}</td>
                                                    <td className="pl-2 text-sm text-gray-700">{input.description}</td>
                                                    <td className="pl-2 text-sm text-gray-700">{`${input.defaultValue == null ? "" : input.defaultValue}`}</td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            )}
                            {selectedNodeType?.outputSlots && selectedNodeType?.outputSlots.length > 0 && (
                                <div className="mb-4">
                                    <h4 className="mb-2 text-md font-bold text-gray-900">Outputs</h4>
                                    <table className="">
                                        <thead>
                                            <tr>
                                                <th>Name</th>
                                                <th>Type</th>
                                                <th>Description</th>
                                                <th>Default Value</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {selectedNodeType?.outputSlots.map(output => (
                                                <tr key={`output-${output.id}`}>
                                                    <td className="pl-2 text-sm font-bold text-gray-700">{output.id}</td>
                                                    <td className="pl-2 text-sm text-gray-700">{output.type.replaceAll("|", "or")}</td>
                                                    <td className="pl-2 text-sm text-gray-700">{output.description}</td>
                                                    <td className="pl-2 text-sm text-gray-700">{`${output.defaultValue == null ? "" : output.defaultValue}`}</td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            )}
                            {selectedNodeType?.properties && selectedNodeType?.properties.length > 0 && (
                                <div className="mb-4">
                                    <h4 className="mb-2 text-md font-bold text-gray-900">Properties</h4>
                                    <table className="">
                                        <thead>
                                            <tr>
                                                <th>Name</th>
                                                <th>Type</th>
                                                <th>Description</th>
                                                <th>Default Value</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {selectedNodeType?.properties.map(property => {
                                                if (!property.isHidden) {
                                                    return (
                                                        <tr key={`properties-${property.id}`}>
                                                            <td className="pl-2 text-sm font-bold text-gray-700">{property.id}</td>
                                                            <td className="pl-2 text-sm text-gray-700">{property.type.replaceAll("|", "or")}</td>
                                                            <td className="pl-2 text-sm text-gray-700">{property.description}</td>
                                                            <td className="pl-2 text-sm text-gray-700">{`${property.defaultValue == null ? "" : property.defaultValue}`}</td>
                                                        </tr>
                                                    );
                                                }
                                                return null;
                                            })}
                                        </tbody>
                                    </table>
                                </div>
                            )}
                        </Scrollbars>
                        <div className="mt-auto flex flex-row-reverse">
                            <PrimaryButton text="Done" 
                                className="px-2 py-1 text-xs"
                                onClick={async () => await store.dispatch(clearSelectedNodeTypeAsync())} />
                        </div>
                    </div>
                </CustomModal>
            </div>
        );
    }
}

const Notebook = connect(mapStateToProps)(NotebookInner);
export default Notebook;