import NodeExecManager from "../managers/NodeExecManager";
import { NodeType, PrimitiveTree, PrimitiveObject, PrimitiveOutput, NodeCollection } from "../types";
import Descriptions from "../utils/Descriptions";

const primitiveOutputNode: NodeType = {
    id: "primitive-output",
    name: "Primitive Output",
    //variableName: "outputResult",
    description: Descriptions.NodeSpecific.PrimitiveOutput.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.InputValueToOutput,
            type: Types.OR(Types.String, Types.Number, Types.Boolean)
        }
    ],
    properties: [
        {
            id: "outputOrder",
            description: Descriptions.Common.OutputOrderProperty,
            type: "number"
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined): Promise<PrimitiveTree> => {
        if (!input["input"] || !properties)
            return {};

        const inputType = typeof input["input"];
        if (inputType === Types.String || inputType === Types.Number || inputType === Types.Boolean) {
            const output: PrimitiveOutput = {
                type: "primitive",
                outputOrder: properties["outputOrder"] as number,
                output: input["input"] as string | number | boolean
            };
            return output;
        }

        // Return different types to the output visualizer depending on the input
        return {};
    }
};

const wackyOutputNode: NodeType = {
    id: "wacky-primitive-output",
    name: "Wacky Output",
    //variableName: "wackyOutputResult",
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            type: Types.OR(Types.String, Types.Number, Types.Boolean)
        }
    ],
    properties: [
        {
            id: "outputOrder",
            description: Descriptions.Common.OutputOrderProperty,
            type: "number"
        },
        {
            id: "isWackyOutput",
            type: "boolean"
        },
        {
            id: "repetitionAmount",
            type: "number"
        },
        {
            id: "finalOutput",
            type: "string"
        },
        {
            id: "hidden",
            type: "string",
            isHidden: true
        },
        {
            id: "readOnly",
            type: "string",
            isReadOnly: true,
            defaultValue: "someValuesdfgsdfgsdfgsdfg"
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties?: PrimitiveObject): Promise<PrimitiveTree> => {
        if (!input["input"] || !properties)
            return {};

        const inputType = typeof input["input"];
        if (inputType === Types.String || inputType === Types.Number || inputType === Types.Boolean) {
            let outputMsg = `${input["input"] as string | number | boolean}`;

            // Outputs normally if no properties found or flag is not true
            const isWackyOutput = properties["isWackyOutput"] as boolean;
            if (isWackyOutput) {
                const inputStr = outputMsg;
                outputMsg = "";
                for (let i = 0; i < (properties["repetitionAmount"] as number); i++) {
                    outputMsg += `${inputStr}\n`;
                }

                outputMsg += `${(properties["finalOutput"] as string)}`;
            }

            const output: PrimitiveOutput = {
                type: "primitive",
                outputOrder: properties["outputOrder"] as number,
                output: outputMsg
            };

            return output;
        }
        
        // Return different types to the output visualizer depending on the input
        return {};
    }
};

const csvSourceNode: NodeType = {
    id: "csv-source",
    name: "CSV Source",
    //variableName: "csvSourceResult",
    style: {
        width: 120
    },
    outputSlots: [
        {
            id: "output",
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "sourceId",
            type: "string",
            isHidden: true
        }
    ],
    isHiddenFromList: true,
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined): Promise<PrimitiveTree> => {
        if (!properties)
            return {};

        const sourceId = properties["sourceId"] as string;
        const csv = new CSVSource(sourceId);
        const loadResult = await csv.loadToDf();
        if (!loadResult || !csv.df)
            return {};

        return {
            output: csv.df
        };
    }
};

// Getters and Setters

const getStringVariableNode: NodeType = {
    id: "string-variable-getter",
    name: "Get String",
    //variableName: "getStringVariableResult",
    style: {
        width: 120
    },
    outputSlots: [
        {
            id: "output",
            type: Types.String
        }
    ],
    properties: [
        {
            id: "variableId",
            type: "string",
            isHidden: true
        }
    ],
    isHiddenFromList: true,
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined): Promise<PrimitiveTree> => {
        if (!properties)
            return {};

        const variableId = properties["variableId"] as string;
        const value = NodeExecManager.getVariable(variableId);
        if (value == null)
            return {};
        
        return {
            output: value as string
        };
    }
};

const setStringVariableNode: NodeType = {
    id: "string-variable-setter",
    name: "Set String",
    //variableName: "setStringVariableResult",
    style: {
        width: 120
    },
    inputSlots: [
        {
            id: "input",
            type: Types.String
        }
    ],
    properties: [
        {
            id: "variableId",
            type: "string",
            isHidden: true
        }
    ],
    isHiddenFromList: true,
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined): Promise<PrimitiveTree> => {
        if (!properties)
            return {
                result: false
            };

        const inputVal = input["input"] as string;
        const variableId = properties["variableId"] as string;
        NodeExecManager.setVariable(variableId, inputVal);

        return {
            result: true
        };
    }
};

