import React, {  } from "react";
import { CellObject } from "../db/models";
import { Stage, Layer } from "react-konva";
import Konva from "konva";
import Node from "./Node";
import { KonvaEventObject } from "konva/lib/Node";
import { Vector2d } from "konva/lib/types";
import GridArray from "./GridArray";
import NodeConnection from "./NodeConnection";
import { KonvaForm } from "./KonvaForm";
import store from "../app/store";
import { clearSelectedNodeAsync, deleteNodeAsync, getNodes, getSelectedNode } from "../features/notebooks/notebookSlice";
import { NodeCellControls, NodeControls } from "./NodeCellControls";
import { Bar, Chart, Line } from "react-chartjs-2";
import ResizeObserver from "rc-resize-observer";
import NodeCellDropZone from "./NodeCellDropZone";
import Scrollbars from "react-custom-scrollbars-2";
import { twMerge } from "tailwind-merge";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Colors from "../utils/Colors";
import NodeExecManager from "../managers/NodeExecManager";
import NodeConnectionManager from "../managers/NodeConnectionManager";
import { PrimitiveTree, NodeError, PrimitiveOutput, ChartOutput, DataFrameOutput } from "../types";

type NodeCellProps = {
    cell: CellObject;
}

type NodeCellState = {
    stagePos: Vector2d;
    results: PrimitiveTree[];
}

export default class NodeCell extends React.Component<NodeCellProps, NodeCellState> {
    private stage: React.RefObject<Konva.Stage>;
    private gridArray: React.RefObject<GridArray>;
    private outputBox: React.RefObject<HTMLDivElement>;
    private readonly minScale = 0.25;
    private readonly maxScale = 2;

    constructor(props: NodeCellProps) {
        super(props);

        this.stage = React.createRef<Konva.Stage>();
        this.gridArray = React.createRef<GridArray>();
        this.outputBox = React.createRef<HTMLDivElement>();

        this.state = {
            stagePos: { x: 0, y: 0 },
            results: []
        };

        this.fitToParent = this.fitToParent.bind(this);

        // Zoom
        this.zoomInView = this.zoomInView.bind(this);
        this.zoomOutView = this.zoomOutView.bind(this);
        this.resetView = this.resetView.bind(this);
        this.onZoom = this.onZoom.bind(this);
        this.onDeleteNode = this.onDeleteNode.bind(this);

        this.runCell = this.runCell.bind(this);
    }

    componentDidMount(): void {
        const { cell } = this.props;
        NodeExecManager.addCellExec(cell.cellId, this.runCell);
    }

    fitToParent(width: number, height: number): void {
        const stage = this.stage.current;
        if (!stage) {
            console.error("Container or stage could not be found");
            return;
        }
        
        const stageObj = stage.getStage();
        stageObj.width(width - 1);
        stageObj.height(height);

        const outputBox = this.outputBox.current;
        if (outputBox) {
            outputBox.style.width = `${width - 1}px`;
        }

        this.gridArray.current?.setContainerWidth(width);
        stageObj.draw();
    }

    zoomInView(): void {
        KonvaForm.cancel();
        const stage = this.stage.current?.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }

        const newScale = stage.scaleX() * 1.2;
        if (newScale < this.minScale || newScale > this.maxScale)
            return;

        stage.scaleX(newScale);
        stage.scaleY(newScale);
        
        this.gridArray.current?.setScale(newScale);
        stage.batchDraw();
    }

    zoomOutView(): void {
        KonvaForm.cancel();
        const stage = this.stage.current?.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }

        const newScale = stage.scaleX() / 1.2;
        if (newScale < this.minScale || newScale > this.maxScale)
            return;
            
        stage.scaleX(newScale);
        stage.scaleY(newScale);
        
        this.gridArray.current?.setScale(newScale);
        stage.batchDraw();
    }

    resetView(): void {
        KonvaForm.cancel();
        const stage = this.stage.current?.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }

        stage.scaleX(1);
        stage.scaleY(1);
        
        this.gridArray.current?.setScale(1);
        stage.position({ x: 0, y: 0 });
        stage.batchDraw();
    }

    onZoom(event: KonvaEventObject<WheelEvent>): void {
        event.evt.preventDefault();
        const stage = event.target.getStage();
        if (!stage) {
            console.error("The Konva Stage could not be found.");
            return;
        }
            
        const oldScale: number = stage.scaleX();
        const scaleBy = 1.1;
        const pointer = stage.getPointerPosition();
        if (!pointer) {
            console.error("The Konva pointer position could not be found.");
            return;
        }

        const mousePosition = {
            x: (pointer.x - stage.x()) / oldScale,
            y: (pointer.y - stage.y()) / oldScale
        };

        const newScale = event.evt.deltaY < 0 ? oldScale * scaleBy : oldScale / scaleBy;
        if (newScale < this.minScale || newScale > this.maxScale)
            return;

        stage.scale({ x: newScale, y: newScale});

        const newPos = {
            x: pointer.x - mousePosition.x * newScale,
            y: pointer.y - mousePosition.y * newScale
        };

        this.gridArray.current?.setScale(newScale);
        stage.position(newPos);
        stage.batchDraw();
    }

    onDeleteNode(event: React.KeyboardEvent<HTMLDivElement>): void {
        if (event.key === "Delete") {
            const selectedNode = getSelectedNode(store.getState());
            if (!selectedNode)
                return;

            store.dispatch(clearSelectedNodeAsync()).then(() => {
                return store.dispatch(deleteNodeAsync(selectedNode.cellId, selectedNode.nodeId));
            });
        }
    }

    async runCell(): Promise<void> {
        const { cell } = this.props;
        NodeExecManager.resetCellOutputs(cell.cellId);

        const nodes = getNodes(store.getState(), cell.cellId);

        // Run all the setter variable nodes in sequence
        const setters = nodes.filter(node => node.type.endsWith("-setter"));
        for (const setter of setters) {
            const output = await NodeExecManager.runFunction(cell.cellId, setter.nodeId);
            const result = output["result"] as boolean;
            if (!result) {
                console.error(`A setter variable node of id '${setter.nodeId}' has failed`);
                return;
            }
        }

        // Run the final output node
        const outputNodes = nodes.filter(node => node.type.endsWith("-output"));
        if (outputNodes.length < 0)
            return;

        const nodeIds = outputNodes.sort((a, b) => {
            const aOrderIndex = a.properties.findIndex(prop => prop.id === "outputOrder");
            const bOrderIndex = b.properties.findIndex(prop => prop.id === "outputOrder");

            const aOrder = aOrderIndex >= 0 ? a.properties[aOrderIndex].value as number : 0;
            const bOrder = bOrderIndex >= 0 ? b.properties[bOrderIndex].value as number : 0;
            return aOrder - bOrder;
        }).map(node => node.nodeId);
        const results = await NodeExecManager.runFunctions(cell.cellId, nodeIds, true);
        this.setState({results});
    }

    render(): JSX.Element {
        const { cell } = this.props;
        const { stagePos, results } = this.state;
        const errors = NodeExecManager.getErrors(cell.cellId);
        
        return (
            <div className="flex flex-col w-full">
                <div className="flex flex-row w-full bg-gray-100 shadow-inner" style={{minHeight: "24rem"}}>
                    <ResizeObserver onResize={props => {
                        this.fitToParent(props.offsetWidth, props.offsetHeight);
                    }}>
                        <div className="relative w-full h-full outline-none"
                            onKeyDown={this.onDeleteNode}
                            tabIndex={0}>
                            <Stage ref={this.stage}
                                onWheel={(event: KonvaEventObject<WheelEvent>) => {
                                    KonvaForm.cancel();
                                    this.onZoom(event);
                                }}
                                onDragStart={(event: KonvaEventObject<DragEvent>) => {
                                    KonvaForm.cancel();
                                }}
                                onDragMove={(event: KonvaEventObject<DragEvent>) => {
                                    event.target.preventDefault();
                                    const pos = event.currentTarget.position();
                                    this.gridArray.current?.setPos(pos.x, pos.y);
                                }}
                                onDragEnd={(event: KonvaEventObject<DragEvent>) => {
                                    event.target.preventDefault();
                                    const pos = event.currentTarget.position();
                                    const scale = event.currentTarget.scale();
                                    this.setState({stagePos: { x: -pos.x / scale.x, y: -pos.y / scale.y }});
                                }}
                                onClick={async (event: KonvaEventObject<MouseEvent>) => {
                                    KonvaForm.cancel();
                                    await store.dispatch(clearSelectedNodeAsync());
                                }}
                                draggable>
                                <Layer listening={false}>
                                    <GridArray ref={this.gridArray} />
                                </Layer>
                                <Layer id="edgesLayer" ref={ref => {
                                    if (ref)
                                        NodeConnectionManager.setLayerRef(ref);
                                }}>
                                    {cell.connections.map((conn, index) => (
                                        <NodeConnection key={conn.connectionId}
                                            cellId={cell.cellId}
                                            connection={conn}
                                            ref={ref => {
                                                if (ref)
                                                    NodeConnectionManager.addRef(cell.cellId, conn.connectionId, ref);
                                            }} />
                                    ))}
                                </Layer>
                                <Layer>
                                    {cell.nodes.map((value, index) => (
                                        <Node key={index} cellId={cell.cellId} node={value} />
                                    ))}
                                </Layer>
                                <Layer name="movingNodes"></Layer>
                            </Stage>
                            <NodeCellControls onZoomIn={this.zoomInView} onZoomOut={this.zoomOutView} onReset={this.resetView} />
                            <NodeControls cellId={cell.cellId} stagePos={stagePos} onRunCell={this.runCell}  />
                            <NodeCellDropZone cellId={cell.cellId} stagePos={stagePos} />
                        </div>
                    </ResizeObserver>
                </div>
                <div ref={this.outputBox} className="">
                    {results.length > 0 && (
                        <div className="flex flex-col mt-2 p-2 bg-gray-200 text-gray-900">
                            <span className="mb-2 text-xs font-medium text-gray-600">Output</span>
                            {errors.map((error, index) => (
                                <div key={`${cell.cellId}-error-${index}`} className="mb-2">
                                    <ErrorItem error={error} />
                                </div>
                            ))}
                            {results.map((result, index) => (
                                <div key={`${cell.cellId}-result-${index}`} className="mb-4">
                                    <ShowResult result={result} />
                                </div>
                            ))}
                        </div>
                    )}
                </div>
            </div>
        );
    }
}

type ErrorItemProps = {
    error: NodeError;
}

function ErrorItem(props: ErrorItemProps): JSX.Element {
    const { error } = props;
    const debugStyle = "bg-indigo-300 text-indigo-700";
    const infoStyle = "bg-gray-300 text-gray-700";
    const warningStyle = "bg-yellow-300 text-yellow-700";
    const errorStyle = "bg-red-300 text-red-700";
    
    let currentStyle = infoStyle;
    if (error.type === "debug")
        currentStyle = debugStyle;
    else if (error.type === "warning")
        currentStyle = warningStyle;
    else if (error.type === "error")
        currentStyle = errorStyle;

    const split = error.message.split(": ");
    const source = split[0];
    const message = split.length > 2 ? split.slice(1).join(": ") : split[1];
    return (
        <div className={twMerge("px-3 py-2 text-sm", currentStyle)}>
            {error.type === "debug" && (
                <FontAwesomeIcon className="mr-1" icon="bug" />
            ) ||
            error.type === "info" && (
                <FontAwesomeIcon className="mr-1" icon="info-circle" />
            ) ||
            error.type === "warning" && (
                <FontAwesomeIcon className="mr-1" icon="exclamation-triangle" />
            ) ||
            error.type === "error" && (
                <FontAwesomeIcon className="mr-1" icon="times-circle" />
            )}
            <span className="font-bold mr-2">{source}:</span>
            <span className="">{message}</span>
        </div>
    );
}

type ShowResultProps = {
    result: PrimitiveTree | undefined
}

export function ShowResult(props: ShowResultProps): JSX.Element | null {
    const { result } = props;
    if (!result)
        return null;

    const resultType = result["type"];
    if (resultType === "primitive") {
        const typedResult = result as PrimitiveOutput;
        return (
            <span className="pl-3 text-blue-500">{`${typedResult.output}`}</span>
        );
    } else if (resultType === "chart") {
        const typedResult = result as ChartOutput;
        const colors = [
            Colors.Red[500],
            Colors.Blue[500],
            Colors.Orange[500],
            Colors.Indigo[500],
            Colors.Yellow[500],
            Colors.Purple[500],
            Colors.Green[500],
            Colors.Pink[500],
            Colors.Teal[500]
        ];

        return (
            <>
                <div style={{maxWidth: "40rem"}}>
                    {typedResult.chartType.endsWith("-line") && (
                        <Line data={{
                            labels: typedResult.labels,
                            datasets: typedResult.data.map((arr, index) => {
                                return {
                                    label: typedResult.dataLabels[index],
                                    data: arr as number[],
                                    fill: typedResult.chartType === "stacked-line",
                                    backgroundColor: Colors.rgbaToStr(Colors.hexToRGBA(colors[index % colors.length], 0.4)),
                                    borderColor: colors[index % colors.length],
                                    borderWidth: 1
                                };
                            })
                        }} 
                        options={{
                            plugins: {
                                title: {
                                    display: typedResult.title !== "",
                                    text: typedResult.title
                                }
                            },
                            scales: {
                                x: {
                                    ticks: {
                                        precision: 2
                                    }
                                },
                                y: {
                                    beginAtZero: true,
                                    stacked: typedResult.chartType === "stacked-line",
                                    ticks: {
                                        precision: 2
                                    }
                                }
                            },
                            elements: {
                                line: {
                                    tension: typedResult.options?.isSmooth ? 0.3 : 0
                                }
                            }
                        }} />
                    ) ||
                    typedResult.chartType.endsWith("-bar") && (
                        <Bar data={{
                            labels: typedResult.labels,
                            datasets: typedResult.data.map((arr, index) => {
                                return {
                                    label: typedResult.dataLabels[index],
                                    data: arr as number[],
                                    backgroundColor: Colors.rgbaToStr(Colors.hexToRGBA(colors[index % colors.length], 0.4)),
                                    borderColor: colors[index % colors.length],
                                    borderWidth: 1,
                                    borderRadius: 4,
                                    stack: "Stack 0"
                                };
                            })
                        }} 
                        options={{
                            indexAxis: typedResult.chartType === "horizontal-bar" ? "y" : "x",
                            plugins: {
                                title: {
                                    display: typedResult.title !== "",
                                    text: typedResult.title
                                }
                            },
                            scales: {
                                y: {
                                    beginAtZero: true,
                                    stacked: typedResult.chartType === "stacked-bar"
                                }
                            }
                        }} />
                    ) || 
                    typedResult.chartType.endsWith("-plot") && (
                        <Chart type={typedResult.chartType === "violin-plot" ? "violin" : "boxplot"}
                            data={{
                                labels: typedResult.labels,
                                datasets: typedResult.data.map((arr, index) => {
                                    return {
                                        label: typedResult.dataLabels[index],
                                        data: arr as number[][],
                                        backgroundColor: Colors.rgbaToStr(Colors.hexToRGBA(colors[index % colors.length], 0.4)),
                                        borderColor: colors[index % colors.length],
                                        borderWidth: 1,
                                        borderRadius: 4,
                                        itemRadius: 2,
                                        outlierStyle: "triangle"
                                    };
                                })
                            }}
                            options={{
                                plugins: {
                                    title: {
                                        display: typedResult.title !== "",
                                        text: typedResult.title
                                    }
                                },
                                scales: {
                                    y: {
                                        beginAtZero: true
                                    }
                                }
                            }}
                        />
                    )}
                </div>
                {/*
                <span className="pl-3">{`Chart Type: ${typedResult.chartType}, x: '${typedResult.x}', y: '${typedResult.y}'`}</span>
                */}
            </>
        );
    } else if (resultType === "dataframe") {
        const typedResult = result as DataFrameOutput;
        const columns = typedResult.columns;
        const indexes = typedResult.indexes;
        const values = typedResult.values;
        return (
            <>
                <Scrollbars autoHeight autoHeightMax={"32rem"}>
                    <div className="pl-3">
                        <table>
                            <thead>
                                <tr>
                                    {indexes.length > 0 && (
                                        <th></th>
                                    )}
                                    {columns.map((col, index) => (
                                        <th key={`th-${index}-${col}`}>{col}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {values.map((row, index) =>(
                                    <tr key={`tr-${index}`}>
                                        <>
                                            {indexes.length > 0 && (
                                                <td>{indexes[index]}</td>
                                            )}
                                            {row.map((val, index2) => (
                                                <React.Fragment key={`td-${index}-${index2}`}>
                                                    {(val === "" || val === "null") && (
                                                        <td className="text-red-500">
                                                            null
                                                        </td>
                                                    ) || 
                                                    val !== "" && (
                                                        <td>{val}</td>
                                                    )}
                                                </React.Fragment>
                                            ))}
                                        </>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </Scrollbars>
            </>
        );
    }

    return null;
}