import { Primitives, Vector2 } from "../types";
import AceDB from "./AceDB";
import { CellModel, DataSourceModel, NodeConnectionModel, NodeModel, NotebookModel, UserModel, VariableModel } from "./models";

export enum DBType {
    Test,
    Offline,
    Online
}

export interface DBInterface {
    // Users
    createNewUser(name: string): Promise<UserModel>;
    removeUser(userId: string): Promise<void>;
    updateUserName(userId: string, name: string): Promise<void>;
    getAllUsers(): Promise<UserModel[]>;
    getUserDetail(userId: string): Promise<UserModel>;

    // Notebooks
    createNewNotebook(userId: string, name: string): Promise<NotebookModel>;
    addNotebook(userId: string, notebook: NotebookModel): Promise<void>;
    removeNotebook(userId: string, notebookId: string): Promise<void>;
    updateNotebookName(userId: string, notebookId: string, name: string): Promise<void>;
    getNotebook(userId: string, notebookId: string): Promise<NotebookModel>;
    getNotebooksByUser(userId: string): Promise<NotebookModel[]>;
    getNotebookDetailsByUser(userId: string): Promise<NotebookModel[]>;

    // Data Sources
    createNewDataSource(userId: string, notebookId: string, name: string, type: string): Promise<DataSourceModel>;
    updateDataSourceName(userId: string, notebookId: string, sourceId: string, name: string): Promise<void>;
    removeDataSource(userId: string, notebookId: string, sourceId: string): Promise<void>;
    getDataSource(userId: string, notebookId: string, sourceId: string): Promise<DataSourceModel>;
    getDataSources(userId: string, notebookId: string): Promise<DataSourceModel[]>;

    // Data
    addData(sourceId: string, data: Uint8Array): Promise<void>;
    removeData(sourceId: string): Promise<void>;
    getData(sourceId: string): Promise<Uint8Array | undefined>;
    isDataExists(sourceId: string): Promise<boolean>;

    // Variables
    createNewVariable(userId: string, notebookId: string, name: string, type: string): Promise<VariableModel>;
    updateVariableName(userId: string, notebookId: string, variableId: string, name: string): Promise<void>;
    updateVariableValue(userId: string, notebookId: string, variableId: string, value: string | number | boolean): Promise<void>;
    removeVariable(userId: string, notebookId: string, variableId: string): Promise<void>;
    getVariable(userId: string, notebookId: string, variableId: string): Promise<VariableModel>;
    getVariables(userId: string, notebookId: string): Promise<VariableModel[]>;

    // Generic Cell
    createNewCell(userId: string, notebookId: string, type: "markdown" | "node"): Promise<CellModel | undefined>;
    insertCell(userId: string, notebookId: string, cellId: string, insertNextToCellId: string): Promise<void>;
    insertCellWithIndex(userId: string, notebookId: string, cellId: string, insertNextTo: number): Promise<void>;
    insertCellToTop(userId: string, notebookId: string, cellId: string): Promise<void>;
    removeCell(userId: string, notebookId: string, cellId: string): Promise<void>;
    updateCellType(userId: string, notebookId: string, cellId: string, type: "markdown" | "node"): Promise<void>;
    getCellsByNotebook(userId: string, notebookId: string): Promise<CellModel[]>;

    // Markdown Cell
    updateMarkdownCell(userId: string, notebookId: string, cellId: string, value: string): Promise<void>;

    // Node Cell nodes
    createNewNode(userId: string, notebookId: string, cellId: string, name: string, type: string, coords?: Vector2): Promise<NodeModel>;
    addNode(userId: string, notebookId: string, cellId: string, node: NodeModel): Promise<void>;
    removeNode(userId: string, notebookId: string, cellId: string, nodeId: string): Promise<void>;
    updateNodeName(userId: string, notebookId: string, cellId: string, nodeId: string, name: string): Promise<void>;
    updateNodeCoords(userId: string, notebookId: string, cellId: string, nodeId: string, coords: Vector2): Promise<void>;
    setNodeInputValue(userId: string, notebookId: string, cellId: string, nodeId: string, slotId: string, value: Primitives): Promise<void>;
    setNodeProperty(userId: string, notebookId: string, cellId: string, nodeId: string, propertyId: string, value: string | number | boolean): Promise<void>;

    // Node Cell connections
    createNewNodeConnection(userId: string, notebookId: string, cellId: string, fromNodeId: string, fromSlotId: string, toNodeId: string, toSlotId: string): Promise<NodeConnectionModel>;
    removeNodeConnection(userId: string, notebookId: string, cellId: string, fromNodeId: string, fromSlotId: string, toNodeId: string, toSlotId: string): Promise<void>;
    removeNodeConnectionWithId(userId: string, notebookId: string, cellId: string, connectionId: string): Promise<void>;

    setupSchema(): Promise<void>;
    seedDatabase(): Promise<void>;
    exportDatabase(): Promise<string>;
    onReady(fn: () => Promise<void>): void;
}

class DBService {
    private _db: DBInterface | undefined;
    private _isRunning = false;

    public async start(dbType: DBType): Promise<void> {
        if (this._isRunning)
            return;

        this._db = new AceDB(dbType);
        if (dbType === DBType.Test)
            await this._db.setupSchema();

        this._isRunning = true;
    }

    public get isRunning() {
        return this._isRunning;
    }

    public get db() {
        return this._db;
    }
}

export default new DBService();