import { DataFrame, Series } from "danfojs";
import Errors from "../utils/Errors";
import Regexes from "../utils/Regexes";
import { Types } from "../utils/Types";
import * as random from "random-seed";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler } from "../types";
import Meta from "../utils/Meta";
import Descriptions from "../utils/Descriptions";

const randomLabelsColumn: NodeType = {
    id: "random-labels-column",
    name: "Random Labels",
    description: Descriptions.NodeSpecific.RandomLabels.Description,
    style: {
        width: 130
    },
    inputSlots: [
        {
            id: "seed",
            description: Descriptions.Common.DataFrameDataGen.SeedInput,
            type: Types.String,
            defaultValue: "SomeSeed"
        }
    ],
    outputSlots: [
        {
            id: "labels",
            description: Descriptions.NodeSpecific.RandomLabels.LabelsOutput,
            type: Types.Series
        }
    ],
    properties: [
        {
            id: "columnName",
            description: Descriptions.Common.DataFrameDataGen.ColumnNameProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelRegex, Regexes.emptyRegex)
        },
        {
            id: "rowCount",
            description: Descriptions.Common.DataFrame.RowCountProperty,
            type: "number",
            defaultValue: 1
        },
        {
            id: "labels",
            description: Descriptions.NodeSpecific.RandomLabels.LabelsProperty,
            type: "string",
            regex: Regexes.labelCommaRegex
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["seed"])) {
            errorHandler.error(Errors.MissingNodeInput("seed"));
            return {};
        }

        const seed = input["seed"] as string;
        const columnName = properties["columnName"] as string;
        const rowCount = properties["rowCount"] as number;
        const labelPattern = properties["labels"] as string;

        if (rowCount <= 0)
            errorHandler.error(Errors.NumberCannotBeZeroOrNegative("rowCount", "property"));

        if (!Regexes.isMatch(labelPattern, Regexes.labelCommaRegex))
            errorHandler.error(Errors.CannotBeEmpty("labels", "property"));

        if (rowCount <= 0 || !Regexes.isMatch(labelPattern, Regexes.labelCommaRegex))
            return {};

        const rand = random.create(seed !== "" ? seed : undefined);
            
        let colName = columnName;
        if (!Regexes.isMatch(columnName, Regexes.labelRegex))
            colName = "Labels";

        const labelNames = Regexes.processCommaDelimString(labelPattern, Regexes.baseLabelRegex);
        const labels: string[] = [];
        for (let i = 0; i < rowCount; i++) {
            const randIndex = rand(labelNames.length);
            labels.push(labelNames[randIndex]);
        }

        return {
            labels: new Series(labels, { columns: [colName] })
        };
    }
};

const randomData: NodeType = {
    id: "random-data-dataframe",
    name: "Random Data",
    description: Descriptions.NodeSpecific.RandomData.Description,
    style: {
        width: 120
    },
    inputSlots: [
        {
            id: "seed",
            description: Descriptions.Common.DataFrameDataGen.SeedInput,
            type: Types.String,
            defaultValue: "SomeSeed"
        }
    ],
    outputSlots: [
        {
            id: "data",
            description: Descriptions.NodeSpecific.RandomData.DataOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "columnNames",
            description: Descriptions.NodeSpecific.RandomData.ColumnNamesProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelCommaRegex, Regexes.emptyRegex)
        },
        {
            id: "rowCount",
            description: Descriptions.Common.DataFrame.RowCountProperty,
            type: "number",
            defaultValue: 1
        },
        {
            id: "minMax",
            description: Descriptions.NodeSpecific.RandomData.MinMaxProperty,
            type: "string",
            regex: Regexes.combine(Regexes.numberMapRegex, Regexes.emptyRegex)
        },
        {
            id: "isIntegers",
            description: Descriptions.NodeSpecific.RandomData.IsIntegersProperty,
            type: "boolean"
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["seed"])) {
            errorHandler.error(Errors.MissingNodeInput("seed"));
            return {};
        }

        const seed = input["seed"] as string;
        const columnNamesPattern = properties["columnNames"] as string;
        const rowCount = properties["rowCount"] as number;
        const minMaxPattern = properties["minMax"] as string;
        const isIntegers = properties["isIntegers"] as boolean;

        if (rowCount <= 0) {
            errorHandler.error(Errors.NumberCannotBeZeroOrNegative("rowCount", "property"));
            return {};
        }

        const rand = random.create(seed !== "" ? seed : undefined);

        let columnNames: string[] = [];
        let minMax: string[] = [];
        if (columnNamesPattern === "" && minMaxPattern === "") {
            columnNames = ["Data"];
            minMax = ["0:1"];
        } else if (columnNamesPattern === "" && minMaxPattern !== "") {
            if (!Regexes.isMatch(minMaxPattern, Regexes.numberMapRegex)) {
                errorHandler.error(Errors.DoesNotMatchPattern("minMax", minMaxPattern, "property"));
                return {};
            }

            minMax = Regexes.processCommaDelimString(minMaxPattern, Regexes.baseNumberMapRegex);
            minMax.forEach((val, index) => {
                columnNames.push(`Data_${index + 1}`);
            });
        } else if (columnNamesPattern !== "" && minMaxPattern === "") {
            if (!Regexes.isMatch(columnNamesPattern, Regexes.labelCommaRegex)) {
                errorHandler.error(Errors.DoesNotMatchPattern("columnNames", columnNamesPattern, "property"));
                return {};
            }

            columnNames = Regexes.processCommaDelimString(columnNamesPattern, Regexes.baseLabelRegex);
            columnNames.forEach(() => {
                minMax.push("0:1");
            });
        } else {
            let hasError = false;
            if (!Regexes.isMatch(minMaxPattern, Regexes.numberMapRegex)) {
                errorHandler.error(Errors.DoesNotMatchPattern("minMax", minMaxPattern, "property"));
                hasError = true;
            }

            if (!Regexes.isMatch(columnNamesPattern, Regexes.labelCommaRegex)) {
                errorHandler.error(Errors.DoesNotMatchPattern("columnNames", columnNamesPattern, "property"));
                hasError = true;
            }

            if (hasError)
                return {};

            minMax = Regexes.processCommaDelimString(minMaxPattern, Regexes.baseNumberMapRegex);
            columnNames = Regexes.processCommaDelimString(columnNamesPattern, Regexes.baseLabelRegex);
        }

        if (minMax.length !== columnNames.length) {
            errorHandler.error(Errors.ArrayLengthsNotEqual("columnNames", "minMax"));
            return {};
        }

        const dataObj: {
            [column: string]: number[];
        } = {};

        for (let i = 0; i < columnNames.length; i++) {
            const minMaxSplit = minMax[i].split(":").map(val => Number(val));
            const min = minMaxSplit[0];
            const max = minMaxSplit[1];
            if (min > max) {
                errorHandler.error(Errors.MinCannotBeGreaterThanMax(minMax[i]));
                return {};
            }

            const column = columnNames[i];
            if (!dataObj[column])
                dataObj[column] = [];

            for (let j = 0; j < rowCount; j++) {
                const baseRandomVal = (rand.random() * 0.5) + (rand.random() * 0.5);
                let randomVal = baseRandomVal * (max - min) + min;
                if (isIntegers)
                    randomVal = Math.round(randomVal);
                    
                dataObj[column].push(randomVal);
            }
        }

        return {
            data: new DataFrame(dataObj)
        };
    }
};

const arangeNode: NodeType = {
    id: "arange-dataframe",
    name: "Array of Integers",
    description: Descriptions.NodeSpecific.ARange.Description,
    style: {
        width: 120
    },
    outputSlots: [
        {
            id: "arange",
            description: Descriptions.NodeSpecific.ARange.ARangeOutput,
            type: Types.Series
        }
    ],
    properties: [
        {
            id: "columnName",
            description: Descriptions.Common.DataFrameDataGen.ColumnNameProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelRegex, Regexes.emptyRegex)
        },
        {
            id: "start",
            description: Descriptions.NodeSpecific.ARange.StartProperty,
            type: "number",
            defaultValue: 0
        },
        {
            id: "stop",
            description: Descriptions.NodeSpecific.ARange.StopProperty,
            type: "number",
            defaultValue: 10
        },
        {
            id: "step",
            description: Descriptions.NodeSpecific.ARange.StepProperty,
            type: "number",
            defaultValue: 1
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        const columnName = properties["columnName"] as string;
        const start = properties["start"] as number;
        const stop = properties["stop"] as number;
        const step = properties["step"] as number;

        if (step === 0) {
            errorHandler.error(Errors.NumberCannotBeZero("step", "property"));
            return {};
        }

        let hasError = false;
        if (start < stop) {
            if (step < 0) {
                errorHandler.error(Errors.InvalidStartStopStepConfig(start, stop, step));
                hasError = true;
            }
        } else {
            if (step > 0) {
                errorHandler.error(Errors.InvalidStartStopStepConfig(start, stop, step));
                hasError = true;
            }
        }

        if (hasError)
            return {};

        let colName = columnName;
        if (!Regexes.isMatch(columnName, Regexes.labelRegex))
            colName = "Array";

        const arr: number[] = [];
        for (let i = start; (start < stop) ? i < stop : i > stop; i += step)
            arr.push(i);

        return {
            arange: new Series(arr, { columns: [colName] })
        };
    }
};

const DataGenNodes = [
    randomLabelsColumn,
    randomData,
    arangeNode
];

export default DataGenNodes;