import { DataFrame, Series } from "danfojs";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler, NodeCollection } from "../types";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import Meta from "../utils/Meta";
import Regexes from "../utils/Regexes";
import { Types } from "../utils/Types";

const dataFrameToSeries: NodeType = {
    id: "dataframe-to-series",
    name: "DataFrame To Series",
    description: Descriptions.NodeSpecific.DataFrameToSeries.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.Series
        }
    ],
    properties: [
        {
            id: "column",
            description: Descriptions.NodeSpecific.DataFrameToSeries.ColumnProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelRegex, Regexes.emptyRegex)
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const df = input["input"] as DataFrame;
        const column = properties["column"] as string;

        if (!df.columns.includes(column)) {
            errorHandler.error(Errors.ValueNotExists(column, "column"));
            return {
                output: df
            };
        }

        const newDf = df.column(column) as Series;
        return {
            output: newDf
        };
    }
};

const seriesToDataFrame: NodeType = {
    id: "series-to-dataframe",
    name: "Series To DataFrame",
    description: Descriptions.NodeSpecific.SeriesToDataFrame.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.Series
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const series = input["input"] as Series;
        const newDf = new DataFrame({ [series.columns[0]]: series.values });
        return {
            output: newDf
        };
    }
};

const ConverterNodes: NodeCollection = {
    id: "converter",
    name: "Converter Nodes Collection",
    description: "",
    nodeGroups: [
        {
            name: "",
            types: [
                dataFrameToSeries,
                seriesToDataFrame
            ]
        }
    ]
};
export default ConverterNodes;