import React from "react";
import ReactMarkdown from "react-markdown";
import rehypeHighlight from "rehype-highlight";
import remarkGfm from "remark-gfm";
import { markdown } from "@codemirror/lang-markdown";
import CodeMirror, { ReactCodeMirrorRef } from "@uiw/react-codemirror";
import { basicSetup } from "@codemirror/basic-setup";
import { oneDarkHighlightStyle } from "@codemirror/theme-one-dark";
import { CellObject } from "../db/models";
import store from "../app/store";
import { clearSelectedNodeAsync, createNewCellAsync, updateCellValueAsync } from "../features/notebooks/notebookSlice";
import CodeMirrorManager from "../managers/CodeMirrorManager";
import { OKButton } from "./CustomComponents";

type MarkdownCellProps = {
    cell: CellObject;
}

type MarkdownCellState = {
    input: string;
    savedInput: string;
    isEditing: boolean;
}

export default class MarkdownCell extends React.Component<MarkdownCellProps, MarkdownCellState> {
    private ref: React.RefObject<ReactCodeMirrorRef>;
    constructor(props: MarkdownCellProps) {
        super(props);
        this.ref = React.createRef<ReactCodeMirrorRef>();

        this.state = {
            input: "",
            savedInput: props.cell.value ?? "",
            isEditing: props.cell.value === undefined || props.cell.value === ""
        };

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onSaveCell = this.onSaveCell.bind(this);
        this.onEdit = this.onEdit.bind(this);
    }

    componentDidMount(): void {
        //CodeMirrorManager.addRef(this.props.cell.cellId, this.ref);
    }

    onChange(event: React.ChangeEvent<HTMLTextAreaElement>): void {
        this.setState({input: event.target.value});
    }

    async onSubmit(event: React.KeyboardEvent<HTMLDivElement>): Promise<void> {
        if (event.key === "Enter" && event.shiftKey) {
            event.preventDefault();
            await this.onSaveCell(true);
        }
    }

    async onSaveCell(createNewCell = false): Promise<void> {
        const { cell } = this.props;
        const { input } = this.state;

        await store.dispatch(updateCellValueAsync(cell.cellId, input));
        if (createNewCell) {
            if (cell.nextCell === "") {
                const newCell = await store.dispatch(createNewCellAsync("markdown"));
                if (newCell)
                    CodeMirrorManager.delayedFocus(newCell.cellId);
            } else {
                CodeMirrorManager.focus(cell.nextCell);
            }
        }
        this.setState({savedInput: input, isEditing: false});
    }

    onEdit(event: React.MouseEvent<HTMLDivElement, MouseEvent>): void {
        event.preventDefault();
        this.setState({isEditing: true});
    }

    render(): JSX.Element {
        const { cell } = this.props;
        const { savedInput, isEditing } = this.state;
        return (
            <div className="relative flex w-full" style={{minHeight: "4rem"}}>
                {isEditing && (
                    <>
                        <CodeMirror
                            ref={ref => {
                                if (ref)
                                    CodeMirrorManager.addRef(cell.cellId, ref);
                            }}
                            value={savedInput}
                            extensions={[basicSetup, oneDarkHighlightStyle, markdown()]}
                            className="w-full overflow-auto shadow-inner"
                            height="100%"
                            minHeight="4rem"
                            onClick={async () => await store.dispatch(clearSelectedNodeAsync())}
                            onUpdate={update => {
                                if (update.docChanged)
                                    this.setState({input: update.state.doc.toString()});
                            }}
                            onKeyDown={this.onSubmit} />
                        <div className="absolute flex flex-row-reverse"
                            style={{top: "0.5rem", right: "0.5rem"}}>
                            <OKButton icon="save" text="Save"
                                className="absolute text-xs px-2 py-1"
                                onClick={async () => await this.onSaveCell()} />
                        </div>
                    </>
                    
                ) ||
                !isEditing && (
                    <div className="p-2 w-full markdown-body"
                        style={{backgroundColor: "#ffffff"}}
                        onDoubleClick={this.onEdit}>
                        <ReactMarkdown remarkPlugins={[remarkGfm]} rehypePlugins={[rehypeHighlight]}>
                            {savedInput}
                        </ReactMarkdown>
                    </div>
                )}
            </div>
        );
    }
}