import Konva from "konva";
import { KonvaEventObject } from "konva/lib/Node";
import { Vector2d } from "konva/lib/types";
import React from "react";
import { Circle, Group, Rect, Text } from "react-konva";
import { Portal } from "react-konva-utils";
import { Unsubscribe } from "redux";
import store from "../app/store";
import { NodeConnectionObject, NodeObject } from "../db/models";
import { clearSelectedNodeAsync, createNewConnectionAsync, deleteConnectionWithIdAsync, deleteNodeAsync, getNodeById, getSelectedNode, getSeparateConnectionsByNodeId, selectNodeAsync, updateNodeCoordsAsync, updateNodeNameAsync } from "../features/notebooks/notebookSlice";
import NodeTypeManager from "../managers/NodeTypeManager";
import Colors from "../utils/Colors";
import KonvaUtils from "../utils/KonvaUtils";
import { KonvaButton } from "./KonvaComponents";
import { Input, KonvaForm } from "./KonvaForm";
import { PrimitiveTree, Slot as SlotObject } from "../types";
import NodeConnectionManager from "../managers/NodeConnectionManager";

type NodeHandleProps = {
    cellId: string;
    nodeId: string;
    slotId: string;
    x: number;
    y: number;
    fill: string;
    stroke: string;
    isInput: boolean;
    isConnected: boolean;
}

type NodeHandleState = {
    startPosition: Vector2d;
}

class NodeHandle extends React.Component<NodeHandleProps, NodeHandleState> {
    private static path: Konva.Path | null = null;

    constructor(props: NodeHandleProps) {
        super(props);

        this.state = {
            startPosition: { x: 0, y: 0 }
        };

        this.onDragStart = this.onDragStart.bind(this);
        this.onDragMove = this.onDragMove.bind(this);
        this.onDragEnd = this.onDragEnd.bind(this);
        this.onConnect = this.onConnect.bind(this);

        this.isSlotTypeEqual = this.isSlotTypeEqual.bind(this);
        this.isOtherInputSlotConnected = this.isOtherInputSlotConnected.bind(this);
        this.isGraphCyclic = this.isGraphCyclic.bind(this);
    }

    onDragStart(event: KonvaEventObject<DragEvent>) {
        KonvaForm.cancel();
        
        const target = event.target;
        const parent = target.parent;
        if (!parent) {
            console.error("This Konva node should be under a container node.");
            return;
        }

        const startPos = target.getPosition();
        const endPos = startPos;
        const pathData = `M${startPos.x} ${startPos.y} L${endPos.x} ${endPos.y}`;
        const path = new Konva.Path({
            data: pathData,
            stroke: Colors.Gray[900],
            strokeWidth: 3
        });
        parent.add(path);
        path.setZIndex(0);
        NodeHandle.path = path;
        this.setState({startPosition: startPos});
    }

    onDragMove(event: KonvaEventObject<DragEvent>) {
        const { isInput } = this.props;
        const { startPosition } = this.state;

        const layer: Konva.Layer | null = event.target.getLayer();
        if (!layer) {
            console.error("The Konva layer could not be found.");
            return;
        }
        
        const thisCircle = event.target;
        const position = thisCircle.getPosition();
        let startPos, endPos;

        if (isInput) {
            // The isInput specifies if its either the input handle or the output
            // handle that's being dragged.
            // Gotta flip the start and end positions so the path forms an S shape
            // instead of a mirrored S shape when dragged.
            startPos = position;
            endPos = startPosition;
        } else {
            startPos = startPosition;
            endPos = position;
        }

        const midpointPos: Vector2d = {
            x: ((endPos.x - startPos.x) / 2) + (startPos.x),
            y: ((endPos.y - startPos.y) / 2) + (startPos.y)
        };
        const pathData = `M${startPos.x} ${startPos.y} Q${startPos.x + 30} ${startPos.y} ${midpointPos.x} ${midpointPos.y} Q${endPos.x - 30} ${endPos.y} ${endPos.x} ${endPos.y}`;
        const path = NodeHandle.path;
        if (!path) {
            console.error("The Konva path could not be found.");
            return;
        }

        path.data(pathData);
    }

    async onDragEnd(event: KonvaEventObject<DragEvent>) {
        const { isInput, nodeId } = this.props;
        const { startPosition } = this.state;

        const thisCircle = event.target;
        thisCircle.position(startPosition);

        const path = NodeHandle.path;
        if (path != null) {
            path.destroy();
            NodeHandle.path = null;
        }

        const stage = thisCircle.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }

        stage.draw();

        const pointerPos = stage.getPointerPosition();
        if (!pointerPos) {
            console.error("The Konva pointer position could not be found.");
            return;
        }

        const screenX = Math.floor(pointerPos.x);
        const screenY = Math.floor(pointerPos.y);

        const elements = stage.getAllIntersections({x: screenX, y: screenY}) as Konva.Shape[];
        
        if (!elements || elements.length <= 0)
            return;

        let element: Konva.Shape | undefined;
        let otherNodeId = "";
        let otherSlotId = "";

        let i;
        for (i = 0; i < elements.length; i++) {
            const elem = elements[i];
            const type = elem.getAttr("data-type");
            if (!type || (isInput ? type !== "output" : type !== "input"))
                continue;

            otherNodeId = elem.getAttr("data-node-id");
            otherSlotId = elem.getAttr("data-slot-id");
            // NOTE: These ids can be 0 and !id will return true
            // TODO: Make ids to be unique uuids
            if (otherNodeId == null || otherSlotId == null) {
                console.error("No attributes 'data-item-id' or 'data-slot-id' could be found from the element.");
                continue;
            }

            if (nodeId === otherNodeId)
                continue;

            element = elem;
            break;
        }

        if (!element)
            return;

        await this.onConnect(otherNodeId, otherSlotId);
    }

    async onConnect(otherNodeId: string, otherSlotId: string): Promise<void> {
        const { cellId, nodeId, slotId, isInput } = this.props;
        if (nodeId === otherNodeId)
            return;
        if (!this.isSlotTypeEqual(cellId, isInput, nodeId, slotId, otherNodeId, otherSlotId))
            return;
        if (this.isGraphCyclic(cellId, isInput ? nodeId : otherNodeId, isInput ? otherNodeId : nodeId))
            return;
        if (this.isOtherInputSlotConnected(cellId, isInput, nodeId, slotId, otherNodeId, otherSlotId)) {
            const connections = getSeparateConnectionsByNodeId(store.getState(), cellId, isInput ? nodeId : otherNodeId);
            const toConnections = connections.toConnections;
            const connection = toConnections.find(conn => conn.toNodeId === (isInput ? nodeId : otherNodeId) && conn.toSlotId === (isInput ? slotId : otherSlotId));
            if (connection)
                await store.dispatch(deleteConnectionWithIdAsync(cellId, connection.connectionId));
        }

        if (isInput)
            await store.dispatch(createNewConnectionAsync(cellId, otherNodeId, otherSlotId, nodeId, slotId));
        else
            await store.dispatch(createNewConnectionAsync(cellId, nodeId, slotId, otherNodeId, otherSlotId));
    }

    // Check if both node slots have the same types
    isSlotTypeEqual(cellId: string, isInput: boolean, nodeId: string, slotId: string, otherNodeId: string, otherSlotId: string): boolean {
        const node = getNodeById(store.getState(), cellId, nodeId);
        const otherNode = getNodeById(store.getState(), cellId, otherNodeId);
        if (!node || !otherNode)
            return false;

        const nodeType = NodeTypeManager.getType(node.type);
        const otherNodeType = NodeTypeManager.getType(otherNode.type);
        if (!nodeType || !otherNodeType)
            return false;

        const nodeSlots = isInput ? nodeType.inputSlots : nodeType.outputSlots;
        const nodeSlot = nodeSlots.find(slot => slot.id === slotId);
        if (!nodeSlot)
            return false;

        const otherNodeSlots = isInput ? otherNodeType.outputSlots : otherNodeType.inputSlots;
        const otherNodeSlot = otherNodeSlots.find(slot => slot.id === otherSlotId);
        if (!otherNodeSlot)
            return false;

        const inputSlotTypes = isInput ? nodeSlot.type.replace(/[ ]/g, "").split("|") : otherNodeSlot.type.replace(/[ ]/g, "").split("|");
        const outputSlotType = isInput ? otherNodeSlot.type : nodeSlot.type;

        return inputSlotTypes.includes(outputSlotType);
    }

    // Output slot can connect to multiple input slots,
    // but input slot can only connect to one output slot
    isOtherInputSlotConnected(cellId: string, isInput: boolean, nodeId: string, slotId: string, otherNodeId: string, otherSlotId: string): boolean {
        const connections = getSeparateConnectionsByNodeId(store.getState(), cellId, isInput ? nodeId : otherNodeId);
        const toConnections = connections.toConnections;
        const index = toConnections.findIndex(conn => conn.toNodeId === (isInput ? nodeId : otherNodeId) && conn.toSlotId === (isInput ? slotId : otherSlotId));
        if (index < 0)
            return false;

        return true;
    }

    isGraphCyclic(cellId: string, startNodeId: string, endNodeId: string): boolean {
        const connections = getSeparateConnectionsByNodeId(store.getState(), cellId, endNodeId).toConnections;

        for (let i = 0; i < connections.length; i++) {
            const conn = connections[i];
            if (conn.fromNodeId === startNodeId)
                return true;

            if (this.isGraphCyclic(cellId, startNodeId, conn.fromNodeId))
                return true;
        }

        return false;
    }

    render(): JSX.Element {
        const { nodeId, slotId, x, y, fill, stroke, isInput, isConnected } = this.props;

        return (
            <>
                {!isConnected && (
                    <Circle x={x}
                        y={y}
                        radius={4}
                        fill={fill}
                        stroke={stroke}
                        strokeWidth={1}
                        listening={false} />
                ) ||
                isConnected && (
                    <Rect x={x}
                        y={y - 5}
                        width={7}
                        height={7}
                        rotation={45}
                        fill={stroke}
                        strokeEnabled={false} />
                )}

                <Circle data-type={`${isInput ? "input" : "output"}`}
                    data-node-id={nodeId}
                    data-slot-id={slotId}
                    x={x}
                    y={y}
                    radius={15}
                    fill="transparent"
                    draggable
                    onDragStart={event => {
                        event.cancelBubble = true;
                        this.onDragStart(event);
                    }}
                    onDragMove={event => {
                        event.cancelBubble = true;
                        this.onDragMove(event);
                    }}
                    onDragEnd={async event => {
                        event.cancelBubble = true;
                        await this.onDragEnd(event);
                    }}
                    onMouseEnter={evt => {
                        KonvaUtils.setCursor(evt, "pointer");
                    }}
                    onMouseLeave={evt => {
                        KonvaUtils.setCursor(evt, "default");
                    }} />
            </>
        );
    }
}

type SlotProps = {
    cellId: string;
    nodeId: string;
    slot: SlotObject;
    x?: number;
    y?: number;
    height?: number;
    isInput?: boolean;
    isConnected?: boolean;
}

type SlotState = {
    labelWidth: number;
    typeWidth: number;
}

class Slot extends React.Component<SlotProps, SlotState> {
    private label: React.RefObject<Konva.Text>;
    private type: React.RefObject<Konva.Text>;

    constructor(props: SlotProps) {
        super(props);

        this.label = React.createRef();
        this.type = React.createRef();

        this.state = {
            labelWidth: 100,
            typeWidth: 100
        };
    }

    componentDidMount() {
        const labelWidth = this.label.current?.width();
        const typeWidth = this.type.current?.width();
        if (!labelWidth || !typeWidth) {
            console.error("The referenced Konva label or type text could not be found.");
            return;
        }

        this.setState({labelWidth, typeWidth});
    }

    render(): JSX.Element {
        const { cellId, nodeId, slot, x = 0, y = 0, height = 40, isInput = true, isConnected = false } = this.props;
        const { labelWidth, typeWidth } = this.state;
        return (
            <Group x={x}
                y={y}>
                <Text ref={this.label}
                    x={isInput ? 16 : 0 }
                    y={0}
                    offsetX={isInput ? 0 : 16 + labelWidth}
                    height={height / 2}
                    text={slot.id}
                    fontSize={12}
                    fontStyle="bold"
                    align={isInput ? "left" : "right"}
                    verticalAlign="bottom" />
                <Text ref={this.type}
                    x={isInput ? 16 : 0 }
                    y={20}
                    offsetX={isInput ? 0 : 16 + typeWidth}
                    height={height / 2}
                    text={slot.type}
                    fontSize={10}
                    align={isInput ? "left" : "right"}
                    verticalAlign="top" />
                <NodeHandle cellId={cellId}
                    nodeId={nodeId}
                    slotId={slot.id}
                    x={0}
                    y={height / 2}
                    fill={Colors.Blue[200]}
                    stroke={Colors.Blue[600]}
                    isInput={isInput}
                    isConnected={isConnected} />
            </Group>
        );
    }
}

type NodeProps = {
    cellId: string;
    node: NodeObject;
}

type NodeState = {
    connections: {
        fromConnections: NodeConnectionObject[];
        toConnections: NodeConnectionObject[];
    };
    isDragging: boolean;
    isSelected: boolean;
}

export default class Node extends React.Component<NodeProps, NodeState> {
    private unsubscribe: Unsubscribe = () => { return; };

    constructor(props: NodeProps) {
        super(props);

        this.state = {
            connections: { fromConnections: [], toConnections: [] },
            isDragging: false,
            isSelected: false
        };

        this.updateNode = this.updateNode.bind(this);

        this.onClickNode = this.onClickNode.bind(this);
        this.onDragNodeStart = this.onDragNodeStart.bind(this);
        this.onDragNode = this.onDragNode.bind(this);
        this.onDragNodeEnd = this.onDragNodeEnd.bind(this);

        this.onUpdateName = this.onUpdateName.bind(this);
        this.onDeleteNode = this.onDeleteNode.bind(this);
    }

    componentDidMount(): void {
        this.unsubscribe = store.subscribe(this.updateNode);
        this.updateNode();
    }

    componentWillUnmount(): void {
        this.unsubscribe();
    }

    updateNode(): void {
        const { cellId, node } = this.props;
        const state = store.getState();
        const connections = getSeparateConnectionsByNodeId(state, cellId, node.nodeId);
        const selectedNode = getSelectedNode(state);
        this.setState({connections, isSelected: (!!selectedNode && selectedNode.cellId === cellId && selectedNode.nodeId === node.nodeId)});
    }

    async onClickNode(event: KonvaEventObject<MouseEvent>): Promise<void> {
        event.cancelBubble = true;
        KonvaForm.cancel();
        
        const { cellId, node } = this.props;
        const selectedNode = getSelectedNode(store.getState());
        if (!selectedNode || !(selectedNode.cellId === cellId && selectedNode.nodeId === node.nodeId)) {
            // Node should be selected, make it glow!
            await store.dispatch(selectNodeAsync(cellId, node.nodeId));
            this.setState({isSelected: true});
        } else {
            // Node already selected, unselect it
            await store.dispatch(clearSelectedNodeAsync());
            this.setState({isSelected: false});
        }
    }

    onDragNodeStart(event: KonvaEventObject<DragEvent>): void {
        KonvaUtils.setCursor(event, "move");
        KonvaForm.cancel();
        this.setState({isDragging: true});
    }

    onDragNode(event: KonvaEventObject<DragEvent>): void {
        const { cellId } = this.props;
        const { connections } = this.state;
        const pos = event.target.position();
        NodeConnectionManager.moveConnections(cellId, connections.fromConnections, connections.toConnections, pos);
    }

    async onDragNodeEnd(event: KonvaEventObject<DragEvent>): Promise<void> {
        KonvaUtils.setCursor(event, "default");
        const { cellId, node } = this.props;

        const newPos = event.target.position();
        await store.dispatch(updateNodeCoordsAsync(cellId, node.nodeId, { x: newPos.x, y: newPos.y }));
        this.setState({isDragging: false});
    }

    async onUpdateName(name: string): Promise<boolean> {
        const { cellId, node } = this.props;

        const regex = /^\w+[a-zA-Z0-9 \-.,]*$/;
        if (!name.match(regex))
            return false;

        await store.dispatch(updateNodeNameAsync(cellId, node.nodeId, name));
        return true;
    }

    async onDeleteNode(): Promise<void> {
        const { cellId, node } = this.props;
        const selectedNode = getSelectedNode(store.getState());
        if (selectedNode && selectedNode.cellId === cellId && selectedNode.nodeId === node.nodeId) {
            store.dispatch(clearSelectedNodeAsync()).then(() => {
                return store.dispatch(deleteNodeAsync(cellId, node.nodeId));
            });
        } else {
            await store.dispatch(deleteNodeAsync(cellId, node.nodeId));
        }
    }

    render(): JSX.Element {
        const { cellId, node } = this.props;
        const { connections, isDragging, isSelected } = this.state;
        const nodeType = NodeTypeManager.getType(node.type);

        const nodeWidth = nodeType?.style?.width ?? 200;
        const topBarHeight = 20;

        const inputSlots = nodeType?.inputSlots ?? [];
        const outputSlots = nodeType?.outputSlots ?? [];
        const inputValues: PrimitiveTree = {};
        node.inputValues.forEach(val => {
            inputValues[val.id] = val.value;
        });

        let nodeHeight = nodeType?.style?.height;
        if (!nodeHeight || nodeHeight <= -1) {
            // Calculate node height based on the number of slots
            const numSlots = Math.max(inputSlots.length, outputSlots.length);
            nodeHeight = topBarHeight + numSlots * 40;
        }

        return (
            <Portal selector=".movingNodes" enabled={isDragging}>
                <Group x={node.coords.x}
                    y={node.coords.y}
                    onClick={this.onClickNode}
                    onDragStart={this.onDragNodeStart}
                    onDragMove={this.onDragNode}
                    onDragEnd={this.onDragNodeEnd}
                    draggable>
                    <Rect name="background"
                        x={0}
                        y={0}
                        shadowEnabled={isSelected}
                        shadowColor={Colors.Orange[500]}
                        shadowBlur={10}
                        width={nodeWidth}
                        height={nodeHeight}
                        fill={Colors.Gray[400]} />
                    {/* Top bar */}
                    <Rect name="top-bar"
                        width={nodeWidth}
                        height={topBarHeight}
                        fill={Colors.Gray[800]} />
                    <Input width={nodeType?.isDeletable ? nodeWidth - topBarHeight : nodeWidth}
                        height={topBarHeight}
                        textColor={Colors.Gray[100]}
                        bgColor={Colors.Gray[800]}
                        hoverColor={Colors.Gray[700]}
                        padding={8}
                        value={node.name}
                        onChange={this.onUpdateName} />
                    {nodeType && nodeType.isDeletable && (
                        <KonvaButton x={nodeWidth - topBarHeight + 3}
                            y={3}
                            width={topBarHeight - 6}
                            height={topBarHeight - 6}
                            cornerRadius={topBarHeight}
                            backgroundColor={Colors.Red[500]}
                            text="&#215;"
                            textOffsetY={-1}
                            fontSize={14}
                            textColor={Colors.Gray[900]}
                            onClick={async (event) => {
                                event.cancelBubble = true;
                                await this.onDeleteNode();
                            }} />
                    )}
                    {/* Input Slots */}
                    {inputSlots.map((slot, index) => {
                        const isConnected = connections.toConnections.findIndex(conn => conn.toSlotId === slot.id) >= 0;
                        return (
                            <Slot key={`${node.nodeId}-${slot.id}`}
                                cellId={cellId}
                                nodeId={node.nodeId}
                                slot={slot}
                                x={0}
                                y={topBarHeight + 40 * index}
                                isInput={true}
                                isConnected={isConnected} />
                        );
                    })}
                    {outputSlots.map((slot, index) => {
                        const isConnected = connections.fromConnections.findIndex(conn => conn.fromSlotId === slot.id) >= 0;
                        return (
                            <Slot key={`${node.nodeId}-${slot.id}`}
                                cellId={cellId}
                                nodeId={node.nodeId}
                                slot={slot}
                                x={nodeWidth}
                                y={topBarHeight + 40 * index}
                                isInput={false}
                                isConnected={isConnected} />
                        );
                    })}
                </Group>
            </Portal>
        );
    }
}