import { DataFrame, Series } from "danfojs";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler } from "../types";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import Meta from "../utils/Meta";
import { Types } from "../utils/Types";

const addDataFrame: NodeType = {
    id: "add-dataframe",
    name: "Add DataFrame",
    description: Descriptions.NodeSpecific.AddDataFrame.Description,
    style: {
        width: 160
    },
    inputSlots: [
        {
            id: "input1",
            description: Descriptions.Common.DataFrame.Input1Input,
            type: Types.DataFrame
        },
        {
            id: "input2",
            description: Descriptions.Common.DataFrameBinary.Input2Input,
            type: Types.OR(Types.DataFrame, Types.Series, Types.Number)
        }
    ],
    outputSlots: [
        {
            id: "result",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "isRowAxis",
            description: Descriptions.Common.DataFrameBinary.IsRowAxisProperty,
            type: "boolean"
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input1"]))
            errorHandler.error(Errors.MissingNodeInput("input1"));

        if (Meta.isEmpty(input["input2"]))
            errorHandler.error(Errors.MissingNodeInput("input2"));

        if (Meta.isEmpty(input["input1"]) || Meta.isEmpty(input["input2"]))
            return {};

        const input1 = input["input1"] as DataFrame;
        const input2 = input["input2"] as DataFrame | Series | number;
        const isRowAxis = properties["isRowAxis"] as boolean;

        const invalidColumns1 = input1.dtypes.map((type, index) => {
            if (type === "string" || type === "boolean")
                return input1.columns[index];
        }).filter((col): col is string => !!col);
        if (invalidColumns1.length > 0)
            errorHandler.error(Errors.DataFrameNumericalColumnsOnly("input1", invalidColumns1));

        let invalidColumns2: string[] = [];
        if (input2 instanceof DataFrame) {
            if (input2.shape !== input1.shape) {
                errorHandler.error(Errors.DataFrameMismatchShape("input1", input1.shape, "input2", input2.shape));
                return {};
            }

            invalidColumns2 = input2.dtypes.map((type, index) => {
                if (type === "string" || type === "boolean")
                    return input2.columns[index];
            }).filter((col): col is string => !!col);
        }
        else if (input2 instanceof Series) {
            if (!isRowAxis && (input2.size !== input1.columns.length)) {
                errorHandler.error(Errors.DataFrameSeriesMismatchShape("input1", input1.columns.length, "input2", input2.size, "column"));
                return {};
            }

            if (isRowAxis && (input2.size !== input1.size)) {
                errorHandler.error(Errors.DataFrameSeriesMismatchShape("input1", input1.size, "input2", input2.size, "row"));
                return {};
            }

            invalidColumns2 = (input2.dtype === "string" || input2.dtype === "boolean") ? ["Series"] : [];
        }

        if (invalidColumns2.length > 0)
            errorHandler.error(Errors.DataFrameNumericalColumnsOnly("input2", invalidColumns2));

        if (invalidColumns1.length > 0 || invalidColumns2.length > 0)
            return {};

        return {
            result: input1.add(input2, { axis: isRowAxis && input2 instanceof Series ? 0 : 1 }) as DataFrame
        };
    }
};

