import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppDispatch, RootState } from "../../app/store";
import DBService from "../../db/DBService";
import { CellObject, DataSourceObject, NodeConnectionObject, NodeModel, NodeObject, NotebookObject, VariableObject } from "../../db/models";
import { getCurrentUser } from "../users/userSlice";
import cuid from "cuid";
import { SourceType, VariableType } from "../../utils/Types";
import { Primitives, Vector2 } from "../../types";
import { WritableDraft } from "@reduxjs/toolkit/node_modules/immer/dist/internal";

// Model
export type NotebookState = {
    notebookDetails: NotebookObject[];
    currentNotebook: NotebookObject | undefined;
    selectedNode: {
        cellId: string;
        nodeId: string;
    } | undefined;
    selectedNodeTypeHelp: string | undefined;
    isDraggingSource: boolean;
    isDraggingVariable: boolean;
    isDraggingCell: boolean;
}

const initialState: NotebookState = {
    notebookDetails: [],
    currentNotebook: undefined,
    selectedNode: undefined,
    selectedNodeTypeHelp: undefined,
    isDraggingSource: false,
    isDraggingVariable: false,
    isDraggingCell: false
};

// Payload Types
/// Notebooks
type UpdateNotebookNamePayload = {
    notebookId: string;
    name: string;
}

type UpdateDataSourceNamePayload = {
    sourceId: string;
    name: string;
}

type UpdateVariableNamePayload = {
    variableId: string;
    name: string;
}

type UpdateVariableValuePayload = {
    variableId: string;
    value: string | number | boolean;
}

/// Cells
type UpdateCellTypePayload = {
    cellId: string;
    type: "node" | "markdown";
}

type UpdateCellValuePayload = {
    cellId: string;
    value: string;
}

type InsertCellPayload = {
    cellId: string;
    targetCellId: string;
}

/// Nodes
type AddNodePayload = {
    cellId: string;
    node: NodeObject;
}

type RemoveNodePayload = {
    cellId: string;
    nodeId: string;
}

type UpdateNodeNamePayload = {
    cellId: string;
    nodeId: string;
    name: string;
}

type UpdateNodeCoordsPayload = {
    cellId: string;
    nodeId: string;
    coords: Vector2;
}

type SelectNodePayload = {
    cellId: string;
    nodeId: string;
}

type SetNodeInputValuePayload = {
    cellId: string;
    nodeId: string;
    slotId: string;
    value: Primitives;
}

type SetNodePropertyPayload = {
    cellId: string;
    nodeId: string;
    propertyId: string;
    value: string | number | boolean;
}

/// Connections
type AddConnectionPayload = {
    cellId: string;
    connection: NodeConnectionObject;
}

type RemoveConnectionPayload = {
    cellId: string;
    fromNodeId: string;
    fromSlotId: string;
    toNodeId: string;
    toSlotId: string;
}

type RemoveConnectionWithIdPayload = {
    cellId: string;
    connectionId: string;
}

export const slice = createSlice({
    name: "notebooks",
    initialState: initialState,
    reducers: {
        // Notebooks
        openNotebook: (state, action: PayloadAction<NotebookObject>) => {
            state.currentNotebook = action.payload;
        },
        closeNotebook: (state, action: PayloadAction<undefined>) => {
            state.currentNotebook = undefined;
        },
        setNotebookDetails: (state, action: PayloadAction<NotebookObject[]>) => {
            state.notebookDetails = action.payload;
        },
        updateNotebookName: (state, action: PayloadAction<UpdateNotebookNamePayload>) => {
            const payload = action.payload;
            const index = state.notebookDetails.findIndex(notebook => notebook.notebookId === payload.notebookId);
            if (index < 0)
                return;

            const notebook = state.notebookDetails[index];
            const now = Date.now();
            notebook.name = payload.name;
            notebook.dateModified = now;
        },
        addNotebook: (state, action: PayloadAction<NotebookObject>) => {
            const notebook: NotebookObject = {
                ...action.payload,
                cells: []
            };
            state.notebookDetails.push(notebook);
        },
        removeNotebook: (state, action: PayloadAction<string>) => {
            const id = action.payload;
            const index = state.notebookDetails.findIndex(notebook => notebook.notebookId === id);
            if (index < 0)
                return;

            const newArr = state.notebookDetails.slice();
            newArr.splice(index, 1);
            state.notebookDetails = newArr;
        },

        // Data Sources
        addDataSource: (state, action: PayloadAction<DataSourceObject>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            state.currentNotebook.sources = [...state.currentNotebook.sources, payload];
        },
        updateDataSourceName: (state, action: PayloadAction<UpdateDataSourceNamePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const index = state.currentNotebook.sources.findIndex(source => source.sourceId === payload.sourceId);
            if (index < 0)
                return;

            state.currentNotebook.sources[index].name = payload.name;
        },
        removeDataSource: (state, action: PayloadAction<string>) => {
            const id = action.payload;
            if (!state.currentNotebook || state.currentNotebook.sources.length <= 0)
                return;

            const sourceIndex = state.currentNotebook.sources.findIndex(source => source.sourceId === id);
            if (sourceIndex < 0)
                return;

            const newArr = state.currentNotebook.sources.slice();
            newArr.splice(sourceIndex, 1);
            state.currentNotebook.sources = newArr;
        },
        setIsDraggingSource: (state, action: PayloadAction<boolean>) => {
            const value = action.payload;
            if (!state.currentNotebook)
                return;

            state.isDraggingSource = value;
        },

        // Variables
        addVariable: (state, action: PayloadAction<VariableObject>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            state.currentNotebook.variables = [...state.currentNotebook.variables, payload];
        },
        updateVariableName: (state, action: PayloadAction<UpdateVariableNamePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const index = state.currentNotebook.variables.findIndex(variable => variable.variableId === payload.variableId);
            if (index < 0)
                return;

            state.currentNotebook.variables[index].name = payload.name;
        },
        updateVariableValue: (state, action: PayloadAction<UpdateVariableValuePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const index = state.currentNotebook.variables.findIndex(variable => variable.variableId === payload.variableId);
            if (index < 0)
                return;

            state.currentNotebook.variables[index].value = payload.value;
        },
        removeVariable: (state, action: PayloadAction<string>) => {
            const id = action.payload;
            if (!state.currentNotebook || state.currentNotebook.variables.length <= 0)
                return;

            const variableIndex = state.currentNotebook.variables.findIndex(variable => variable.variableId === id);
            if (variableIndex < 0)
                return;

            const newArr = state.currentNotebook.variables.slice();
            newArr.splice(variableIndex, 1);
            state.currentNotebook.variables = newArr;
        },
        setIsDraggingVariable: (state, action: PayloadAction<boolean>) => {
            const value = action.payload;
            if (!state.currentNotebook)
                return;

            state.isDraggingVariable = value;
        },

        // Cells
        addCell: (state, action: PayloadAction<CellObject>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const lastCellIndex = state.currentNotebook.cells.findIndex(cell => cell.nextCell === "");
            if (state.currentNotebook.cells.length > 0 && lastCellIndex >= 0)
                state.currentNotebook.cells[lastCellIndex].nextCell = payload.cellId;

            state.currentNotebook.cells = [...state.currentNotebook.cells, payload];
        },
        updateCellType: (state, action: PayloadAction<UpdateCellTypePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            state.currentNotebook.cells[cellIndex].type = payload.type;
        },
        removeCell: (state, action: PayloadAction<string>) => {
            const id = action.payload;
            if (!state.currentNotebook || state.currentNotebook.cells.length <= 0)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === id);
            if (cellIndex < 0)
                return;

            const lastCellIndex = state.currentNotebook.cells.findIndex(cell => cell.nextCell === id);
            if (state.currentNotebook.cells.length > 1 && lastCellIndex >= 0)
                state.currentNotebook.cells[lastCellIndex].nextCell = state.currentNotebook.cells[cellIndex].nextCell;

            const newArr = state.currentNotebook.cells.slice();
            newArr.splice(cellIndex, 1);
            state.currentNotebook.cells = newArr;
        },
        updateCellValue: (state, action: PayloadAction<UpdateCellValuePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            state.currentNotebook.cells[cellIndex].value = payload.value;
        },
        insertCell: (state, action: PayloadAction<InsertCellPayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            if (payload.cellId === payload.targetCellId)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;
            
            const targetCellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.targetCellId);
            if (targetCellIndex < 0)
                return;

            const prevCellIndex = state.currentNotebook.cells.findIndex(cell => cell.nextCell === payload.cellId);

            const nextCell = state.currentNotebook.cells[cellIndex].nextCell;
            const cells = state.currentNotebook.cells.slice();
            if (cells[targetCellIndex].nextCell === cells[cellIndex].cellId)
                return;

            cells[cellIndex].nextCell = cells[targetCellIndex].nextCell;
            cells[targetCellIndex].nextCell = cells[cellIndex].cellId;
            if (prevCellIndex >= 0)
                cells[prevCellIndex].nextCell = nextCell;

            state.currentNotebook.cells = cells;
        },
        insertCellToTop: (state, action: PayloadAction<string>) => {
            const cellId = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
            if (cellIndex < 0)
                return;

            const prevCellIndex = state.currentNotebook.cells.findIndex(cell => cell.nextCell === cellId);
            if (prevCellIndex < 0)
                return;

            const nextCells = state.currentNotebook.cells.map(cell => cell.nextCell);
            let topCellIndex = -1;
            for (let i = 0; i < state.currentNotebook.cells.length; i++) {
                const cell = state.currentNotebook.cells[i];
                if (!nextCells.includes(cell.cellId)) {
                    topCellIndex = i;
                    break;
                }
            }
            if (topCellIndex < 0)
                return;

            if (topCellIndex === cellIndex)
                return;

            const cells = state.currentNotebook.cells.slice();
            cells[prevCellIndex].nextCell = cells[cellIndex].nextCell;
            cells[cellIndex].nextCell = cells[topCellIndex].cellId;

            state.currentNotebook.cells = cells;
        },
        setIsDraggingCell: (state, action: PayloadAction<boolean>) => {
            const value = action.payload;
            if (!state.currentNotebook)
                return;

            state.isDraggingCell = value;
        },

        // Nodes
        addNode: (state, action: PayloadAction<AddNodePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const nodes = state.currentNotebook.cells[cellIndex].nodes;
            state.currentNotebook.cells[cellIndex].nodes = [...nodes, payload.node];
        },
        removeNode: (state, action: PayloadAction<RemoveNodePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const nodeIndex = state.currentNotebook.cells[cellIndex].nodes.findIndex(node => node.nodeId === payload.nodeId);
            if (nodeIndex < 0)
                return;

            // Delete the connections to this node as well
            const newConns = state.currentNotebook.cells[cellIndex].connections
                .filter(conn => conn.fromNodeId !== payload.nodeId && conn.toNodeId !== payload.nodeId);
            state.currentNotebook.cells[cellIndex].connections = newConns;
            
            const newArr = state.currentNotebook.cells[cellIndex].nodes.slice();
            newArr.splice(nodeIndex, 1);
            state.currentNotebook.cells[cellIndex].nodes = newArr;
        },
        updateNodeName: (state, action: PayloadAction<UpdateNodeNamePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const nodeIndex = state.currentNotebook.cells[cellIndex].nodes.findIndex(node => node.nodeId === payload.nodeId);
            if (nodeIndex < 0)
                return;
            
            state.currentNotebook.cells[cellIndex].nodes[nodeIndex].name = payload.name;
        },
        updateNodeCoords: (state, action: PayloadAction<UpdateNodeCoordsPayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const nodeIndex = state.currentNotebook.cells[cellIndex].nodes.findIndex(node => node.nodeId === payload.nodeId);
            if (nodeIndex < 0)
                return;
            
            state.currentNotebook.cells[cellIndex].nodes[nodeIndex].coords = payload.coords;
        },
        selectNode: (state, action: PayloadAction<SelectNodePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            state.selectedNode = { cellId: payload.cellId, nodeId: payload.nodeId };
        },
        clearSelectedNode: (state, action: PayloadAction<undefined>) => {
            state.selectedNode = undefined;
        },
        selectNodeType: (state, action: PayloadAction<string>) => {
            const nodeType = action.payload;
            if (!state.currentNotebook)
                return;

            state.selectedNodeTypeHelp = nodeType;
        },
        clearSelectedNodeType: (state, action: PayloadAction<undefined>) => {
            state.selectedNodeTypeHelp = undefined;
        },
        setNodeInputValue: (state, action: PayloadAction<SetNodeInputValuePayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const nodeIndex = state.currentNotebook.cells[cellIndex].nodes.findIndex(node => node.nodeId === payload.nodeId);
            if (nodeIndex < 0)
                return;

            const slotIndex = state.currentNotebook.cells[cellIndex].nodes[nodeIndex].inputValues.findIndex(val => val.id === payload.slotId);
            if (slotIndex < 0) {
                const inputVals = state.currentNotebook.cells[cellIndex].nodes[nodeIndex].inputValues;
                state.currentNotebook.cells[cellIndex].nodes[nodeIndex].inputValues = [...inputVals, { id: payload.slotId, value: payload.value }];
            } 
            else
                state.currentNotebook.cells[cellIndex].nodes[nodeIndex].inputValues[slotIndex].value = payload.value;
        },
        setNodeProperty: (state, action: PayloadAction<SetNodePropertyPayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const nodeIndex = state.currentNotebook.cells[cellIndex].nodes.findIndex(node => node.nodeId === payload.nodeId);
            if (nodeIndex < 0)
                return;

            const propertyIndex = state.currentNotebook.cells[cellIndex].nodes[nodeIndex].properties.findIndex(val => val.id === payload.propertyId);
            if (propertyIndex < 0) {
                const properties = state.currentNotebook.cells[cellIndex].nodes[nodeIndex].properties;
                state.currentNotebook.cells[cellIndex].nodes[nodeIndex].properties = [...properties, { id: payload.propertyId, value: payload.value }];
            } 
            else
                state.currentNotebook.cells[cellIndex].nodes[nodeIndex].properties[propertyIndex].value = payload.value;
        },

        // Connections
        addConnection: (state, action: PayloadAction<AddConnectionPayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const connections = state.currentNotebook.cells[cellIndex].connections;
            state.currentNotebook.cells[cellIndex].connections = [...connections, payload.connection];
        },
        removeConnection: (state, action: PayloadAction<RemoveConnectionPayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const connections = state.currentNotebook.cells[cellIndex].connections
                .filter(connection => connection.fromNodeId === payload.fromNodeId &&
                                      connection.fromSlotId === payload.fromSlotId &&
                                      connection.toNodeId === payload.toNodeId &&
                                      connection.toSlotId === payload.toSlotId);
            if (connections.length <= 0)
                return;

            const newArr = state.currentNotebook.cells[cellIndex].connections.slice();
            connections.forEach(connection => {
                const connectionIndex = newArr.findIndex(conn => conn.connectionId === connection.connectionId);
                if (connectionIndex < 0)
                    return;

                newArr.splice(connectionIndex, 1);
            });

            state.currentNotebook.cells[cellIndex].connections = newArr;
        },
        removeConnectionWithId: (state, action: PayloadAction<RemoveConnectionWithIdPayload>) => {
            const payload = action.payload;
            if (!state.currentNotebook)
                return;

            const cellIndex = state.currentNotebook.cells.findIndex(cell => cell.cellId === payload.cellId);
            if (cellIndex < 0)
                return;

            const connectionIndex = state.currentNotebook.cells[cellIndex].connections.findIndex(connection => connection.connectionId === payload.connectionId);
            if (connectionIndex < 0)
                return;

            const newArr = state.currentNotebook.cells[cellIndex].connections.slice();
            newArr.splice(connectionIndex, 1);
            state.currentNotebook.cells[cellIndex].connections = newArr;
        }
    }
});

// Notebook Actions
export const { openNotebook, closeNotebook, setNotebookDetails, updateNotebookName, addNotebook, removeNotebook } = slice.actions;
// Data Source Actions
export const { addDataSource, updateDataSourceName, removeDataSource, setIsDraggingSource } = slice.actions;
// Variable Actions
export const { addVariable, updateVariableName, updateVariableValue, removeVariable, setIsDraggingVariable } = slice.actions;
// Cell Actions
export const { addCell, updateCellType, removeCell, updateCellValue, insertCell, insertCellToTop, setIsDraggingCell } = slice.actions;
// Node Actions
export const { addNode, removeNode, updateNodeName, updateNodeCoords, selectNode, clearSelectedNode, selectNodeType, clearSelectedNodeType, setNodeInputValue, setNodeProperty } = slice.actions;
// Connection Actions
export const { addConnection, removeConnection, removeConnectionWithId } = slice.actions;

// Thunks
/// Notebooks
export const fetchNotebookDetailsAsync = () => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            const user = getCurrentUser(getState());
            if (!user)
                throw new ReferenceError("No user is currently logged in.");

            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            const notebooks = await DBService.db.getNotebookDetailsByUser(user.userId);
            dispatch(setNotebookDetails(notebooks.map(notebook => notebook.toObject())));
        } catch (err) {
            console.log(err);
        }
    };
};

export const openNotebookAsync = (notebookId: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            const user = getCurrentUser(getState());
            if (!user)
                throw new ReferenceError("No user is currently logged in.");

            // Don't open a currently opened notebook
            if (isNotebookOpen(getState(), notebookId))
                return;

            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            const notebook = await DBService.db.getNotebook(user.userId, notebookId);
            dispatch(openNotebook(notebook.toObject()));
        } catch (err) {
            console.log(err);
        }
    };
};

export const closeNotebookAsync = () => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            const notebook = getCurrentNotebook(getState());
            if (!notebook)
                throw new ReferenceError("No notebook currently selected.");

            // Write any async API calls here
            // Either from a server or from IndexedDb
            dispatch(closeNotebook());
        } catch (err) {
            console.log(err);
        }
    };
};

export const updateNotebookNameAsync = (notebookId: string, name: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            const user = getCurrentUser(getState());
            if (!user)
                throw new ReferenceError("No user is currently logged in.");

            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            // Update both database and our local state
            // TODO: Don't update if updating somehow failed
            await DBService.db.updateNotebookName(user.userId, notebookId, name);
            dispatch(updateNotebookName({ notebookId, name }));
        } catch (err) {
            console.log(err);
        }
    };
};

export const createNewNotebookAsync = (name: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            const user = getCurrentUser(getState());
            if (!user)
                throw new ReferenceError("No user is currently logged in.");

            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            // Update both database and our local state
            // TODO: Don't update if updating somehow failed
            const notebook = await DBService.db.createNewNotebook(user.userId, name);
            dispatch(addNotebook(notebook.toObject()));
        } catch (err) {
            console.log(err);
        }
    };
};

export const deleteNotebookAsync = (notebookId: string) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<void> => {
        try {
            const user = getCurrentUser(getState());
            if (!user)
                throw new ReferenceError("No user is currently logged in.");

            if (!isNotebookInList(getState(), notebookId))
                return;
            
            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            // Update both database and our local state
            // TODO: Don't update if updating somehow failed
            await DBService.db.removeNotebook(user.userId, notebookId);
            dispatch(removeNotebook(notebookId));
        } catch (err) {
            console.log(err);
        }
    };
};

/// Data Sources

export const createNewDataSourceAsync = (name: string, type: SourceType) => {
    return async (dispatch: AppDispatch, getState: () => RootState): Promise<DataSourceObject | undefined> => {
        try {
            const user = getCurrentUser(getState());
            if (!user)
                throw new ReferenceError("No user is currently logged in.");

            const notebook = getCurrentNotebook(getState());
            if (!notebook)
                throw new ReferenceError("No notebook currently selected.");

            // Write any async API calls here
            // Either from a server or from IndexedDb
            if (!DBService.db)
                throw new ReferenceError("The DBService has not been started yet.");

            // Update both database and our local state
            // TODO: Don't update if updating somehow failed
            const source = await DBService.db.createNewDataSource(user.userId, notebook.notebookId, name, type);
            if (!source)
                throw new ReferenceError("An error has occured when creating a data source.");

            const sourceObj = source.toObject();
            dispatch(addDataSource(sourceObj));
            return sourceObj;
        } catch (err) {
            console.log(err);
        }
    };
};

