// Selectors
/// Notebooks
export const getNotebookDetails = (state: RootState): NotebookObject[] => state.notebook.notebookDetails;
export const getCurrentNotebook = (state: RootState): NotebookObject | undefined => state.notebook.currentNotebook;
export const isNotebookOpen = (state: RootState, notebookId: string): boolean => {
    const currentNotebook = getCurrentNotebook(state);
    return currentNotebook?.notebookId === notebookId;
};
export const isNotebookInList = (state: RootState, notebookId: string): boolean => {
    const notebookDetails = getNotebookDetails(state);
    const notebookIndex = notebookDetails.findIndex(notebook => notebook.notebookId === notebookId);
    return notebookIndex > -1;
};

/// Data Sources
export const getDataSources = (state: RootState): DataSourceObject[] => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return [];

    return currentNotebook.sources;
};

export const getIsDraggingSource = (state: RootState): boolean => state.notebook.isDraggingSource;

/// Variables
export const getVariables = (state: RootState): VariableObject[] => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return [];

    return currentNotebook.variables;
};

export const getIsDraggingVariable = (state: RootState): boolean => state.notebook.isDraggingVariable;

/// Cells
export const getCells = (state: RootState): CellObject[] => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return [];

    return currentNotebook.cells;
};

/// Nodes
export const getNodes = (state: RootState, cellId: string): NodeObject[] => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return [];

    const cellIndex = currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
    if (cellIndex < 0)
        return [];

    return currentNotebook.cells[cellIndex].nodes;
};

export const getNodeById = (state: RootState, cellId: string, nodeId: string): NodeObject | undefined => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return;

    const cellIndex = currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
    if (cellIndex < 0)
        return;

    const nodeIndex = currentNotebook.cells[cellIndex].nodes.findIndex(node => node.nodeId === nodeId);
    if (nodeIndex < 0)
        return;

    return currentNotebook.cells[cellIndex].nodes[nodeIndex];
};

export const getSelectedNode = (state: RootState): { cellId: string; nodeId: string; } | undefined => state.notebook.selectedNode;

/// Connections
export const getConnections = (state: RootState, cellId: string): NodeConnectionObject[] => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return [];

    const cellIndex = currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
    if (cellIndex < 0)
        return [];

    return currentNotebook.cells[cellIndex].connections;
};

export const getConnection = (state: RootState, cellId: string, fromNodeId: string, fromSlotId: string, toNodeId: string, toSlotId: string): NodeConnectionObject | undefined => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return;

    const cellIndex = currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
    if (cellIndex < 0)
        return;

    const connectionIndex = currentNotebook.cells[cellIndex].connections
        .findIndex(connection => connection.fromNodeId === fromNodeId &&
                                 connection.fromSlotId === fromSlotId &&
                                 connection.toNodeId === toNodeId &&
                                 connection.toSlotId === toSlotId);
    if (connectionIndex < 0)
        return;

    return currentNotebook.cells[cellIndex].connections[connectionIndex];
};

export const getConnectionById = (state: RootState, cellId: string, connectionId: string): NodeConnectionObject | undefined => {
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return;

    const cellIndex = currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
    if (cellIndex < 0)
        return;

    const connectionIndex = currentNotebook.cells[cellIndex].connections.findIndex(connection => connection.connectionId === connectionId);
    if (connectionIndex < 0)
        return;

    return currentNotebook.cells[cellIndex].connections[connectionIndex];
};

export const getSeparateConnectionsByNodeId = (state: RootState, cellId: string, nodeId: string): {
    fromConnections: NodeConnectionObject[];
    toConnections: NodeConnectionObject[];
} => {
    const fromConnections: NodeConnectionObject[] = [];
    const toConnections: NodeConnectionObject[] = [];
    const currentNotebook = getCurrentNotebook(state);
    if (!currentNotebook)
        return { fromConnections: [], toConnections: [] };

    const cellIndex = currentNotebook.cells.findIndex(cell => cell.cellId === cellId);
    if (cellIndex < 0)
        return { fromConnections: [], toConnections: [] };

    const connections = currentNotebook.cells[cellIndex].connections;
    connections.forEach(conn => {
        if (conn.fromNodeId === nodeId)
            fromConnections.push(conn);
        else if (conn.toNodeId === nodeId)
            toConnections.push(conn);
    });

    return { fromConnections, toConnections };
};

const notebookSlice = slice.reducer;
export default notebookSlice;