import { DataFrame, Series } from "danfojs";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler } from "../types";
import DanfoUtils from "../utils/DanfoUtils";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import Meta from "../utils/Meta";
import Regexes from "../utils/Regexes";
import { Types } from "../utils/Types";

function getConditionMask(df: DataFrame, col: string, cond: string, comp: string): Series | undefined {
    let result: Series | undefined;

    try {
        if (cond === ">")
            result = df.column(col).gt(Number(comp) ?? comp);
        else if (cond === ">=")
            result = df.column(col).ge(Number(comp) ?? comp);
        else if (cond === "<")
            result = df.column(col).lt(Number(comp) ?? comp);
        else if (cond === "<=")
            result = df.column(col).le(Number(comp) ?? comp);
        
        // These two use square brackets instead of column() so it will
        // support equality checking for both strings and numbers, instead
        // of only numbers.
        else if (cond === "==") {
            const dtype = df.column(col).dtype;
            if (dtype === "int32" || dtype === "float32")
                result = df.column(col).eq(Number(comp)) as Series;
            else
                result = df[col].eq(comp) as Series;
        }
        else if (cond === "!=") {
            const dtype = df.column(col).dtype;
            if (dtype === "int32" || dtype === "float32")
                result = df.column(col).ne(Number(comp)) as Series;
            else
                result = df[col].ne(comp) as Series;
        }

        else if (cond === "has")
            result = df.column(col).str.includes(comp) as Series;
        else if (cond === "start")
            result = df.column(col).str.startsWith(comp) as Series;
        else if (cond === "end")
            result = df.column(col).str.endsWith(comp) as Series;
    } catch(err) {
        console.error(err);
        result = undefined;
    }

    return result;
}

const locNode: NodeType = {
    id: "loc-dataframe",
    name: "DataFrame Loc",
    description: Descriptions.NodeSpecific.DataFrameLoc.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "columns",
            description: Descriptions.NodeSpecific.DataFrameLoc.ColumnsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.labelCommaRegex, Regexes.labelSliceRegex, Regexes.emptyRegex)
        },
        {
            id: "rows",
            description: Descriptions.NodeSpecific.DataFrameLoc.RowsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.conditionRegex, Regexes.emptyRegex)
        },
        {
            id: "resetIndex",
            description: Descriptions.NodeSpecific.DataFrameLoc.ResetIndexProperty,
            type: "boolean",
            defaultValue: false
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }
        
        const columnPattern = properties["columns"] as string;
        const rowPattern = properties["rows"] as string;
        const resetIndex = properties["resetIndex"] as boolean;
        const df = input["input"] as DataFrame;

        let columns: string[] | undefined;
        if (Regexes.isMatch(columnPattern, Regexes.labelCommaRegex))
            columns = Regexes.processCommaDelimString(columnPattern, Regexes.baseLabelRegex);
        else if (Regexes.isMatch(columnPattern, Regexes.labelSliceRegex))
            columns = [columnPattern];

        // These row patterns should refer to row indexes instead of actual values
        let rows: string[] | Series | undefined;
        if (Regexes.isMatch(rowPattern, Regexes.conditionRegex)) {
            const parts = rowPattern.split(" ");
    
            const col = parts[0];
            const cond = parts[1];
            let comp: string;
            if (parts.length > 3)
                comp = parts.slice(2).join(" ").replaceAll("'", "");
            else
                comp = parts[2];
    
            rows = getConditionMask(df, col, cond, comp);
        }
        
        let loc = DanfoUtils.loc(df, columns, rows);
        loc = resetIndex ? loc.reset_index() as DataFrame : loc;
        return { 
            output: loc 
        };
    }
};

const ilocNode: NodeType = {
    id: "iloc-dataframe",
    name: "DataFrame iLoc",
    description: Descriptions.NodeSpecific.DataFrameiLoc.Description,
    style: {
        width: 140
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    properties: [
        {
            id: "columns",
            description: Descriptions.NodeSpecific.DataFrameiLoc.ColumnsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.numberCommaRegex, Regexes.numberSliceRegex, Regexes.emptyRegex)
        },
        {
            id: "rows",
            description: Descriptions.NodeSpecific.DataFrameiLoc.RowsProperty,
            type: "string",
            regex: Regexes.combine(Regexes.numberCommaRegex, Regexes.numberSliceRegex, Regexes.emptyRegex)
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }
        
        const columnPattern = properties["columns"] as string;
        const rowPattern = properties["rows"] as string;
        const df = input["input"] as DataFrame;

        let columns: (string | number)[] | undefined;
        if (Regexes.isMatch(columnPattern, Regexes.numberCommaRegex))
            columns = Regexes.processCommaDelimNumber(columnPattern, Regexes.baseNumberRegex);
        else if (Regexes.isMatch(columnPattern, Regexes.numberSliceRegex))
            columns = [columnPattern];

        let rows: (string | number)[] | undefined;
        if (Regexes.isMatch(rowPattern, Regexes.numberCommaRegex))
            rows = Regexes.processCommaDelimNumber(rowPattern, Regexes.baseNumberRegex);
        else if (Regexes.isMatch(rowPattern, Regexes.numberSliceRegex))
            rows = [rowPattern];

        const iloc = df.iloc({ columns, rows });
        return { 
            output: iloc 
        };
    }
};

const getNumericalColumns: NodeType = {
    id: "numerical-column-dataframe",
    name: "Get Numerical Columns",
    description: Descriptions.NodeSpecific.GetNumericalColumns.Description,
    style: {
        width: 170
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.DataFrameInput,
            type: Types.DataFrame
        }
    ],
    outputSlots: [
        {
            id: "output",
            description: Descriptions.Common.ResultOutput,
            type: Types.DataFrame
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }
        
        const df = input["input"] as DataFrame;
        const validColumns = df.dtypes.map((type, index) => {
            if (type !== "string" && type !== "boolean")
                return df.columns[index];
        }).filter((col): col is string => !!col);

        const newDf = df.loc({ columns: validColumns });
        return { 
            output: newDf 
        };
    }
};

const QueryNodes = [
    locNode,
    ilocNode,
    getNumericalColumns
];

export default QueryNodes;