import * as Danfo from "danfojs";
import * as Papa from "papaparse";
import DBService from "../db/DBService";
import DanfoUtils from "../utils/DanfoUtils";

abstract class DataSource {
    private _id: string;
    protected _df?: Danfo.DataFrame;

    constructor(id: string) {
        this._id = id;
    }

    get id(): string {
        return this._id;
    }

    get df(): Danfo.DataFrame | undefined {
        return this._df;
    }

    public abstract loadToDf(): Promise<boolean>;
}

abstract class LocalDataSource extends DataSource {
    public async saveToDB(): Promise<boolean> {
        if (!this.df)
            return false;
        
        if (!DBService.db)
            throw new ReferenceError("The DBService has not been started yet.");
            
        const jsonObj = Danfo.to_json(this.df, { download: false, format: "row" });
        const buffer = new TextEncoder().encode(JSON.stringify(jsonObj));
        await DBService.db.addData(this.id, buffer);
        return true;
    }
}

export class CSVSource extends LocalDataSource {
    private csvFile: File | undefined;

    constructor(id: string, csvFile?: File) {
        super(id);
        this.csvFile = csvFile;
    }

    async loadToDf(): Promise<boolean> {
        if (!DBService.db)
            throw new ReferenceError("The DBService has not been started yet.");

        const data = await DBService.db.getData(this.id);
        if (data) {
            const jsonObj = JSON.parse(new TextDecoder().decode(data));
            const df = new Danfo.DataFrame(jsonObj);
            this._df = df;
            return true;
        }

        // Data isn't saved in db yet, check pathToCsv and save to db
        if (!this.csvFile)
            return false;

        const csvFile: File = this.csvFile;
        const parsedData = await new Promise(resolve => {
            Papa.parse(csvFile, {
                header: true,
                dynamicTyping: false,
                complete: results => {
                    resolve(results.data);
                }
            });
        });

        const df = new Danfo.DataFrame(parsedData);
        const df2 = DanfoUtils.rebuildDataFrame(df, df.dtypes);
        this._df = df2;
        return true;
    }
}

