import { Series } from "danfojs";
import { NodeType, PrimitiveTree, PrimitiveObject, NodeErrorHandler } from "../types";
import Descriptions from "../utils/Descriptions";
import Errors from "../utils/Errors";
import Meta from "../utils/Meta";
import { Types } from "../utils/Types";

const countSeries: NodeType = {
    id: "count-column-dataframe",
    name: "Count Series",
    description: Descriptions.NodeSpecific.CountSeries.Description,
    style: {
        width: 130
    },
    inputSlots: [
        {
            id: "input",
            description: Descriptions.Common.SeriesInput,
            type: Types.Series
        }
    ],
    outputSlots: [
        {
            id: "count",
            description: Descriptions.Common.ResultOutput,
            type: Types.Number
        }
    ],
    properties: [
        {
            id: "skipNull",
            description: Descriptions.NodeSpecific.CountSeries.SkipNullProperty,
            type: "boolean",
            defaultValue: false
        }
    ],
    nodeFunction: async (input: PrimitiveTree, properties: PrimitiveObject | undefined, errorHandler: NodeErrorHandler | undefined): Promise<PrimitiveTree> => {
        if (!errorHandler)
            return {};

        if (!properties) {
            errorHandler.error(Errors.MissingNodeProperties);
            return {};
        }

        if (Meta.isEmpty(input["input"])) {
            errorHandler.error(Errors.MissingNodeInput("input"));
            return {};
        }

        const series = input["input"] as Series;
        const skipNull = properties["skipNull"] as boolean;
        const count = skipNull ? series.count() : series.size;
        return {
            count
        };
    }
};

