import React from "react";
import store from "../app/store";
import { insertCellAsync, insertCellToTopAsync } from "../features/notebooks/notebookSlice";

type InsertCellDropZoneProps = {
    cellId: string;
}

type InsertCellDropZoneState = {
    isHovering: boolean;
}

export default class InsertCellDropZone extends React.Component<InsertCellDropZoneProps, InsertCellDropZoneState> {
    constructor(props: InsertCellDropZoneProps) {
        super(props);

        this.state = {
            isHovering: false
        };

        this.onDrop = this.onDrop.bind(this);
    }

    async onDrop(event: React.DragEvent<HTMLDivElement>): Promise<void> {
        const { cellId } = this.props;
        event.preventDefault();
        this.setState({isHovering: false});
        const data = JSON.parse(event.dataTransfer.getData("text/plain"));
        const currentCellId = data["cellId"];

        // cellid is empty only when this dropzone is at the top
        await store.dispatch(cellId === "" ? insertCellToTopAsync(currentCellId) : 
            insertCellAsync(currentCellId, cellId));
    }

    render(): JSX.Element {
        const { isHovering } = this.state;
        return (
            <div className={`absolute w-full h-4 z-50 ${isHovering ? "bg-yellow-500 opacity-50" : ""}`}
                style={{bottom: "-0.5rem"}}
                onDragEnter={event => {
                    event.preventDefault();
                    this.setState({isHovering: true});
                }}
                onDragOver={event => {
                    event.preventDefault();
                }}
                onDragLeave={event => {
                    event.preventDefault();
                    this.setState({isHovering: false});
                }}
                onDrop={this.onDrop}></div>
        );
    }
}