import { KonvaEventObject } from "konva/lib/Node";

export default class KonvaUtils {
    public static setCursor(event: KonvaEventObject<Event>, cursorType: string): void {
        const stage = event.target.getStage();
        if (!stage) {
            console.error("The Konva stage could not be found.");
            return;
        }

        stage.container().style.cursor = cursorType;
    }
}