import { ReactCodeMirrorRef } from "@uiw/react-codemirror";
import store from "../app/store";

class CodeMirrorManager {
    private refs: {
        [key: string]: ReactCodeMirrorRef
    } = {};
    private delayFocusKey = "";
    private currentNotebookId = "";

    constructor() {
        store.subscribe(this.onStoreUpdate);
    }

    onStoreUpdate(): void {
        const currentNotebook = store.getState().notebook.currentNotebook;
        if (!currentNotebook || !this)
            return;

        if (this.currentNotebookId !== currentNotebook.notebookId)
            this.reset();
    }

    addRef(key: string, ref: ReactCodeMirrorRef): void {
        this.refs[key] = ref;
        if (key === this.delayFocusKey)
            this.focus(key);
    }

    focus(key: string): void {
        if (this.refs[key])
            this.refs[key].view?.focus();
    }

    delayedFocus(key: string): void {
        this.delayFocusKey = key;
    }

    reset(): void {
        this.refs = {};
        this.delayFocusKey = "";
    }
}

export default new CodeMirrorManager();